import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    Text,
    TouchableOpacity,
    Pressable, Alert
} from 'react-native'
import {Group} from "../../templateComponents/Group/GroupMain";
import {TypeDataReturnRender} from "../../components/TypeDataReturnRender";
import {THEME} from "../../theme";
import { MaterialIcons } from '@expo/vector-icons';
import {setStatusReloadCategoryCatalog} from "../../store/actions/post";
import {useDispatch, useSelector} from "react-redux";
import {CheckWorkTimeAffiliateNew} from "../../components/CheckWorkTimeAffiliateNew";
import {CheckWorkTimeAffiliate} from "../../components/SelectionBlockDelivery/ChekWorkTimeAffiliate";

import { AntDesign } from '@expo/vector-icons';

export const OneAdressAffiliate = ({ dataAdress, onPress , onOpen, onOpenProduct = null , statusDelivery = false}) => {

    const dispatch = useDispatch();

    const SelectAffiliate = useSelector(state=>state.post.affiliateSelect);

    const getActiveAffiliate = (Title,DataDay)=>{
          //console.log(DataDay);

        // const StartTime = (getNumberPlusNull(DataDay.hoursStart)+':'+getNumberPlusNull(DataDay.minuteStart));
        // const StopTime = (getNumberPlusNull(DataDay.hoursStop)+':'+getNumberPlusNull(DataDay.minuteStop));

        //console.log(StartTime,StopTime);

        let activeAf = false;
        activeAf = CheckWorkTimeAffiliateNew(DataDay.hoursStart,DataDay.minuteStart,DataDay.hoursStop,DataDay.minuteStop);
        //activeAf = CheckWorkTimeAffiliate(StartTime,StopTime);
          //console.log('Точка '+Title+' сейчас ',activeAf);
        return activeAf;
    };

    const getNumberPlusNull = (str)=>{
        if(str === null ){
            return false;
        }
        let returns = 0;

        if(str*1 <= 9){
            returns = '0'+str;
        }else{
            returns = str;
        }
        return returns;
    };


    const returnInfoCheckAdres = (id)=>{
        if(SelectAffiliate === null){return false;}else {
            if (SelectAffiliate.id === id) {
                return true;
            } else {
                return false;
            }
        }
    };

    const StatusWorkAffiliate = getActiveAffiliate(dataAdress.title,dataAdress.worktime.days[new Date().getDay()]);

    const TitleAdress = ({data})=>{
        return(
            <Group
                // title={data.comment != '' ? data.comment : null}
                bottomLabel={StatusWorkAffiliate ? <Text style={{color:"#4CAF50"}}>Открыто</Text> : <Text style={{color:THEME.MAIN_COLOR}}>Закрыто</Text>}
                styles={{
                    paddingVertical: "3%",
                    paddingHorizontal: "3%",
                    shadowOffset: {
                        width: 0,
                        height: 0,
                    },
                    shadowOpacity: 0,
                    shadowRadius: 0,
                    elevation: 0,
                    borderBottomWidth:1,
                    borderTopWidth:0,
                    borderColor:"#dddddd",
                    marginVertical: 0,
                    marginBottom:0
                }}
            >
                <Text>
                    <CommentAdres title={dataAdress.title} fontWeight="bold" fontSize={14}/>
                    {"\n"}
                    <CommentAdres title={dataAdress.operating_mode} fontSize={14}/>
                </Text>

                {/*{StatusWorkAffiliate ?*/}
                {/*    <View style={styles.infoIcon}><AntDesign name="check" size={24} color="#81C784" /></View>*/}
                {/*    :*/}
                {/*    <View style={styles.infoIcon}><AntDesign name="closecircleo" size={24} color={THEME.MAIN_COLOR} /></View>*/}
                {/*}*/}

                {returnInfoCheckAdres(dataAdress.id) ?
                    <View style={styles.infoIcon}><AntDesign name="check" size={24} color="#16A085" /></View>
                    :
                    false
                }

            </Group>
        )
    };



    const CommentAdres = ({title, fontWeight = 'regular', streetStop = false, fontSize = 14}) =>{
        return (
            <Text style={fontWeight === 'regular' ? [styles.Fontregular,{fontSize:fontSize}] : [styles.Fornbold,{fontSize:fontSize}] }>
                {title}
            </Text>
        )
    };

    const StreenAdress = ({dataAdress, fontWeight = 'regular', streetStop = false, fontSize = 14}) =>{
        return (
            <Text style={fontWeight === 'regular' ? [styles.Fontregular,{fontSize:fontSize}] : [styles.Fornbold,{fontSize:fontSize}] }>
                {!streetStop ?
                    <>
                        <TypeDataReturnRender data={dataAdress.city} type="" separator={false}/>
                        <TypeDataReturnRender data={dataAdress.street} type="ул. " separator=", "/>
                        <TypeDataReturnRender data={dataAdress.home} type="д. " separator=", "/>
                        <TypeDataReturnRender data={dataAdress.kvartira} type="кв. " separator=", "/>
                    </>
                    :
                    <>
                        <TypeDataReturnRender data={dataAdress.city} type="" separator={false}/>
                        <TypeDataReturnRender data={dataAdress.street} type="ул." separator=", "/>
                    </>
                }
            </Text>
        )
    };

    const allCategory = useSelector(state => state.post.allCategory);



    if(StatusWorkAffiliate === true) {

        return (
            <TouchableOpacity activeOpacity={0.5} onPress={() => {

                 onPress(dataAdress);
                 //dispatch(setActiveCategoryId(allCategory[0].id));
            }}>
                <View style={styles.adressBlock}>
                    <TitleAdress data={dataAdress}/>
                </View>
            </TouchableOpacity>
        )
    }else{
        return (
            <Pressable
                activeOpacity={1}
                onPress={()=>
                    Alert.alert(
                        "Точка уже закрыта",
                        "К сожалению получить заказ, с данной точки, можно только в рабочее время.",
                        [
                            { text: "OK", onPress: () => console.log("OK Pressed") }
                        ]
                    )}
                style={({ pressed }) => [
                    {
                        opacity: pressed
                            ? 1
                            : 0.97
                    }
                ]
                }
            >
                <View style={styles.adressBlock}>
                    <TitleAdress data={dataAdress}/>
                </View>
            </Pressable>
        )
    }
};

const styles = StyleSheet.create({
    adressBlock:{
        paddingVertical:0,
        paddingHorizontal:0,
        margin:0
    },
    TitleAdress:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    Fontregular:{
        fontFamily: "open-regular"
    },
    Fornbold:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    errorDelivery:{
        position:"absolute",
        top:0,
        right:0,
        backgroundColor:"rgba(255,255,255,0.96)",
        width:"100%",
        height:"99%",
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center"
    },
    textErrorDelivery:{
        color:THEME.MAIN_COLOR,
        fontSize:12,
        textAlign:'center'
    },
    infoIcon:{
        position: "absolute",
        top:0,
        right:0,
        width:50,
        height:70,
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center"
    }
});
