import React, {useCallback, useEffect, useRef} from 'react';
import {Image, StyleSheet, Text, View} from "react-native";
import {GetPriceProductBasket} from "../../Basket/getPriceProductBasket";
import {ButtonInCartTopping} from "../../Basket/ButtonInCartTopping";
import {ModyficateBlock} from "../../Catalog/Product/Modyficate/ModyficateBlock";
import {THEME} from "../../../theme";
import {Price} from "../../Catalog/Product/ProductCatd/Price";
import {URL_SITE} from "../../../store/types";
import {ButtonInCart} from "../../Catalog/ButtonInCart";

export const OneProductRecommend = ({productRecommendBasket = [], product = null, navigation , allProduct = null}) => {

    // console.log("Вот тут стопоримся --- ",product.id_tovar);
    // console.log("Вот тут стопоримся -- "+product.id_tovar+"-- ",allProduct[product.id_category].ProductList[product.id_tovar]);
    if(product === null || allProduct === null){
        return false;
    }

    let StatusFalseReturn = false;
  //  console.log(productRecommendBasket);

    productRecommendBasket.forEach(
        (DTProduct,number)=>{
            //console.log('В корзине - ',DTProduct, product);
            if(product.id_tovar === DTProduct.id){
                StatusFalseReturn = true;
            }
        }
    );

    if(StatusFalseReturn){
        return false;
    }


    // console.log('Вот тут стопоримся ',allProduct[product.id_category]);

    if(!(product.id_tovar in allProduct[product.id_category].ProductList)){return false;}

    const product2 = {
        ...allProduct[product.id_category].ProductList[product.id_tovar][0],
        id:product.id_tovar
    };

    // return(
    //     <View style={styles.productBlock}>
    //         <View style={styles.textWrap}>
    //             <Text>Хоба</Text>
    //         </View>
    //     </View>
    // );

    return (
        <View style={styles.productBlock}>
            <View style={[styles.textWrap,styles.shadow]}>
                <Image style={styles.image} source={{ uri: "https:"+product2.img }} />
                <View style={styles.TextBlockProduct}>
                    <View style={product2.type_info !== '' ? styles.HeaderBlocks : styles.HeaderBlocksCenter}>
                        <View style={
                            product2.type_info !== '' ? styles.BlockTitle : styles.BlockTitleMaxWidth }>
                            <Text style={styles.title}>
                                {product2.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                            </Text>

                        </View>
                        {product2.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                            <Text style={styles.TypeInfo}>
                                {product2.type_info.trim()}
                            </Text>
                        </View> : false}
                    </View>
                    <View style={styles.ViewPrice}>
                        {/*<Price ProductData={product}/>*/}
                        <Price ProductData={product2} fontSize={14} priceBlock="column"/>
                        <ButtonInCart Product={product2} titleButton="Добавить" SizeButton="normal"/>
                    </View>
                </View>
                <ModyficateBlock ModyficateProduct={product2.modyficate}/>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden',
        width:165,
        height:"100%",
        paddingHorizontal: 1,
        marginHorizontal:7,
        marginVertical: 0,
        borderRadius: 10
    },
    image: {
        width: THEME.WIDTH_PRODUCT_RECOMEND_IMG,
        height: THEME.HEIGHT_PRODUCT_RECOMEND_IMG,
        marginRight:0,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"100%",
        flexDirection:"column",
        justifyContent:"space-between",
        alignItems:"center",
        paddingRight:0,
        flexWrap:"wrap",
        minHeight:95
    },
    HeaderBlocks:{
        marginBottom:10,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"baseline",
        width:'100%'
    },

    HeaderBlocksCenter:{
        marginBottom:10,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"baseline"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"flex-start",
        alignItems:"flex-start",
        width:"70%",
    },
    BlockTitleMaxWidth:{
        flexDirection:"row",
        justifyContent:"flex-start",
        alignItems:"flex-start",
        width:"100%",
        paddingBottom:0,
        minHeight:44
    },
    textWrap: {
        flex:1,
        flexDirection:"column",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 10,
        paddingHorizontal: 10,
        alignItems: 'center',
        width: '100%',
        flexWrap:"wrap",
        borderRadius:10
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize: 12,
        width:"100%",
        textAlign: "left",
        marginTop:10
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    ViewPrice:{
        width:'100%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    priceText:{
        paddingVertical:5,
        fontFamily:"open-bold, segoe ui, Arial"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:0,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:2,
        paddingBottom:0,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'30%',
        flexDirection:"row",
        // alignItems:"center",
        alignItems:"flex-start",
        justifyContent: "flex-start",
        paddingBottom: 5,
        minHeight:44
    },
    shadow:{
        borderBottomWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.15,
        shadowRadius: 5,
        elevation: 2,
        marginBottom:10,
        marginTop:10,
        padding:5,
    }
})