import React, {useEffect, useRef, useState} from 'react';
import {
    View,
    Text,
    StyleSheet,
    SafeAreaView,
    ScrollView,
    Pressable,
    TextInput,
    Dimensions,
    FlatList, Platform, ActivityIndicator
} from 'react-native'
import { HeaderButtons, Item } from 'react-navigation-header-buttons'
import { AppHeaderIcon } from '../../components/AppHeaderIcon'
import { useDispatch, useSelector } from 'react-redux';
import {setActiveCategoryId, updateCountNoticeNew, UpdatePushNoticeList} from "../../store/actions/post";
import {THEME} from "../../theme";
import {CatalogActiveCategory} from "../../components/Catalog/CatalogActiveCategory";
import {OneProduct} from "../../components/Catalog/Product/ProductCatd/OneProduct";
import {AntDesign} from "@expo/vector-icons";
import {CheckCategoryOff} from "../../components/CheckCategoryOff";
import {IconLabelEbee} from "../../components/IconLabelEbee";
import {RadioButton} from "react-native-paper";
import RadioBottomUI from "../../components/RadioBottom/RadioBottom";
import {ButtonEbee} from "../../templateComponents/Button/ButtonEbee";
import {getTypeDevice} from "../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";


export const SearchScreen = ({navigation, route}) => {

    const [text, onChangeText] = React.useState('');
    const [number, onChangeNumber] = React.useState('');
    const [filterModal, setFilterModal] = React.useState(false);
    const [typeSearch, setTypeSeach] = React.useState(false);
    const [typeMark, setTypeMark] = React.useState("all");
    const dispatch = useDispatch();

    const allProduct = useSelector(state => state.post.allProducts);


    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);

    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    let CheckNulls = null;
    let CheckedOffCategorys = [];

    let NoneCategoryStop = [];
    let TextNoneDelivered = "Нет доставки на Ваш адрес";

    if(TypeDeliverySystem === true){
        CheckNulls = DeliveryAdresSystem;
        CheckedOffCategorys = DeliveryAdresSystem;
        NoneCategoryStop = DeliveryAdresSystem?.OffCategoryData?.OffCategory;
        TextNoneDelivered = "Нет доставки на Ваш адрес"
    }else{
        CheckNulls = AffiliateAdresSystem;
        CheckedOffCategorys = AffiliateAdresSystem;
        NoneCategoryStop = AffiliateAdresSystem?.JsonNoneCheck?.category_none;
        TextNoneDelivered = "Не готовят на выбранной точке"
    }

    // console.log(AffiliateAdresSystem,DeliveryAdresSystem,NoneCategoryStop);

    const reloadedCatalog = useSelector(state => state.post.reloadedCatalog);

    if(typeof (NoneCategoryStop) === "object"){
        NoneCategoryStop = Object.keys(NoneCategoryStop);
    }

    if(NoneCategoryStop?.length <0 || NoneCategoryStop === undefined || NoneCategoryStop === null){
        NoneCategoryStop = [];
    }

    const getFilterTextInCombo = (arrayText,ArrayProduct)=> {
        let statusReturn = true; //По умолчанию - товар проходит

        let KPr = Object.keys(ArrayProduct);

        //console.log("Проверяем товар с комбо ",arrayText,KPr);

        Object.keys(allProduct).map((key, n) => {
            let ProductsListActive = allProduct[key].ProductList;
            Object.keys(ProductsListActive).map((id, num) => {

                ProductsListActive[id].map((type) => {

                    KPr.forEach((d,nn)=>{
                        if(d == type?.id_Product){
                            //console.log("Товары в комбо совпали по id, проверяем вхождение слов ");
                            arrayText.forEach((word,nw)=>{
                                if(word === undefined || word === null || word === ' ' || word == '' || word.length <=2){}else {
                                    if (type.description.toLowerCase().includes(word.toLowerCase().trim())) {
                                        statusReturn = false;
                                    }
                                    if (type.title.toLowerCase().includes(text.toLowerCase().trim())) {
                                        statusReturn = false;
                                    }
                                }
                            });
                        }
                    });

                })
            })
        })
        return statusReturn;
    }


    const setActiveProductList = ()=>{

        let ProductList = [];
        if((text === '' || text === ' ' || text.length <=2) & typeMark == "all"){}else {
            Object.keys(allProduct).map((key, n) => {
                let ProductsListActive = allProduct[key].ProductList;
                Object.keys(ProductsListActive).map((id, num) => {
                    // console.log(ProductsListActive[id]);
                    ProductsListActive[id].map((type) => {
                        let AddTrue = true;

                        // console.log('type=',type);
                        // console.log('type=',type?.id_Product);
                        // console.log('type=',type?.komboPoduct);

                        NoneCategoryStop.forEach((idC,nC)=>{
                            if(type.id_Category == idC){
                                AddTrue = false
                            }
                        });


                        if(!typeSearch){
                            //По наименованию
                            let textSplit = text.split(',');
                            let StatusAddProduct = false;
                            let StatusAddProductMark = false;

                            textSplit.forEach((word,n)=>{
                                if(word === undefined || word === null || word === ' ' || word == '' || word.length <=2){}else {
                                    if (type.description.toLowerCase().includes(word.toLowerCase())) {
                                        StatusAddProduct = true;
                                    }
                                    if (type.title.toLowerCase().includes(text.toLowerCase())) {
                                        StatusAddProduct = true;
                                    }
                                }
                            });

                            if(typeMark !== "all"){
                                // console.log("Ищем среди ",typeMark);

                                type?.modyficate.forEach((dd,nn)=>{
                                    // contains(MarksArray,dd.mod) === true ? false : MarksArray.push(dd.mod);
                                    if(dd.mod == typeMark){
                                        if((text === '' || text === ' ')){
                                            StatusAddProduct = true;
                                        }
                                        StatusAddProductMark = true;
                                        // console.log("Есть совпадение ",dd.mod,' == ',typeMark);
                                    }
                                });
                            }else{
                                StatusAddProductMark = true;
                                // console.log("Тут все товары ищим");
                            }

                            if(StatusAddProduct === true & StatusAddProductMark === true){
                                ProductList.push({
                                    ...type,
                                    id: id,
                                    img: "https:" + type.img,
                                    activeProduct: AddTrue
                                });
                            }

                        }else{
                            //Обратный поиск
                            let textSplit = text.split(',');
                            let StatusAddProduct = true;
                            let StatusAddProductMark = false;

                            textSplit.forEach((word,n)=>{
                                if(word === undefined || word === null || word === ' ' || word == '' || word.length <=2){}else {
                                    if (type.description.toLowerCase().includes(word.toLowerCase().trim())) {
                                        StatusAddProduct = false;
                                    }
                                    if (type.title.toLowerCase().includes(text.toLowerCase().trim())) {
                                        StatusAddProduct = false;
                                    }
                                }
                            });

                            if(typeof (type?.komboPoduct) === "object" & StatusAddProduct === true){
                                // console.log("Должны проверить этот комбо (сет) ",type);
                                StatusAddProduct = getFilterTextInCombo(textSplit,type?.komboPoduct);
                            }

                            if(typeMark !== "all"){
                                // console.log("Ищем среди ",typeMark);

                                type?.modyficate.forEach((dd,nn)=>{
                                    // contains(MarksArray,dd.mod) === true ? false : MarksArray.push(dd.mod);
                                    if(dd.mod !== typeMark){
                                        if((text === '' || text === ' ')){
                                            StatusAddProduct = true;
                                        }
                                        StatusAddProductMark = true;
                                        // console.log("Есть совпадение ",dd.mod,' == ',typeMark);
                                    }
                                });
                            }else{
                                StatusAddProductMark = true;
                                // console.log("Тут все товары ищим");
                            }

                            if(StatusAddProduct === true & StatusAddProductMark === true){
                                ProductList.push({
                                    ...type,
                                    id: id,
                                    img: "https:" + type.img,
                                    activeProduct: AddTrue
                                });
                            }
                        }

                    });
                    // ProductList.push(OneProducts);
                    //return OneProducts;
                });
            });
        }
        //console.log(ProductList);

        let SortCategory = ProductList.sort(( a, b ) => b.activeProduct - a.activeProduct);
        // console.log('Должны показать товары из массива',activeCategorys,allCategory[0].id,allCategory[0]);

        return SortCategory;
    };
    // const countPushListNew = useSelector(state => state.post.pushNoticeCountNew);

    // useEffect( ()=> {
    //         const asyncFn = async () => {
    //             //Тут если че надо обновлять при открытии
    //         };
    //         asyncFn();
    //     }
    //     ,[dispatch]);

    const flatListRef = useRef();

    const handleItemPress = () => {
        // console.log('Прокрутка');
        flatListRef.current.scrollToOffset({ animated: false, offset:0});
    };

    const AllProductSearch = setActiveProductList(allProduct);

    const handleClearSearch = ()=>{
        onChangeText('');
    }

    const handleOpenCloseFilter = ()=>{
        setFilterModal(!filterModal);
    }

    const handleTypeSearch = ()=>{
        setTypeSeach(!typeSearch);
    }
    const handleTypeMark = (e)=>{
        setTypeMark(e);
    }

    const FilterSettings = ()=>{
        return (
            <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {height:Dimensions.get("window").height-95,backgroundColor:"#ffffff"} :{height:Dimensions.get("window").height-115,backgroundColor:"#ffffff"}}>
                <RadioBottomUI
                    status={ !typeSearch }
                    label={"Поиск по названию и описанию"}
                    onPress={handleTypeSearch}
                    description={"Для поиска нескольких товаров, введите наименования через запятую"}
                />
                <RadioBottomUI
                    status={ typeSearch }
                    label={"Обратный поиск"}
                    onPress={handleTypeSearch}
                    description={"Введите слова через запятую. Поиск исключит все товары, где они присутствуют в описании."}
                />
                <View style={{borderTopWidth:1,borderTopColor:THEME.MAIN_COLOR,paddingBottom:0,marginVertical:5,marginBottom:0}}><Text style={{paddingVertical:10,paddingHorizontal:"3%",paddingBottom:0}}>Показать товары</Text></View>
                <GetMarksProduct productList={allProduct} />
                <View style={{paddingHorizontal:"2%",paddingBottom:"4%",position:"absolute",width:"100%",bottom:0,backgroundColor:"#ffffff"}}>
                        <ButtonEbee style={{borderRadius:10,marginHorizontal: "2%",boxSizing:"border-box"}} onPress={() => handleOpenCloseFilter()}>Применить</ButtonEbee>
                </View>
            </View>
        )
    }

    const contains = (arr, elem)=>{
        for (var i = 0; i < arr.length; i++) {
            if (arr[i].mod === elem) {
                return true;
            }
        }
        return false;
    }


    const getTextMarkInfo = (mark)=>{
        switch (mark) {
            case "all": return {"name":"Все","description":false};
            case "hot": return {"name":"Горячие","description":false};
            case "new": return {"name":"Новинка","description":false};
            case "burn": return {"name":"Острые","description":false};
            case "fire": return {"name":"Жареные","description":false};
            case "vegan": return {"name":"Веганские","description":false};
            case "fish": return {"name":"Сырая рыба","description":false};
            default: return {"name":mark,"description":false};
        }
    }

    const GetMarksProduct = ({productList})=>{
        let MarksArray = [{mod:"all",title:"Все"}];
            Object.keys(productList).map((key, n) => {
                let ProductsListActive = allProduct[key].ProductList;
                Object.keys(ProductsListActive).map((id, num) => {
                    ProductsListActive[id].map((type) => {

                        // console.log(type);
                        // console.log(type?.modyficate);
                        type?.modyficate.forEach((dd,nn)=>{
                            //console.log(dd.mod);
                            contains(MarksArray,dd.mod) === true ? false : MarksArray.push(dd);
                        });
                    });
                });
            });

            //console.log("MarksArray",MarksArray);
            //console.log("typeMark",typeMark);
        return(
            <ScrollView>
                {MarksArray.map((d,n)=>{
                    return(
                        <RadioBottomUI
                            status={ typeMark === d.mod ? true : false}
                            label={
                                !typeSearch || d.mod == "all"
                                    ?
                                    d.mod == "all" ? getTextMarkInfo(d.mod).name :
                                    d?.title
                                        ?
                                        d?.title
                                        :
                                        getTextMarkInfo(d?.mod).name
                                    :
                                    d?.title
                                        ?
                                        "Исключить: "+d?.title
                                        :
                                        "Исключить: "+getTextMarkInfo(d.mod).name
                            }
                            // key={d?.mod}
                            onPress={()=>handleTypeMark(d?.mod)}
                            description={getTextMarkInfo(d?.mod).description}
                        />
                    )
                })}
                <View style={{height:60}}></View>
            </ScrollView>
        );
    }

    const handleCHangeText = (e)=>{
        // console.log(e);
        onChangeText(e);
        if(filterModal === true) {
            setFilterModal(false)
        }
    }

    return (
        <View style={[styles.center,{width:"100%"}]}>
            <View style={styles.allPushCountBlock}>
                {/*<Text style={styles.allPushCountText}>Поле для поиска товаров</Text>*/}
                {Platform.OS === 'android' ? <Pressable style={{width:"10%",textAlign:"center",paddingLeft:"1%",borderRightWidth:1,height:"100%",alignItems:"center",justifyContent:"center",flexDirection:"row",borderColor:THEME.BORDER_COLOR_DEFAULT}} onPress={() => navigation.goBack()}><IconLabelEbee name={"arrow-back"} size={24} color={THEME.MAIN_COLOR} typeIcon={"Ionicons"}/></Pressable> : false}
                <TextInput
                    style={styles.input}
                    onChangeText={handleCHangeText}
                    value={text}
                    placeholder="Введите наименование или состав"
                    // keyboardType="numeric"
                />
                <Pressable style={{width:"10%",textAlign:"center"}} onPress={() => handleClearSearch()}><IconLabelEbee name={"close"} size={24} color={THEME.MAIN_COLOR} typeIcon={"Ionicons"}/></Pressable>
                <Pressable style={{width:"10%",textAlign:"center",borderLeftWidth:1,height:"100%",alignItems:"center",justifyContent:"center",flexDirection:"row",borderColor:THEME.BORDER_COLOR_DEFAULT,paddingTop:2,boxSizing:"border-box"}} onPress={() => handleOpenCloseFilter()}><IconLabelEbee name={"filter-menu-outline"} size={20} color={filterModal ? THEME.MAIN_COLOR : THEME.TEXT_COLOR_TABS_MENU_DEFAULT} typeIcon={"MaterialCommunityIcons"}/></Pressable>
            </View>
            <View style={{width:"100%"}}>
                <View style={{backgroundColor:"#ffffff",paddingVertical:"2%",width:"100%",transition:"2s",display: filterModal ? "flex" : "none"}}>
                    <FilterSettings/>
                </View>
            </View>
            {reloadedCatalog || CheckNulls === null ?
                <SafeAreaView style={{
                    width:"100%",
                    height:Dimensions.get('window').height*0.5,
                    alignItems:"center",
                    justifyContent:"center",
                }}>
                    {/*<ActivityIndicator color={THEME.MAIN_COLOR} />*/}

                    {CheckNulls === null ? <>
                            <AntDesign name="exclamationcircleo" size={72} color={THEME.MAIN_COLOR} />
                            <Text style={{textAlign:"center"}}>
                                {"\n"}Выберите адрес {"\n"}для доставки или самовывоза
                            </Text>
                        </>
                        :
                        <ActivityIndicator color={THEME.MAIN_COLOR} />}
                </SafeAreaView>

                : AllProductSearch.length > 0 ? <FlatList
                            // scrollEnabled={scrollChildren}
                            showsVerticalScrollIndicator={false}
                            ref={flatListRef}
                            data={AllProductSearch}
                            // nestedScrollEnabled={scrollChildren}
                            // numColumns={Dimensions.get('window').width >= 421 ?  2 : 1}
                            numColumns={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ?  2 : 1}
                            keyExtractor={product => product.id.toString() + "_" + product.type_product.toString()}
                            columnWrapperStyle={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {width:"100%"} : false}
                            renderItem={({item}) => {
                                let OneProductReturn = item.activeProduct === true ? <OneProduct
                                    product={item}
                                    onOpen={
                                        () => {}
                                    }
                                    onOpenProduct={() => {}}
                                />
                                :
                                    <View>
                                        <View style={{backgroundColor:"rgba(255,250,255,0.8)",zIndex:99,position:"absolute",width:"100%",height:"100%",textAlign:"center",justifyContent:"center",alignItems:"center",flexDirection:"row"}}><Text style={{color:THEME.MAIN_COLOR,backgroundColor:"#ffffff",paddingVertical:"2%",paddingHorizontal:"5%",borderRadius:THEME.BORDER_RADIUS_DEFAULT}}>{TextNoneDelivered}</Text></View>
                                        <OneProduct
                                            product={item}
                                            onOpen={
                                                () => {}
                                            }
                                            onOpenProduct={() => {}}
                                        />
                                    </View>
                                return (
                                    OneProductReturn
                                    )
                            }}
                            style={{height: Dimensions.get('window').height - (Dimensions.get('window').height / 5),width:"100%"}}
                        />
                        :
                        <SafeAreaView style={styles.wrapper}>
                            <View style={styles.wrapper}>
                                <AntDesign name="search1" size={72} color={THEME.MAIN_COLOR} />
                                <Text style={styles.noItems}>{text.length >0 ? "Товары отсутсвуют" : "Введите запрос"}</Text>
                            </View>
                        </SafeAreaView>
                }


        </View>
    )
}

