const styles = StyleSheet.create({
    wrapper: {
        padding: 5,
        paddingHorizontal:"1.8%",
        height:Dimensions.get('window').height-(Dimensions.get('window').height/5),
        alignItems: "center",
        // backgroundColor:THEME.MAIN_COLOR,
        justifyContent: "center"
        // height:Platform.OS === 'android' ? Dimensions.get('window').height : "100%"
        //  height:Platform.OS === 'android' ? "100%" : "100%"
    },
    input: {
        margin: 0,
        borderWidth: 0,
        padding: 10,
        borderColor:THEME.BORDER_COLOR_DEFAULT,
        borderTopColor:THEME.BORDER_COLOR_DEFAULT,
        color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT,
        backgroundColor:"#ffffff",
        // borderRadius: THEME.BORDER_RADIUS_DEFAULT,
        marginHorizontal: 0,
        boxSizing:"border-box",
        width: Platform.OS === 'android' ? "70%" : "80%",
    },
    center: {
        flex: 1,
        justifyContent: 'flex-start',
        alignItems: 'center',
        // paddingVertical:"2%"
    },
    version: {
        fontFamily: 'open-bold, segoe ui, Arial'
    },
    allPushCountBlock:{
        width:"100%",
        // height:40,
        backgroundColor:"#ffffff",
        paddingVertical:"0%",
        display:"flex",
        justifyContent:"space-between",
        alignItems:"center",
        flexDirection:"row",
        borderBottomWidth:1,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT
    },
    allPushCountText:{
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:18,
        textAlign:"center"
    },
    blockOneNotice:{
        width:"95%",
        paddingVertical: 7,
        paddingHorizontal:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        marginVertical: 10,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.22,
        shadowRadius: 2.22,

        elevation: 3,
        marginLeft:"2.5%",
        borderRadius:10
    },
    titleNoticeActive:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize: 16,
        borderBottomWidth:1,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT,
        paddingBottom:5,
        color:THEME.TEXT_COLOR_TABS_MENU_ACTIVE
    },
    titleNotice:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize: 16,
        borderBottomWidth:1,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT,
        paddingBottom:5,
        color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT
    },
    bodyNoticeActive:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize: 13,
        paddingTop:5
    },
    bodyNotice:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize: 14,
        paddingTop:5
    },
    dateLine:{
        marginTop:10,
        paddingVertical:10,
        textAlign:"left",
        borderTopWidth:1,
        borderTopColor:THEME.BORDER_COLOR_DEFAULT
    },
    dateLineText:{
        fontSize:14,
        fontFamily:"open-bold, segoe ui, Arial"
    },
    dateLineTextActive:{
        fontSize:14,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 18,
        minWidth:150
    }
})
