import React, {useEffect, useState} from 'react'
import {View, StyleSheet, FlatList, Text, ScrollView, SafeAreaView, Dimensions,Pressable} from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import {getAllFilter} from "./getAllFilter";
import {ButtonEbee} from "../../../templateComponents/Button/ButtonEbee";
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";
import {IconLabelEbee} from "../../IconLabelEbee";
import {THEME} from "../../../theme";
import {color} from "react-native-reanimated";

const FilterCategorys = ({navigation,route})=>{
    const dispatch = useDispatch();
    const activeCategory = useSelector(state => state.post.activeCategoryProductList);
    const allCategory = useSelector(state => state.post.allCategory);
    const activeFilter = useSelector(state => state.post.activeFilterCategory);
    const typeDeviceVK = useSelector(state => state.post.typeDeviceVK);


    const listFilter = getAllFilter(allCategory,activeCategory);

    const getTitleFilter = ()=>{
        let TitleFilter = 'не определено!';
        listFilter.forEach((d,n)=>{
            d?.value === activeFilter ? TitleFilter = d?.title : false
        });
        return TitleFilter;
    }

    if(activeCategory === null || listFilter.length <=0 ){
     return false;
    }

    return (
        <>
        {/*<View style={{paddingHorizontal: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height,typeDeviceVK) ? "2%" : "12%"}}>*/}
        {/*    <ButtonEbee*/}
        {/*        size="xl"*/}
        {/*        borderRadius={20}*/}
        {/*        shadow={false}*/}
        {/*        onPress={async ()=>{*/}
        {/*            navigation.navigate('FilterCategory');*/}
        {/*        }}*/}
        {/*    >*/}
        {/*        {activeFilter === null || activeFilter === "all" ? <Text>Выбрать фильтр</Text> : <Text>Фильтр: {getTitleFilter()}</Text>}*/}
        {/*    </ButtonEbee>*/}
        {/*</View>*/}
        <View style={{paddingHorizontal: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height,typeDeviceVK) ? "2%" : "3%",display:"flex",flexDirection:"row",paddingVertical:"1.5%",alignItems: "center",paddingTop:"3%"}}>
            {activeFilter === null || activeFilter === "all" ?
                <>
                    <Text style={{color:"#909090",fontFamily: 'open-regular',fontWeight:600,paddingRight:"2%"}}>Выберите фильтр</Text>
                    <Pressable
                        // style={{paddingLeft:"2%"}}
                    onPress={async ()=>{
                        navigation.navigate('FilterCategory');
                    }}
                        style={{backgroundColor:"#eeeeee",borderRadius:20,padding:5}}
                    >
                        <IconLabelEbee name={"format-list-checks"} size={24} color={activeFilter === null || activeFilter === "all" ? "#909090" :THEME.MAIN_COLOR} label={false} typeIcon={"MaterialCommunityIcons"} />
                    </Pressable>
                </>
                :
                <>
                    <Text style={{color:"#909090",fontFamily: 'open-regular',fontWeight:600,paddingRight:"2%"}}>Фильтр: <span style={{color:THEME.MAIN_COLOR}}>{getTitleFilter()}</span></Text>
                <Pressable
                    // style={{paddingLeft:"2%"}}
                onPress={async ()=>{
                navigation.navigate('FilterCategory');
            }}
                style={{backgroundColor:THEME.MAIN_COLOR,borderRadius:20,padding:5}}
                >
                <IconLabelEbee name={"format-list-checks"} size={24} color={activeFilter === null || activeFilter === "all" ? THEME.COLOR_TEXT_NO_ACTIVE : 'white'} label={false} typeIcon={"MaterialCommunityIcons"} />
                </Pressable>
                </>
            }
        </View>
        </>
    )

}

export default FilterCategorys;