const BaskerTabs = createDrawerNavigator();

function  BasketScreen({ navigation, route }){
    return (
        <BaskerTabs.Navigator initialRouteName="BasketScreen">
            <BaskerTabs.Screen name="BasketScreen" component={MainBakset}
                               options={({ route }) => ({
                                   headerTitle: route?.params?.title || "Корзина",
                                   headerLeft: () => (
                                       <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                                           <Item
                                               title='Toggle Drawer'
                                               iconName='arrow-back'
                                               color={"#333333"}
                                               onPress={() => navigation.goBack()}
                                           />
                                       </HeaderButtons>
                                   ),
                                   drawerLockMode: 'locked-closed',
                                   headerShown: false
                               })}
            />
        </BaskerTabs.Navigator>
    )
}


const MultiScreen = createDrawerNavigator();

function ScreenMenu({navigation, route}) {
    return (
        <MultiScreen.Navigator>
            <MultiScreen.Screen name="Main" Label="Каталог" component={MainScreen} />
            <MultiScreen.Screen name="Product" component={ProductScreen}
                                options={({ route }) => ({
                                    title: route?.params?.title.split('&amp;quot;').join('"').split('&quot;').join('"') || "Заголовок",
                                    headerTitle: route?.params?.title.split('&amp;quot;').join('"').split('&quot;').join('"') || "Товар",
                                    headerLeft: () => (
                                        <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                                            <Item
                                                title='Toggle Drawer'
                                                iconName='arrow-back'
                                                color={"#333333"}
                                                onPress={() => navigation.navigate('Main')}
                                            />
                                        </HeaderButtons>
                                    ),
                                    drawerLockMode: 'locked-closed',
                                    headerShown: true
                                })}
            />
        </MultiScreen.Navigator>
    );
}


// export const AppNavigation = createAppContainer(MainNavigator);
const Tab = createBottomTabNavigator();

function HomeTabs() {

    const basketProduct = useSelector(state => state.post.basket);
    const CountBasketTabs = ()=>{
        if(basketProduct.length<=0){
            return {
            } ;
        }else {
            return {
                tabBarBadge: <GetCountBasketProduct basket={basketProduct}/> };
        }
    }

    return (
        <Tab.Navigator
            screenOptions={({ route }) => ({
                tabBarIcon: ({ focused, color, size }) => {
                    let iconName;
                    let typeIcon = 1;
                    let sizeIcon = 22;
                    switch(route.name){
                        case 'Catalog':
                            iconName = focused
                                ? 'utensils'
                                : 'utensils';
                            break;
                        case 'Basket':
                            iconName = focused ? 'shopping-bag' : 'shopping-bag';
                            typeIcon = 1;
                            break;
                        case 'InfoDelivery':
                            iconName = focused ? 'map-marker-alt' : 'map-marker-alt';
                            typeIcon = 1;
                            break;
                        case 'PointsInfo':
                            iconName = focused ? 'star' : 'star';
                            typeIcon = 2;
                            sizeIcon = 24;
                            break;
                        case 'ProfileScreen':
                            iconName = focused ? 'user-alt' : 'user-alt';
                            typeIcon = 1;
                            break;
                        case 'DeletedProfileScreen':
                            iconName = focused ? 'user-alt' : 'user-alt';
                            typeIcon = 1;
                            break;
                        default: ;
                    }

                    // You can return any component that you like here!
                    if(typeIcon === 2) {
                        return <AntDesign name={iconName} size={sizeIcon} color={color}/>;
                    }else {
                        return <FontAwesome5 name={iconName} size={sizeIcon} color={color}/>;
                    }
                },
                tabBarLabel:({ focused, color, size })=>{
                    let titleTabs = '';
                    let sizeText;
                    focused ? sizeText = {fontSize:12,color:THEME.TEXT_COLOR_TABS_MENU_ACTIVE} : sizeText =  {fontSize:12,color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT}
                    switch(route.name){
                        case 'Catalog':
                            titleTabs = <Text style={sizeText}>Меню</Text>;
                            break;

                        case 'Basket':
                            titleTabs = <Text style={sizeText}>Корзина</Text>;
                            break;
                        case 'InfoDelivery':
                            titleTabs = <Text style={sizeText}>Доставка</Text>;
                            break;
                        case 'PointsInfo':
                            titleTabs = <Text style={sizeText}>Акции</Text>;
                            break;
                        case 'ProfileScreen':
                            titleTabs = <Text style={sizeText}>Профиль</Text>;
                            break;
                        case 'DeletedProfileScreen':
                            titleTabs = <Text style={sizeText}>Удаление аккаунта</Text>;
                            break;
                        default: ;
                    }
                    return titleTabs;
                }
            })}
            tabBarOptions={{
                activeTintColor: THEME.MAIN_COLOR,
                inactiveTintColor: 'gray',
                labelPosition: "below-icon",
                tabStyle:{paddingVertical: 5}
            }}
        >

            <Tab.Screen name="Catalog" component={ScreenMenu} />
            <Tab.Screen name="Basket" component={BasketScreen} options={CountBasketTabs}
            />
            <Tab.Screen name="InfoDelivery" component={InfoDeliveryScreen}
                        options={{
                            headerTitle: "Доставка и оплата",
                            drawerLockMode: 'locked-closed',
                            headerShown: true
                        }}
            />
            <Tab.Screen name="PointsInfo" component={PointsInfoScreen} />
            <Tab.Screen name="ProfileScreen" component={ProfileScreen} />
        </Tab.Navigator>
    );
}

