            {/*<Drawer.Navigator initialRouteName="PointsInfo">*/}
            {/*    <Drawer.Screen name="Product" component={PointsInfoNavigator} />*/}
            {/*    /!*<Drawer.Screen name="Notifications" component={NotificationsScreen} />*!/*/}
