import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../../../theme";
import {ButtonInCart} from "./../../ButtonInCart";
import Icon from "react-native-paper/src/components/Icon";
import {IconLabelBasket} from "../../../../navigation/AppNavigation";
import {IconLabelEbee} from "../../../IconLabelEbee";

export const OneModifyProduct = ({ product, onPress, modificationActive=[], numberProductComboModify = 0}) => {
    product = product[0];
    //console.log("Вот: ",product[0].title, "Что пришло в комбо One");
    // return null;

    // console.log('Данные по модификаторам вкл и выкл ',modificationActive);

    const getCountModifyInTmpCart = (idModify)=>{
        // console.log('Проверяем idModify ',idModify);
        let status = false;
        modificationActive.map((d,n)=>{
            if(idModify == d.idModify){
                // console.log('Есть совпадение ',idModify, d.idModify);
                status = true;
            }
        });

        return status;
    }

    let statusActiveModify = getCountModifyInTmpCart(product?.idModify);

    // console.log('Статус активности модификатора ',statusActiveModify);

    return (
        <TouchableOpacity activeOpacity={0.7} onPress={
            // () => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category)
            () => {
                // console.log('Добавить вот это в модификаторы товара ',product);
            onPress(product,numberProductComboModify);
            }
        } style={styles.productBlock}>
            <View style={statusActiveModify ? styles.textWrapActive : styles.textWrap }>
                {statusActiveModify ? <View style={styles.activeBlockAfter} >
                    <IconLabelEbee name={'check-circle'} size={18} color={THEME.MAIN_COLOR} label={false} />
                </View> : false}
                <Image style={styles.image} source={{ uri: product.img }} />
                <View style={styles.TextBlockProduct}>
                    <View style={styles.BlockTitle}>
                        <Text style={styles.title}>
                            {product.title.trim()}
                        </Text>
                    </View>
                    {/*{product.description !== '' ? <View style={styles.ViewDescription}>*/}
                    {/*    <Text style={styles.description}>*/}
                    {/*        {product.description.trim()}*/}
                    {/*    </Text>*/}
                    {/*</View> : <Text></Text>}*/}
                </View>
                <View>
                    {product.sumDiscount > 0 ?
                        <Text style={styles.weight}>
                            <Text style={{textDecorationLine:"line-through",color:"#dddddd"}}>{product.old_price} ₽</Text> {product.price} ₽
                        </Text>
                        :
                        <Text style={styles.weight}>
                            {product.price <= 0 ? false : product.price+' ₽'}
                        </Text>
                    }
                </View>
            </View>
        </TouchableOpacity>
    )
};

const styles = StyleSheet.create({
    productBlock: {
        width:"32%",
        marginBottom: 0,
        overflow: 'hidden',
        paddingHorizontal: '1%'
    },
    image: {
        width: THEME.WIDTH_ONE_MODIFY_PRODUCT_IMG,
        height: THEME.HEIGHT_ONE_MODIFY_PRODUCT_IMG,
        marginRight:5,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"97%"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"stretch",
        width:"99%",
        minHeight:40,
        textAlign:"center"
    },
    textWrap: {
        height:170,
        // backgroundColor:"#ff7300",
        flexDirection:"column",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 5,
        borderWidth:1,
        borderRadius:10,
        borderColor:"#eeeeee",
        alignItems: 'center',
        width: '100%',
        marginVertical: 5,
    },
    textWrapActive: {
        height:170,
        // backgroundColor:"#ff7300",
        flexDirection:"column",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 5,
        borderWidth:1,
        borderRadius:10,
        borderColor:THEME.MAIN_COLOR,
        alignItems: 'center',
        width: '100%',
        marginVertical: 5,
    },
    activeBlockAfter:{
        width:20,
        height:20,
        position:"absolute",
        // backgroundColor:"#ff7300",
        top:5,
        right:5,
        zIndex:99,
        borderRadius:50
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize: 12,
        width:"97%",
        backgroundColor:"#ffffff",
        textAlign: "center"
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    price: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize:15
    },
    ViewPrice:{
        width:'98%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:1,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:3,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'80%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    }
})
