import React, {useEffect} from 'react';
import {Platform, Pressable, StyleSheet, Text, View,} from 'react-native';
import { useDispatch, useSelector } from 'react-redux';
import {THEME} from "../theme";
import {CityModal} from "./CityModal";
import {AppHeaderIcon} from "../components/AppHeaderIcon";
import {HeaderButtons, Item} from "react-navigation-header-buttons";
import { Entypo } from '@expo/vector-icons';
import {IconLabelBasket} from "./AppNavigation";
import {IconLabelEbee} from "../components/IconLabelEbee";
import {updateCountNoticeNew} from "../store/actions/post";



export const CityHeaderInfo = ({navigation, route}) => {
    const dispatch = useDispatch();
    const countPushListNew = useSelector(state => state.post.pushNoticeCountNew);

    const CityData = useSelector(state => state.post.cityData);

    const IconLabelBasket = ({name, size, color, count, typeIcon = "FontAwesome5"})=>{

        if(count<=0){
            return (<IconLabelEbee name={name} size={size} color={color} label={false} typeIcon={typeIcon} />)
        }else{
            if(count>99){
                count = "...";
            }

            return (<IconLabelEbee name={name} size={size} color={color} label={count} typeIcon={typeIcon}/>)
        }

    };

    const allPushList = useSelector(state => state.post.pushNoticeList);

    // console.log(allPushList);

    const countNewPushNotice = (allPushList)=>{
        let counts = 0;

        Object.keys(allPushList).forEach((data,number)=>{
            if(Number(allPushList[data].readNotice) === 0){
                counts++;
                // console.log('Плюсуем')
            }else{
                // console.log('Не плюсуем');
            }
        });

            // console.log(counts);
        return counts;

    };

    useEffect( ()=> {
            const asyncFn = async () => {
                let countsList = await countNewPushNotice(allPushList);
                // console.log('Всего уведомлений ',countsList);
                dispatch(updateCountNoticeNew(countsList));};
            asyncFn();
    }
    ,[dispatch,allPushList]);

    return (
        <View style={{justifyContent:"flex-start", alignItems:"center",flexDirection:"row"}}>
            {/*<CityModal style={styles.paddings} titleCity={CityData.cityName} keys="CityButtonLocation"/>*/}
            <View style={{marginLeft:"4%",paddingVertical:0,}}>

                <Pressable
                    style={{marginTop:15,marginRight:2,flexDirection:'row'}}
                    onPress={() => {
                          //console.log('Нажали на поиск');
                        navigation.navigate('SearchScreen')
                    }}
                >
                    <Text style={{fontSize:18,marginRight:6,fontFamily:"open-regular, segoe ui, Arial", fontWeight:300,color:THEME.TEXT_DESCRIPTION_PRODUCT}}>Поиск</Text>
                    <IconLabelBasket name='search' size={24} color={THEME.TEXT_DESCRIPTION_PRODUCT} count={0} typeIcon={"Feather"}/>
                </Pressable>
            </View>
                {/*<View style={{marginRight:"4%",marginLeft:"4%",paddingVertical:0}}>*/}
                {/*    <Pressable*/}
                {/*        style={{marginTop:10,marginRight:2}}*/}
                {/*        onPress={() => {console.log('Нажали на колокольчик');navigation.navigate('PushNoticeList')}}*/}
                {/*    ><IconLabelBasket name='bell' size={24} color={THEME.TEXT_DESCRIPTION_PRODUCT} count={countPushListNew} /></Pressable>*/}
                {/*</View>*/}
        </View>
    )
};

const styles = StyleSheet.create({
    paddings:{
       paddingHorizontal:11
    }
});