import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../../../theme";
import {ButtonInCart} from "../../ButtonInCart";
import {NumberFormatPrice} from "../../../../templateComponents/NumberFormat/NumberFormat";

export const Price = ({ ProductData,stylePrice,styleNewPrice,styleOldPrice, onClick , fontSize = 14, fontFamily = 'bold ', priceBlock = "row"}) => {

    if(ProductData.price_discount !== false){
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                    {ProductData.price_discount.new_price} ₽
                </Text>
                <Text style={[styles.Oldprice,styleOldPrice]}>
                    {NumberFormatPrice(ProductData.price_discount.old_price,0,'.',' ')} ₽
                </Text>
            </View>
        )
    }else{
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                <Text style={[[styles.price,stylePrice],{fontSize:fontSize},{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                    {NumberFormatPrice(ProductData.price,0,'.',' ')} ₽
                </Text>
            </View>
        )
    }
};

const styles = StyleSheet.create({
    PriceBlock: {
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch"
    },
    PriceText:{

    },
    price: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:15
    },
    Newprice: {
        color: THEME.MAIN_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:15,
        paddingRight:5
    },
    Oldprice:{
        fontSize: 9,
        textDecorationLine:"line-through"
    }
});
