import React, {useEffect, useCallback, useState} from 'react'
import {
    View,
    Text,
    StyleSheet,
    Image,
    Button,
    ScrollView,
    SafeAreaView,
    Alert, FlatList, ActivityIndicator, TouchableOpacity, Dimensions
} from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import { THEME } from '../../theme'
import {toogleBooked, removePost, setSelectAdres, loadAdressUser, setOrderListServer} from '../../store/actions/post';
import {OneAdress} from "../../screens/DeliveryBlock/OneAdress";
import { Feather } from '@expo/vector-icons';
import {DB} from "../../db";
import {OneAdressProfile} from "./OneAdresProfile";
import {ServerDB} from "../../serverDb";


import { AntDesign } from '@expo/vector-icons';
import OrderListFunction from "./OrderListContent/OrderListFunction";
import {HeaderButtons, Item} from "react-navigation-header-buttons";
import {AppHeaderIcon} from "../../components/AppHeaderIcon";

const OrderListScreenProfile = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    const [loaded,setLoaded] = useState(true);
    const [loadedFC,setLoadedFC] = useState(true);

    const OrderListArray = useSelector(state=>state.post.orderListUser);

    const getOrderList = async ()=>{
        const Result = await ServerDB.getOrderUserList(CityIds,idUserSystem);
        dispatch(setOrderListServer(Result));
    };

    const ReloadListAdres = async ()=>{

       // const Results = await dispatch(loadAdressUser(idUserSystem,[],{cityId:CityIds}));
        await getOrderList();
        setLoaded(false);
    };


    useEffect(() => {
        // Put Your Code Here Which You Want To Refresh or Reload on Coming Back to This Screen.
        if('reloads' in route.params){
            setLoaded(true);
            ReloadListAdres()
        }else{}
        //console.log('Обновляемся ',navigation.state.params);
    }, [route.params]);

    if(loadedFC) {
        useEffect(()=> {
                ReloadListAdres()
            }
            ,[dispatch]);
    }else{

    }

    // if(route.params.reload === true){
    //     ReloadListAdres();
    // }else{
    //
    // }

    const Loadings = ()=>{
        if(loaded){
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <ActivityIndicator color={THEME.MAIN_COLOR} size={50}/>
                    <Text style={styles.TextInfoMini}>Обновление...</Text>
                </SafeAreaView>
            )
        }else{
            return(
                <UserListScreenOrderAwait navigation={navigation} OrderList={OrderListArray} route={route}/>
            )
        }
    };

    return(
        <Loadings />
    )

};


export const UserListScreenOrderAwait = ({ navigation, OrderList = null, route }) => {
    const dispatch = useDispatch()
    const postId = route.params.postId;
    let orderIdOpen = null;

    if('idOrderNow' in route.params){
        orderIdOpen = route.params.idOrderNow;
        // console.log('Должны открыть заказ переданный в push сообщении ', orderIdOpen)
    }


    const product = route.params.productData;

    const AdresList = useSelector(state => state.post.adressUser.listAddress);
    const AdresListNoDelivery = useSelector(state => state.post.adressUser.ListAddressNoDelivery);

    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    if (!product) {
        return null
    }

    const OrderNullPage = ()=>{
        return(
            <SafeAreaView style={styles.centeredViewWindowTop}>
                <AntDesign name="closecircleo" size={50} color={THEME.MAIN_COLOR} />
                <Text style={styles.TextInfo}>Заказы отсутсвтуют!</Text>
                <Text style={styles.TextInfoMini}>Совершите первый заказ и он{"\n"}отобразится на той странице</Text>
            </SafeAreaView>
        );
    };

      //console.log('Ого твою ж меть');

    return (
        OrderList === null ?
            <OrderNullPage />
            :
            <OrderListFunction navigation={navigation} OrderList={OrderList} orderOpenReload={orderIdOpen} route={route}/>
    )
};

OrderListScreenProfile.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;
    return {
        headerTitle: title,
        headerLeft: (
            <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                <Item
                    title='Toggle Drawer'
                    iconName='arrow-back'
                    color={"#333333"}
                    onPress={() => navigation.goBack()}
                />
            </HeaderButtons>
        )
    }
};

