import React from 'react'
import {
    View,
    Text,
    StyleSheet,
    SafeAreaView,
    ScrollView,
    Pressable,
    TextInput,
    Dimensions,
    FlatList, Platform, ActivityIndicator
} from 'react-native'
import {IconLabelEbee} from "../IconLabelEbee";
import {THEME} from "../../theme";

const RadioBottomUI = ({
                           label=false,
                           iconDefault="circle",
                           iconActive="check-circle",
                           typeIcon="FontAwesome5",
                           status=false,
                           style={},
                           size=24,
                           colorDefault=THEME.TEXT_COLOR_TABS_MENU_DEFAULT,
                           colorActive=THEME.MAIN_COLOR,
                           description=false,
                           onPress=()=>{}
                  })=>{
    style === false || style === undefined ? style = {} : false;

    return(
        <Pressable onPress={onPress} style={{marginVertical:"2%"}}>
            <View style={[{display:"flex",justifyContent:"flex-start",alignItems:"center",flexDirection:"row"},style]}>
                <View style={{marginHorizontal:10,width:"6%",minWidth:size}}>
                    <IconLabelEbee name={status ? iconActive: iconDefault} size={size} color={status ? colorActive : colorDefault} typeIcon={typeIcon}/>
                </View>
                <View style={{width: "90%",marginVertical: !description ? 8 : 0, paddingRight:'2%',boxSizing:"border-box"}}>
                    <Text style={{paddingBottom: !description ? 1 : 0}}>{label}</Text>
                    {!description ? false :
                        <View style={{boxSizing: "border-box", paddingVertical: 2, paddingHorizontal: 0,}}><Text
                            style={{fontSize: 12, color: "#7e7e7e"}}>{description}</Text></View>
                    }
                </View>
            </View>
        </Pressable>
    )
}

export default RadioBottomUI;