import React, {useState,useEffect} from 'react'
import {View, StyleSheet, Image, FlatList, Text, ScrollView, SafeAreaView, TouchableOpacity} from 'react-native'
import { OneProduct } from '../ProductCatd/OneProduct'
import { useDispatch, useSelector } from 'react-redux'
import {THEME} from '../../../../theme'
import {OneModifyProduct} from "./OneModifyProduct";
import {updateModifyTmp, activeButtonSetProduct, setModalStatus, setModalContent} from "../../../../store/actions/post";

export const ProductModify = ({
product = [], //данные товара
data = [], //список модификаторов
setPriceProduct = ()=>{} ,
typeModify = 0, //тип модификаторов 0 - список, 1 комбо карточка
countMustModify = 0, //кол-во обязательных модификаторов,
}) => {

    const dispatch = useDispatch();
    if (!data.length) {
        //console.log(data);
        return (
            <View style={styles.wrapper}>
                <Text style={styles.noItems}></Text>
            </View>
        )
    }

    const allProducts = useSelector(state => state.post.allProducts);

//    console.log(data);

    // const [modificationActive,setModificationActiveConst] = useState([]);
    const modificationActive = useSelector(state => state.post.modifyTmp);


    const setModificationActive = (idModification,dataModification,statusClear = false,numberProductComboModify = 0)=>{
        let statusAddNew = true;
        let numberIssetModify = 0;
        let NewModify = [];
        let allPrice = 0;


        if(product.typeCountActiveModify == true){
            if(product.countActiveModifyLimit*1 <= 1){
                statusClear = true;
            }
        }

        if(statusClear === true){
            //Нужно очистить все модификаторы и записать туда новый
            if(numberProductComboModify >0){
                NewModify = [];
                modificationActive.forEach((d,n)=>{
                    if(d.numberProductComboModify != numberProductComboModify){
                        NewModify.push(d);
                    }
                });
            }else{
                NewModify = [];
            }
        }else{
            NewModify = [...modificationActive]
        }


        //console.log('Что имеем на момент добавления нового модификатора = ',modificationActive,dataModification);

        let noAddModify = true;

        if(product.typeCountActiveModify == true){
            if(NewModify.length >= product.countActiveModifyLimit*1){
                noAddModify = false;
                //console.log('Кол-во модификаторов у товара, больше или равно кол-ву по ограничению ',product.countActiveModifyLimit,NewModify.length);
            }
        }

        NewModify.forEach((d,n)=>{
            if(d.idModify != undefined & d.idModify == idModification){
                //console.log('С какого хуя ',d.idModify+' == '+idModification);
                statusAddNew = false;
                numberIssetModify = n;
            }
        });

        if(statusAddNew){
            //console.log('Добавили новый модификатор в массив, так как его там еще нет');
            if(noAddModify === true) {
                NewModify.push(dataModification);
            }else{
                //console.log('Не дам тебе снять ахахах');
            }
        }else{
            //console.log('Почему то скрипт посчитал, что товар уже есть в массиве и не добавляет его кака новй, а плючует кол-во? statusAddNew = ',statusAddNew);
            // NewModify[numberIssetModify].count++;
            NewModify = setModificationNoActive(idModification, numberIssetModify);
        }

        NewModify.map((d,n)=>{
            allPrice += d.price*1;
        });

        //console.log('Стоимость допов в корзине = ',allPrice);

        // setPriceProduct(allPrice);

        return {"ModifyList":NewModify,"ModifyPrice":allPrice*1};
    }

    const setModificationNoActive = (idModification,ModifyNumber)=>{
        let NewReturn = [];

        modificationActive.forEach((d,n)=>{
            if(ModifyNumber != n){
                //console.log('Удаляем товар из массива модификаторов ', ModifyNumber);
                NewReturn.push(d);
            }
        });

        return NewReturn;
    }

    const onPress = (productModify)=>{
        // setModificationActiveConst(setModificationActive(productModify.idModify,productModify))
        dispatch(updateModifyTmp(setModificationActive(productModify.idModify,productModify,false,0)));
    }

    const onPressCombo = async (productModify,numberProductComboModify = 0)=>{
        // setModificationActiveConst(setModificationActive(productModify.idModify,productModify))
       await dispatch(updateModifyTmp(setModificationActive(productModify.idModify,productModify,true,numberProductComboModify)));
       dispatch(setModalStatus('MODAL_MODIFY_LIST',false));
    }

    const KomboListVariantCombo = ({productList,modificationActive = [],numberProductComboModify = 0}) => {
        let KomboList = [];


        if(product?.modifyListProduct !== null){
            product?.modifyListProduct.map((d,n)=>{
                if(numberProductComboModify == d.numberProductComboModify) {
                    KomboList.push({
                        ...d,
                        id: d.idModify,
                        img: "https:" + d.img,
                        count: 1
                    });
                }
            });
        }else{
            return false;
        }


        const ProductList = KomboList.map(
            (Pr,n)=>{
                return <OneModifyProduct product={[KomboList[n]]} key={"KomboListNumber_"+n} onPress={onPressCombo} modificationActive={modificationActive} numberProductComboModify={numberProductComboModify} />;
            }
        );

        return ProductList;
        // return null
    };


    const KomboList = ({productList,modificationActive = [],productData = false}) => {
        let KomboList = [];


        if(product?.modifyListProduct !== null){
            product?.modifyListProduct.map((d,n)=>{
                KomboList.push({...d,
                    id:d.idModify,
                    img:"https:"+d.img,
                    count:1
                });
            });
        }else{
            return false;
        }


        const ProductList = KomboList.map(
            (Pr,n)=>{
                return <OneModifyProduct product={[KomboList[n]]} key={"KomboListNumber_"+n} onPress={onPress} modificationActive={modificationActive} numberProductComboModify={0}/>;
            }
        );

        return ProductList;
        // return null
    };




    ///Логика для комбо карточки

    const handleSetButton = (countVariant,StatusCountMustModify = null)=>{
        if(modificationActive.length*1 >= countVariant*1) {
            //console.log('Кнопку типа ок');
            dispatch(activeButtonSetProduct({status: true, text: false}))
        }else {
            let countVisualNone = countVariant*1-modificationActive.length*1;
            //console.log('Кнопку типа не ок',"Не хватает модификаторов: "+countVisualNone, modificationActive.length * 1);
            dispatch(activeButtonSetProduct({status: false, text: "Не хватает модификаторов: "+countVisualNone}));
        }
    }

    const getListProductVariant = (countVariant = 0, modificationDataVariantTitleCombo = [])=>{
        countVariant = countVariant*1;
        if(countVariant == '0' || countVariant < 0){
            return false
        }

        let ReturnVariant = false;
        let CountArray = Array.from(Array(countVariant).keys());

        //Доп проверки на кол-во обязатаельных комбо модификаторв в корзине
        let statusCheckCountMust = true;
        let StatusCountMustModify = true;
        let count_must_modify = 0;
        let countInCartModify = 0;

        if(product.typeModalModify == '1'){
            let resultCheck = checkCountMustModifyCombo();
            if(resultCheck.countMustModify > 0){
                statusCheckCountMust = false;
            }
            count_must_modify = resultCheck.countMustModify;
            countInCartModify =  resultCheck.countMustModify - resultCheck.countErrorModify;

            if(resultCheck.countErrorModify >0){
                StatusCountMustModify = false;
            }

        }else{
            if(product.must_modify == '1') {
                count_must_modify = product.count_must_modify;
                countInCartModify =  modificationActive.length;
                statusCheckCountMust = false;
                if(product.count_must_modify > modificationActive.length){
                    StatusCountMustModify = false;
                }
            }
        }


        //console.log(CountArray);

        ReturnVariant = CountArray.map((d,n)=>{
            let iid = n+1;
            let mustVariantStatus = false;
            let titleComboVariants = "Выберите товар №"+iid;

            if(modificationDataVariantTitleCombo[n]?.must_modify == '1'){
                mustVariantStatus = true;
            }

            modificationDataVariantTitleCombo[iid]?.title !== '' &  modificationDataVariantTitleCombo[n]?.title !== undefined & modificationDataVariantTitleCombo[n]?.title !== NaN
                ?
                titleComboVariants = modificationDataVariantTitleCombo[n]?.title
                :
                titleComboVariants = "Выберите товар №"+iid;
            return getProductActiveComboVisual(iid,titleComboVariants,mustVariantStatus);
        });

        handleSetButton(count_must_modify);

        return ReturnVariant;
    }

    const getProductActiveComboVisual = (i,titleNoCheckProduct = 'Выберите товар №', mustVariantStatus = false)=>{
        let DataReplace = {
            "imgActive":"https://beewebshop.ru/images/preloader_sushi.png", //Тут нужно будет заменить url и картинку для заглушки не выбранного модификатора
            "TitleActive":titleNoCheckProduct,
            "DescriptionActive":false,
            "PriceActive":false,
            "active":false,
            "must": false
        };

        let statusMustVariant = '';

        //Проходимся по модификаторам уоторые выбраны и если он подходит, то активируем его для визуала
        modificationActive.forEach((d,n)=>{
            if(d.numberProductComboModify == i){
                let pricesReplace = '';
                if(d.price == '0'){
                    pricesReplace = false;
                }else{
                    pricesReplace = <Text>{d.price}₽</Text>;
                }
                DataReplace = {...DataReplace,...{"imgActive":d.img,
                        "TitleActive":d.title,
                        "DescriptionActive":d.description,
                        "PriceActive":pricesReplace,
                        "active":true,
                        "must": mustVariantStatus
                }
                };
            }
        });

        if(mustVariantStatus === true){
            if(DataReplace.must === true) {
                statusMustVariant = <View
                    style={{position:"absolute",
                        right:"3%",
                        top:"7%",
                        width:20,
                        height:20,
                        color:"#ffffff",
                        fontSize:10,
                        textAlign:"center",
                        display:"flex",
                        alignItems:"center",
                        justifyContent:"center",
                        backgroundColor: "#77d177",borderRadius: 10,paddingBottom:0,boxSizing:"border-box",
                        overflow: "hidden"
                    }}
                    title="* обязательный модификатор"><Text style={{color:"#ffffff",fontSize:10,}}>i</Text></View>
            }else{
                statusMustVariant =
                    <View
                        style={{
                            position:"absolute",
                            right:"3%",
                            top:"7%",
                            width:20,
                            height:20,
                            color:"#ffffff",
                            fontSize:10,
                            textAlign:"center",
                            display:"flex",
                            alignItems:"center",
                            justifyContent:"center",
                            backgroundColor: "#ee0121",borderRadius: 10,paddingBottom:0,boxSizing:"border-box",
                            overflow: "hidden"
                        }}
                    title="* обязательный модификатор"><Text style={{color:"#ffffff",fontSize:10,}}>i</Text></View>
            }
        }

        let returnsData =
                <TouchableOpacity activeOpacity={0.7} style={DataReplace.active === false ? styles.blockBetweenKomboRight : styles.blockBetweenKomboRightActive}
                      onPress={
                          // () => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category)
                          async () => {
                              // console.log('Добавить вот это в модификаторы товара ',product);
                              // setActiveButtonSetProduct(true);
                              // dispatch(activeButtonSetProduct({status:true,text:false}));
                              await dispatch(setModalContent('MODAL_MODIFY_LIST',getVariantModifyForModal(i))); //Устанавливаем список модификаторов, которые нужно отобразить в модалке
                              dispatch(setModalStatus('MODAL_MODIFY_LIST',true));
                          }
                      }>
                    {statusMustVariant}
                    <View style={styles.imgMiniKombo}>
                        <Image source={{ uri: DataReplace.imgActive }} style={{width:90,maxWidth:90,height:90}} />
                    </View>
                <View style={styles.textKomboRight}>
                    <View style={{width:"100%"}}>
                        <View><Text>{DataReplace.TitleActive}</Text></View>
                        {DataReplace.DescriptionActive !== false ? <View><Text style={styles.descriptionVisualActiveProductModify}>{DataReplace.DescriptionActive}</Text></View> : false}
                        {DataReplace.PriceActive !== false ? <View><Text style={styles.priceVisualActiveProductModify}>+ {DataReplace.PriceActive}</Text></View> : false}
                    </View>
                </View>
            </TouchableOpacity>;

        return returnsData;
    }


    const getVariantModifyForModal = (i)=>{
        let DataReturn =
            <View style={{display:"flex",flexDirection:"row",flexWrap:"wrap",justifyContent:"space-between",alignItems:"center"}}>
                {data.map((d,n)=><KomboListVariantCombo productList={d} modificationActive={modificationActive} numberProductComboModify={i}/>)}
                <View style={{width:"32%"}}></View>
            </View>
        ;
        return DataReturn;
    }

    ////


    //Визуал для отображения - выше логика....

    const getVisualListModify = ()=>{
        //возвращает модификаторы простым списком
        return (
            data[0].length <=0 ? false : <SafeAreaView style={styles.wrapper}>
                <Text style={styles.TitleKomboList}>{product?.additionInfoH}</Text>
                {product?.additionInfoPrint !== '' ? <Text style={styles.additionInfoPrint}>{product?.additionInfoPrint}</Text>: false}
                <View style={{display:"flex",flexDirection:"row",flexWrap:"wrap",justifyContent:"space-between",alignItems:"center"}}>
                    {data.map((d,n)=><KomboList productList={d} modificationActive={modificationActive} productData={product} />)}
                    <View style={{width:"32%"}}></View>
                </View>

                {/*<FlatList*/}
                {/*    data={data}*/}
                {/*    // style={}*/}
                {/*    // contentContainerStyle={{display:"flex",flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}*/}
                {/*    keyExtractor={product => "komboProductList_" + product.id}*/}
                {/*    listKey={product => "komboList_" + product.id}*/}
                {/*    renderItem={({item}) => (<KomboList productList={item}/>)}*/}
                {/*/>*/}
            </SafeAreaView>
        );
    };

    const getVisualComboModify = ()=>{
        //Возвращает модификаторы в виде карточки комбо товара
        return (
            <SafeAreaView style={styles.wrapper}>
                <Text style={styles.TitleKomboList}>{product?.additionInfoH}</Text>
                {product?.additionInfoPrint !== '' ? <Text style={styles.additionInfoPrint}>{product?.additionInfoPrint}</Text>: false}
                {/*{console.log('modificationDataVariantTitleCombo = ',product?.modificationDataVariantTitleCombo)}*/}
                {/*<View style={{display:"flex",flexDirection:"row",flexWrap:"wrap",justifyContent:"space-between",alignItems:"center"}}>*/}
                <View style={{alignItems:"center"}}>
                    {getListProductVariant(
                        product.countComboProductModal,
                        // getVisualComboModify,
                        product?.modificationDataVariantTitleCombo || []
                    )}
                </View>
            </SafeAreaView>
        )
    };


    useEffect(()=>{
        if(typeModify == '1'){

            //Доп проверки на кол-во обязатаельных комбо модификаторв в корзине
            let statusCheckCountMust = true;
            let StatusCountMustModify = true;
            let count_must_modify = 0;
            let countInCartModify = 0;

            if(product.typeModalModify == '1'){
                let resultCheck = checkCountMustModifyCombo();
                if(resultCheck.countMustModify > 0){
                    statusCheckCountMust = false;
                }
                count_must_modify = resultCheck.countMustModify;
                countInCartModify =  resultCheck.countMustModify - resultCheck.countErrorModify;

                if(resultCheck.countErrorModify >0){
                    StatusCountMustModify = false;
                }

            }else{
                if(product.must_modify == '1') {
                    count_must_modify = product.count_must_modify;
                    countInCartModify =  modificationActive.length;
                    statusCheckCountMust = false;
                    if(product.count_must_modify > modificationActive.length){
                        StatusCountMustModify = false;
                    }
                }
            }

            // setActiveButtonSetProduct(false);
            // handleSetButton(product.countComboProductModal*1) // старая версия отправки кол-ва модификатор выбранных
            //console.log('count_must_modify=StatusCountMustModify',count_must_modify,StatusCountMustModify);
            if(StatusCountMustModify ===true) {
                handleSetButton(count_must_modify * 1,StatusCountMustModify)
            }else{
                handleSetButton(count_must_modify * 1,StatusCountMustModify)
            }
            // dispatch(activeButtonSetProduct({status:false,text:"Не выбраны модификаторы"}));
        }else{
            //Тут нужно дописать проверку, для кол-ва обязательных модификаторов если это не комбо карточка
            //console.log('В товаре условия такие: ',product);
                if (product.must_modify == '1'
                    // & modificationActive.length < product.count_must_modify //Тут под вопросом нужно или нет это условие, так как оно к комбо относится вроди
                ) {
                    // dispatch(activeButtonSetProduct({status:false,text:"Необходимо выбрать модификаторов: "+product.count_must_modify}));
                    if(product.typeCountActiveModify == true){
                        if(product.countActiveModifyLimit <product.count_must_modify){
                            product.count_must_modify = product.countActiveModifyLimit;
                        }
                    }
                    handleSetButton(product.count_must_modify*1)
                }else{
                    dispatch(activeButtonSetProduct({status:true,text:false}));
                }
        }
    });



    const checkCountMustModifyCombo = ()=>{
        let statusAll = true; // По дефолту считаем что все модификаторы на месте.
        let countErorModifyMust = 0; // Кол-во обязательных, но еще не добавленных модификаторов
        let DataModifyCombo = product?.modificationDataVariantTitleCombo;
        let countMustModify = 0;

        if(!DataModifyCombo || DataModifyCombo == undefined){

        }else{
            //console.log('Что в определение обязательных модификаторов комбо ',DataModifyCombo);

            let statusChecked = [];

            DataModifyCombo.forEach((d,n)=>{
                if(d.must_modify == '1'){
                    statusChecked[n] = functionCHeckMustModifyListVariant(n+1);
                    countMustModify++;
                }else{
                    statusChecked[n] = true; // Проверка пройдена, можно добавлять товар в корзину
                }
            });



            statusChecked.forEach((d,n)=>{
                if(d === false){
                    countErorModifyMust++;
                    statusAll = false; //Если есть хотябы один не добавленный обязательный, то ставим ошибку.
                }

                //console.log('Данные по модификатору - ',n,d);
            });
        }


        return {"statusAll":statusAll,"countErrorModify":countErorModifyMust,"countMustModify":countMustModify}
    };

    const functionCHeckMustModifyListVariant = (idVariant)=>{
        //console.log(idVariant);
        if(modificationActive.length <= 0) {
            return false; // Активных модификаторов нет, возвращаем ошибку
        }

        let statusVariantMust = false; // По дефолту ставим, что модификатора нет

        modificationActive.forEach((d,n)=>{
            if(d.numberProductComboModify == idVariant){
                statusVariantMust = true; // Вариант активного модификатора совпал с тем который обязателен, возвращаем результат
            }
        });

        return statusVariantMust;
    };


    return (
        typeModify == '0' ? getVisualListModify() : getVisualComboModify()
    )
}

const styles = StyleSheet.create({
    wrapper: {
        paddingVertical: 10,
        paddingHorizontal:"3%",
        borderTopWidth:1,
        borderColor:THEME.BORDER_COLOR_DEFAULT
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 18
    },
    TitleKomboList:{
        fontFamily: "open-bold, segoe ui, Arial",
        fontWeight:700,
        fontSize: 18,
        paddingVertical: 10,
        paddingBottom:5
    },
    additionInfoPrint:{
        color:THEME.TEXT_DESCRIPTION_PRODUCT,
        paddingVertical:5
    },

    variantProductCombo:{
        width:"100%",
        paddingVertical:15,
        paddingHorizontal:10,
        marginVertical:10,
        borderWidth:1,
        borderRadius:10,
        borderColor:THEME.BORDER_COLOR_DEFAULT
    },


    blockBetweenKomboRight:{
        flex:1,
        width:"100%",
        paddingVertical:5,
        paddingHorizontal:5,
        marginVertical:5,
        borderWidth:1,
        borderRadius:10,
        borderColor:THEME.BORDER_COLOR_DEFAULT,
        color:THEME.TEXT_COLOR,
        display:"flex",flexDirection:"row",flexWrap:"wrap",justifyContent:"flex-start",alignItems:"center"
    },
    blockBetweenKomboRightActive:{
        width:"100%",
        paddingVertical:5,
        paddingHorizontal:5,
        marginVertical:5,
        borderWidth:1,
        borderRadius:10,
        borderColor:THEME.MAIN_COLOR,
        color:THEME.TEXT_COLOR,
        display:"flex",flexDirection:"row",flexWrap:"wrap",justifyContent:"flex-start",alignItems:"center",
    },
    imgMiniKombo:{},
    textKomboRight:{
        display:"flex",
        justifyContent:"flex-start",
        alignItems:"center",
        width:"58%",
        paddingLeft:15,
        // backgroundColor:THEME.MAIN_COLOR
    },
    descriptionVisualActiveProductModify:{
        fontSize:10,color:THEME.TEXT_DESCRIPTION_PRODUCT
    },
    priceVisualActiveProductModify:{
        fontSize:10,color:THEME.MAIN_COLOR
    },

    titleLeftVisuals:{}
})
