import React, {useCallback} from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
import {addProductBasketTopping} from "../../store/actions/post";

export const ButtonInCartTopping = ({ Product }) => {

    const dispatch = useDispatch();
    const basketProduct = useSelector(state => state.post.basketTopping);
    let inCart = false;
    let countInCart = 0;


    const reloadBasketData = (idProduct)=>{
        basketProduct.forEach((product)=>{
            //console.log(product.id, idProduct);
            if(product.id === idProduct){
                        //console.log('Совпанеие есть, товар есть в корзине');
                        inCart = true;
                        countInCart = product.count;

            }else{
                //console.log('Товара нет в корзине');
            }
        });
    };

    let PrArray =[...basketProduct];


    const addTypeProduct = (i,TypeProduct,idCategory)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type.push(
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: 1
                    }
                );
            }
        });
        return ProductsType;
    };


    const reloadTypeProduct = (i,count)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                //console.log(count,'Кол-во должно быть',ProductsType[i]);
                ProductsType[i].count = count;
            }
        });
        //console.log(num,ProductsType);
        return ProductsType;
    };

    const RemoveFullTypeProduct = (i)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
               // console.log(ProductsType, i);
                    ProductsType.splice(i, 1);
            }
        });
        //console.log(ProductsType[i].type);
        return ProductsType;
    };

    const setOneProduct = (idProduct)=>{
        return {
            id:idProduct,
            count: 1
        };
    };


    const checkCartProduct = (idProduct)=>{
        // console.log("Добавление товара");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;

        if(basketProduct.length){
            //Если в корзине есть допы
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    //id допника совпадает с переданным допником
                    ProductInBasket = true;
                    ProductTypeInBasket = true;
                    //console.log('Совпанеие есть, товар есть в корзине');
                    PrArray = [...basketProduct];
                    reloadTypeProduct(i,product.count+1);
                }
            });

            if(!ProductInBasket){
                //Допника нет в корзине
                PrArray = [...basketProduct];
                PrArray.push(setOneProduct(idProduct));
            }
        }else{
            //Корзина пуста, добавляем первый допник
            PrArray = [setOneProduct(idProduct)];
        }
    };


    const RemoveFullProduct = (idProduct)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(PR.id === idProduct){
                //console.log('Удаляем весь товар '+idProduct, PR);
                ProductsType.splice(n, 1);
            }
        });
        PrArray = [...ProductsType];
    };

    const checkCartProductRemove = (idProduct)=>{
        // console.log("Добавление товара");
        //return false;

        let ProductInBasket = false;

        let RemoveAllProduct = true;

        if(basketProduct.length){
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    ProductInBasket = true;

                    if(product.count<=1){
                        //console.log('---00000----',product);
                        //RemoveFullTypeProduct(i);
                    }else{
                        if(product.count>1){
                            RemoveAllProduct = false;
                        }
                        //console.log('---0011111100----',product);
                        PrArray = [...basketProduct];
                        reloadTypeProduct(i,product.count-1);
                    }
                }

            });

             if(RemoveAllProduct){
                RemoveFullProduct(idProduct)
             }

        }else{
            //Корзина пуста, добавляем первый товар
           // console.log('Каким то хреном вот');
            PrArray = [];
        }
    };

    // const addProductBasketHandler = useCallback((id,type_id) => {
    //     checkCartProduct(id,type_id)
    //     dispatch(addProductBasket(PrArray));
    // }, [dispatch,PrArray]);


    const addProductBasketHandler = (id) => {
        checkCartProduct(id);
        dispatch(addProductBasketTopping(PrArray));
    };


    const removeProductBasketHandler = (id) => {
        checkCartProductRemove(id);
        dispatch(addProductBasketTopping(PrArray));
    };


    const returnButtonIncart = ()=>{
        reloadBasketData(Product.id);

        if(inCart){
            return (<View style={styles.BlockButtonCount} >
                    <TouchableOpacity activeOpacity={0.7} onPress={() => removeProductBasketHandler(Product.id)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>-</Text>
                        </View>
                    </TouchableOpacity>
                    <View style={styles.CountProductInCart}>
                        <Text style={styles.CountProductInCartText}>{countInCart}</Text>
                    </View>
                    <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>+</Text>
                        </View>
                    </TouchableOpacity>
                </View>
            )
        }else{
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id)}>
                    <View style={styles.ButtonBlock}>
                        <Text style={styles.buttonText}>В корзину</Text>
                    </View>
                </TouchableOpacity>
            )
        }
    };

    return returnButtonIncart()
};

const styles = StyleSheet.create({
    BlockButtonCount:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        // width: 100
    },
    CountProductInCart:{
        color: "#252622",
        paddingHorizontal: 10
    },
    CountProductInCartText:{
        color: "#252622",
    },
    ButtonBlock: {
        paddingHorizontal:13,
        paddingVertical:5,
        paddingBottom:7,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:10
    },
    buttonText:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:14,
        color:THEME.TEXT_COLOR
    }
});
