import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity, Platform
} from 'react-native'

export const OneFeatures = ({ feature, onOpen }) => {


    const FeaturesList = ()=>{
        let statusReturn = false;
      const RenderRes = Object.keys(feature.features).map((id,t)=>{
          if(feature.features[id].text != '' & feature.features[id].text != ' ') {
              statusReturn = true;
              return (<View style={styles.featureRow} key={"FeaturesStr_" + id}>
                  <Text>{feature.features[id].title}</Text><Text>{feature.features[id].text} {feature.features[id].unit}</Text>
              </View>)
          }else{
              return false;
          }
      });
      return statusReturn === true ? RenderRes : false;
    };


    const DataRender = FeaturesList();

    if(DataRender === false){
        return false;
    }else{
        //console.log(DataRender);
    }

    return (
        feature.show === 'show' ?
                <View style={styles.featuresBlock}>
                    {feature.title !== '' ?
                        <View style={styles.textWrap}>
                            <Text style={styles.Title}>{feature.title.trim()}</Text>
                        </View>
                        :
                        false
                    }
                    <View style={styles.textWrap2}>
                        {DataRender}
                    </View>
                </View>
                :
                false

    )
};

const styles = StyleSheet.create({
    featuresBlock: {
        marginTop: 0,
        overflow: 'hidden',
        // borderBottomWidth:1,
        borderTopWidth:1,
        borderColor:"#eeeeee",
        width:"100%"
    },
    textWrap:{
        paddingVertical:0,
        paddingTop:Platform.OS === "android" ? 10 : "3%",
        paddingHorizontal:2,
    },
    textWrap2:{
        paddingVertical:0,
        paddingTop:Platform.OS === "android" ? 10 : "3%",
        paddingHorizontal:2,
        paddingBottom:Platform.OS === "android" ? "3%" : "3%",
    },
    featureRow:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
    },
    Title:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    }
});
