import React , {useEffect,useState}from 'react';
import {Image, StyleSheet, Text, View} from "react-native";
import {Group} from "../../../templateComponents/Group/GroupMain";
import {ModyficateBlock} from "../../Catalog/Product/Modyficate/ModyficateBlock";
import {GetPriceProductBasketDiscount} from "../getPriceProductBasket";
import {ButtonInCart} from "../../Catalog/ButtonInCart";
import {ConditionsPromokod} from "../PromokodeModal/ConditionsPromokod";
import {useSelector} from "react-redux";

export const InfoBottomBlock = ({productBasketAll = [],allProducts={}, basketTopping = [],DopProductList=[], discountSum = 0, sumDeliverys = 0})=>{

    const [countTopping,setCountTopping] = useState(0);
    const [priceTopping,setPriceTopping] = useState(0);
    const [sumDiscount,setsumDiscount] = useState(0);
    const [sumDiscountThreeForeTwo,setsumDiscountThreeForeTwo] = useState(0);
    const [sumDiscountPromokode,setsumDiscountPromokode] = useState(0);

    const ThreeForTwoActive = useSelector(state => state.post.ThreeForTwoActive);
    const SumDiscountThreeForTwo = useSelector(state => state.post.SumDiscountThreeForTwo);
    const promokodeActive = useSelector(state => state.post.promokodeActive);
    const DataPromokodeDiscountProduct = useSelector(state => state.post.dataPromokodeDiscoun);



    const getCountTopping = ()=>{
     //  console.log(productBasketAll);
        let counts = 0;
        let Prices = 0;
        basketTopping.forEach((d,i)=>{
            counts +=d.count*1;
            Prices +=((d.count*1)*getPriceTopping(d.id))
        });
        setCountTopping(counts);
        setPriceTopping(Prices);
    };

    const getPriceTopping = (idT)=>{
        let Price = 0;
        DopProductList.forEach((product,i)=>{
            //console.log(product);
            if(product.id*1 === idT*1){
                Price = product.price*1;
            }
        });
        return Price;
    };


    const AllProductDiscounts = ()=>{
        let AllSumDiscount = 0;
        let AllCountProductDiscountPromokode = 0;
        let SumProductDiscountPromokode = 0;

        productBasketAll.map((productBasket,ii)=>{
            productBasket.type.map((typeProducts,numbs)=>{

                let product = [];

                if(typeProducts.categoryId in allProducts){
                    //Если категорния есть
                    if(productBasket.id in allProducts[typeProducts.categoryId]['ProductList']){
                        //Если товар есть
                        if(allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].lenght<=0){
                          //  console.log('Нет нет нет');
                            return false;
                        }else
                        {
                            let statusTypeProduct = false;
                            let numberArr = 0;
                            allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].forEach((dataProduct, numb) => {
                                if(typeProducts.type_id == dataProduct['type_product']){
                                    //Если такой тип товара есть
                                    //Идем по ретурну в конце функции------>>>
                                    statusTypeProduct = true;
                                    numberArr = numb;
                                }
                            });

                            if(statusTypeProduct === false){

                            }else{
                                product = {...allProducts[typeProducts.categoryId]['ProductList'][productBasket.id][numberArr],id:productBasket.id};
                                //----->>>>
                                if(promokodeActive === true){
                                    let ListProductDiscount = DataPromokodeDiscountProduct.listProductInCartForPromokode;
                                    //console.log('Товары по промокоду, для вычисления минус скидка',DataPromokodeDiscountProduct);
                                    let  statusMinusDiscount = false;
                                    let countProductMinus = 0;
                                    ListProductDiscount.forEach((d,n)=>{
                                        if(productBasket.id == d.id){
                                            if(typeProducts.type_id == d.type_id){
                                                statusMinusDiscount = true;
                                                countProductMinus = d.count*1;
                                                AllCountProductDiscountPromokode += d.count*1;
                                                SumProductDiscountPromokode +=d.discountOneProduct*d.count;
                                                //Товар учитывается в промокоде, значит удаляем из общей скидки
                                            }
                                        }
                                    });
                                    if(statusMinusDiscount ===  true){
                                        // console.log('Должны вычесть скидку');
                                        let CountMinus = (typeProducts.count*1)-(countProductMinus*1);
                                        if(CountMinus == '0'){
                                            // console.log('Товаров меньше 1, значит скидка не учена за него ',GetPriceProductBasketDiscount(product, typeProducts.count, typeProducts?.modifyList));
                                        }else {
                                            // console.log('Товаров больше 1 , значит скидка будет учтена');
                                            AllSumDiscount += GetPriceProductBasketDiscount(product, CountMinus, typeProducts?.modifyList);
                                        }
                                    }
                                    else{
                                        AllSumDiscount += GetPriceProductBasketDiscount(product,typeProducts.count, typeProducts?.modifyList);
                                    }
                                }else{
                                    AllSumDiscount += GetPriceProductBasketDiscount(product,typeProducts.count, typeProducts?.modifyList);
                                }
                            }
                        }
                    }
                }
            });
        });
        //console.log(AllSumDiscount);

        // let floatSum = AllSumDiscount+discountSum; // ут какая то ошибка, и суммы дублируются (вернее суммируются, скидка считается дважды)
        let floatSum = AllSumDiscount;

        // console.log('Общая скидка составляет ',AllSumDiscount);

        if(ThreeForTwoActive === true) {
            setsumDiscountThreeForeTwo(parseFloat(SumDiscountThreeForTwo.toFixed(0)));
        }

        if(AllCountProductDiscountPromokode > 0) {
            setsumDiscountPromokode(parseFloat(SumProductDiscountPromokode.toFixed(0)));
        }


        setsumDiscount(parseFloat(floatSum.toFixed(0)));
    };


    const sumDeliveryOrder = ()=>{
        if(sumDeliverys<=0){
            return (<Text>Бесплатно</Text>)
        }
      return (<Text>{sumDeliverys} ₽</Text>);
    };




    useEffect(()=>{
        getCountTopping();
        AllProductDiscounts();
    });

    const getTextTopping = ()=>{

        let ToppingText = 'соуов';

        if(countTopping <=0 && countTopping<1){
            ToppingText = "соусов";
        }
        if(countTopping >=1 && countTopping<=1){
            ToppingText = "соус";
        }
        if(countTopping >=2 && countTopping<=4){
            ToppingText = "соуса";
        }
        if(countTopping >=5 && countTopping<=20){
            ToppingText = "соусов";
        }

        if(countTopping >=21 && countTopping<=21){
            ToppingText = "соус";
        }

        if(countTopping >=21 && countTopping<=21){
            ToppingText = "соус";
        }

        if(countTopping >=22 && countTopping<=24){
            ToppingText = "соуса";
        }
        if(countTopping >=25 && countTopping<=30){
            ToppingText = "соусов";
        }

        return  ToppingText;
    };

    return (
        <View style={styles.wrapper}>
            <View style={styles.Between}><Text>{countTopping} {getTextTopping()} </Text><Text> {priceTopping} ₽</Text></View>
            <View style={styles.Between}><Text>Скидки по акциям</Text><Text> {sumDiscount} ₽</Text></View>
            {ThreeForTwoActive !== false ?
                <View style={styles.Between}><Text>Скидка 3 = 2</Text><Text> {sumDiscountThreeForeTwo} ₽</Text></View>
                : false}
            {promokodeActive !== false ?
                <View style={styles.Between}><Text>Скидка по промокоду: </Text><Text> {sumDiscountPromokode} ₽</Text></View>
                : false}
            {/*<View style={styles.Between}><Text>Стоимость доставки</Text><Text>{sumDeliveryOrder()}</Text></View>*/}
        </View>
    )
};

const styles = StyleSheet.create({
    wrapper:{
        paddingVertical: 5
    },
    Between:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        paddingVertical:2,
        paddingHorizontal:"3%"
    }
});