import React, { useState } from "react";
import {View, StyleSheet, Pressable, Text, Modal, Dimensions, ActivityIndicator, ScrollView} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {
    setListDataDelievryList,
    setModalStatus,
    setPreorderData,
    setPreorderDataStatus
} from "../../../store/actions/post";
import {THEME} from "../../../theme";
import {ServerDB} from "../../../serverDb";
import {Ionicons} from "@expo/vector-icons";
import {ContentModalTimeDelivery} from "./ContentModalTimeDelivery";

export const SelectTimeDelivery = ({
                                       item = [], //список option компонентов (массив value, title)
                                       fontSize = 16,
                                       keys = null
                                   }) => {

    const dispatch = useDispatch();
    const StatusModal = useSelector(state=>state.post.modalStateStatus.MODAL_TIME_DELIVERY);
    const TimeValueShow = useSelector(state=>state.post.preorderData.timeDelivery);


    const loadingDataListServer = async ()=>{
        await dispatch(setPreorderDataStatus(false));
        let ListDateServerResult = await ServerDB.getDateList();
       //console.log(ListDateServerResult);
        await dispatch(setListDataDelievryList(ListDateServerResult));
    };


    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };


    const GetModalOption = ()=>{
        return (
            <Modal
                animationType="none"
                transparent={true}
                visible={StatusModal}
                onShow={()=> {
                    // Alert.alert("Модальное окно было открыто");

                }}
                onRequestClose={() => {

                    //setModalVisibleS(!StatusModal);
                    handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                }}
                key={"Modal_Time_"+keys}
            >
                <ContentModalTimeDelivery />
            </Modal>
        )
    };

    const getNowTypeDelivery = ()=>{
        if(TimeValueShow === 0){
            return (<Text style={{color:"red",textAlign:"center"}}>Выберите время</Text>);
        }else{
            return TimeValueShow;
        }
    };

    const GetSelectedValue = ()=>{
        return (
            <Pressable
                onPress={()=>{
                    //setModalVisibleS(true);
                    handleSetStatusModal("MODAL_TIME_DELIVERY",true);
                    loadingDataListServer();
                }}
                style={{
                    width:"100%"
                }}
            >
                <GetModalOption item={item}/>
                <Text style={[styles.defaultValue,{fontSize:fontSize}]}>
                    {/*{item[selectedValueKey].title}*/}
                    {getNowTypeDelivery()}
                </Text>
            </Pressable>
        )
    };


    return (
        <View style={styles.container}>
            <GetSelectedValue />
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:30,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"3.1%"
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    modalContent:{
        width:"100%",
        height:"100%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:70,
        marginVertical: "5%",
        marginHorizontal:"3%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        paddingHorizontal:"5%",
        borderRadius: 10,
        textAlign:"center"
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});