import React, {useEffect, useState} from 'react';
import {View, Text, StyleSheet, SafeAreaView, ScrollView, Pressable} from 'react-native'
import { HeaderButtons, Item } from 'react-navigation-header-buttons'
import { AppHeaderIcon } from '../../components/AppHeaderIcon'
import { useDispatch, useSelector } from 'react-redux';
import {updateCountNoticeNew, UpdatePushNoticeList} from "../../store/actions/post";
import {THEME} from "../../theme";
import {ServerDB} from "../../serverDb";
import {NewAdresScreen} from "../DeliveryBlock/NewAdresScreen";


export const PushNoticeListScreen = ({}) => {

    const [allCountNewNotice,setCountNewNotice] = useState(0);
    const [allNoticeList,setAllNoticeList] = useState(false);
    const dispatch = useDispatch();
    const countPushListNew = useSelector(state => state.post.pushNoticeCountNew);

    const CityData = useSelector(state => state.post.cityData);

    const allPushList = useSelector(state => state.post.pushNoticeList);

    const setNoticeStatus = async (idNotice,numberNoticeList)=>{
        const ResultServer = await ServerDB.setNoticeUserStatus(1,idNotice);
        try {

            let newArr = {...allPushList};


              //console.log("С сервера при замене статуса push уведомления ",ResultServer);
            switch (ResultServer.status) {
                case "Success":
                    newArr[numberNoticeList].status = "1";
                    // setAllNoticeList(newArr);
                    await dispatch(UpdatePushNoticeList(newArr));
                    setCountNewNotice(allCountNewNotice-1);
                    break;
                default: console.log('Ошибка установки статуса push уведомления',ResultServer);
            }
        }catch (e) {
            console.log(e);
        }
    };


    const countNewPushNotice = (allPushList)=>{
        let counts = 0;

        // console.log("Обновляем pushList");
        // console.log(allPushList);

        const allNoticeReturn = Object.keys(allPushList).map((data,number)=>{
            if(Number(allPushList[data].readNotice) === 0){
                counts++;
                return(
                    <Pressable
                        style={styles.blockOneNotice}
                        onPress={() => {
                              //console.log('Нажали на уведомление');
                            setNoticeStatus(allPushList[data].id,data);
                        }}
                    >
                        <View style={{justifyContent: "space-between",flexDirection:"row"}}>
                            <View style={{width:"93%"}}>
                                <Text style={styles.titleNoticeActive}>{allPushList[data].title}</Text>
                                <Text style={styles.bodyNoticeActive}>{allPushList[data].body.replace(/(\\n)/g, '\n')}</Text>
                            </View>
                            <View style={{width:"5%",flexDirection:"row",justifyContent:"center",alignItems:"center"}}>
                                <Text style={{backgroundColor:THEME.MAIN_COLOR,width:10,height:10,borderRadius:50}}></Text>
                            </View>
                        </View>
                        <View style={styles.dateLine}>
                            <Text style={styles.dateLineTextActive}>{getDatePushNotice(allPushList[data].date)}</Text>
                        </View>
                    </Pressable>
                )
                // console.log('Плюсуем')
            }else{
                return(
                    <View style={styles.blockOneNotice}>
                        <View >
                            <Text style={styles.titleNotice}>{allPushList[data].title}</Text>
                            <Text style={styles.bodyNotice}>{allPushList[data].body.replace(/(\\n)/g, '\n')}</Text>
                        </View>
                        <View style={styles.dateLine}>
                            <Text style={styles.dateLineText}>{getDatePushNotice(allPushList[data].date)}</Text>
                        </View>
                    </View>

                )
            }

        });

        // console.log(counts);
        setAllNoticeList(allNoticeReturn);
        return counts;

    };


    const getNumberPlusNull = (str)=>{
        let returns = 0;

        if(str*1 <= 9){
            returns = '0'+str;
        }else{
            returns = str;
        }
        return returns;
    };

    const getMountsTextRus = (number,R)=>{

        let ReturnMounth = 'Не определен';
        let ReturnMounthR = 'Не определен';

        switch (number) {
            case 0: ReturnMounth = 'Январь'; ReturnMounthR = 'Января'; break;
            case 1: ReturnMounth = 'Февраль'; ReturnMounthR = 'Февраля'; break;
            case 2: ReturnMounth = 'Март'; ReturnMounthR = 'Марта'; break;
            case 3: ReturnMounth = 'Апрель'; ReturnMounthR = 'Апреля'; break;
            case 4: ReturnMounth = 'Май'; ReturnMounthR = 'Мая'; break;
            case 5: ReturnMounth = 'Июнь'; ReturnMounthR = 'Июня'; break;
            case 6: ReturnMounth = 'Июль'; ReturnMounthR = 'Июля'; break;
            case 7: ReturnMounth = 'Август'; ReturnMounthR = 'Августа'; break;
            case 8: ReturnMounth = 'Сентябрь'; ReturnMounthR = 'Сентября'; break;
            case 9: ReturnMounth = 'Октябрь'; ReturnMounthR = 'Октября'; break;
            case 10: ReturnMounth = 'Ноябрь'; ReturnMounthR = 'Ноября'; break;
            case 11: ReturnMounth = 'Декабрь'; ReturnMounthR = 'Декабря'; break;
        }

        switch (R) {
            case 1: return ReturnMounth;
            case 2: return ReturnMounthR;
            default: return ReturnMounth
        }

    };


    const getDatePushNotice = (times)=>{
        let newDates = new Date(times*1000);

        // return ()
        return (<Text>
            {newDates.getDate()+" "}
            {getMountsTextRus(newDates.getMonth(),2)+" "}
            {newDates.getFullYear()+" в "}
            {getNumberPlusNull(newDates.getHours())+':'+getNumberPlusNull(newDates.getMinutes())}
        </Text>);
    };

    useEffect( ()=> {
            const asyncFn = async () => {
                let countsList = await countNewPushNotice(allPushList);
                setCountNewNotice(countsList);
                // console.log('Всего уведомлений ',countsList);
                // dispatch(updateCountNoticeNew(countsList));
            };
            asyncFn();
        }
        ,[dispatch,allPushList]);


    return (
        <View style={styles.center}>
            <View style={styles.allPushCountBlock}>
                <Text style={styles.allPushCountText}>Всего push уведомлений: {allCountNewNotice}</Text>
            </View>
            <ScrollView
                style={{width:"100%"}}
                // onScroll={true}
                // scrollEventThrottle={16}
                // stickyHeaderIndices={[4]}
                scrollEnabled={true}
                nestedScrollEnabled={true}
                // contentContainerStyle={styles.contentContainer}
                disableIntervalMomentum={true}
                // snapToInterval={ 100 }
                // endFillColor="black"
                // fadingEdgeLength={1}
            >
                {allNoticeList}
            </ScrollView>
        </View>
    )
}

PushNoticeListScreen.navigationOptions = ({ navigation, route }) => ({
    headerTitle: 'Уведомления',
    headerLeft: (
        <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
            <Item
                title='Toggle Drawer'
                iconName='arrow-back'
                onPress={() => navigation.goBack()}
            />
        </HeaderButtons>
    )
})

const styles = StyleSheet.create({
    center: {
        flex: 1,
        justifyContent: 'flex-start',
        alignItems: 'center',
        // paddingVertical:"2%"
    },
    version: {
        fontFamily: 'open-bold, segoe ui, Arial'
    },
    allPushCountBlock:{
        width:"100%",
        height:40,
        backgroundColor:THEME.BACKGROUND_COLOR_TABS_MENU_DEFAULT,
        paddingVertical:"2%",
    },
    allPushCountText:{
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:18,
        textAlign:"center"
    },
    blockOneNotice:{
        width:"95%",
        paddingVertical: 7,
        paddingHorizontal:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        marginVertical: 10,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.22,
        shadowRadius: 2.22,

        elevation: 3,
        marginLeft:"2.5%",
        borderRadius:10
    },
    titleNoticeActive:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize: 16,
        borderBottomWidth:1,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT,
        paddingBottom:5,
        color:THEME.TEXT_COLOR_TABS_MENU_ACTIVE
    },
    titleNotice:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize: 16,
        borderBottomWidth:1,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT,
        paddingBottom:5,
        color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT
    },
    bodyNoticeActive:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize: 13,
        paddingTop:5
    },
    bodyNotice:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontSize: 14,
        paddingTop:5
    },
    dateLine:{
        marginTop:10,
        paddingVertical:10,
        textAlign:"left",
        borderTopWidth:1,
        borderTopColor:THEME.BORDER_COLOR_DEFAULT
    },
    dateLineText:{
        fontSize:14,
        fontFamily:"open-bold, segoe ui, Arial"
    },
    dateLineTextActive:{
        fontSize:14,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    }
})
