import React, { useState, useEffect } from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView,
    ScrollView,
    Dimensions, TextInput
} from "react-native";
import {THEME} from "../../../theme";
import {AntDesign, Ionicons} from '@expo/vector-icons';
import {useDispatch, useSelector} from "react-redux";
import {
    loadProductScreen,
    loadSlideHeader,
    setCitySystem,
    loadCatalogProduct,
    loadSlideHeaderStatus,
    setCityUserSystemPhone,
    setModalStatus,
    setDefaultStateOrder,
    loadAuthInfo,
    setActiveOrderScreenData
} from "../../../store/actions/post";
import { MaterialIcons } from '@expo/vector-icons';
import SwipeUpDown from 'react-native-swipe-up-down';
import ModalPage from "../../../templateComponents/ModalPage/ModalPage";
import {RecivingOrder} from "./Receiving";
import {PaymentType} from "./PaymentType";
import {PointsBlock} from "./PointsBlock";
import {ButtonEbee} from "../../../templateComponents/Button/ButtonEbee";
import {NumberFormatPrice} from "../../../templateComponents/NumberFormat/NumberFormat";
import {Group} from "../../../templateComponents/Group/GroupMain";
import {ServerDB} from "../../../serverDb";
import {GetAllPriceBasketPreorderToppingSum} from "../GetAllPriceBasketLimitTopping";
import {ModulInformerWorkTimeInBasket} from "../../../templateComponents/InformerWorkTimeBasket/ModulInformerWorkTimeInBasket";
import {ACTIVE_ORDER_SCREEN_RELOAD, REMOVE_POST} from "../../../store/types";
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";



export const OrderModal = ({navigation,titleCity,keys,route}) => {
    const [modalVisible, setModalVisible] = useState(true);
    const dispatch = useDispatch();
    const StatusModal = useSelector(state=>state.post.modalStateStatus.MODAL_ORDER);
    const CityData = useSelector(state=>state.post.cityData.cityId);
    const StatusAllModal = useSelector(state=>state.post.modalStateStatus);

    const statusDelivery = useSelector(state=>state.post.typeDeliverySystem);
    const AffiliateData = useSelector(state=>state.post.affiliateSelect);
    const AdresData = useSelector(state=>state.post.adressSelect);

    const [minSumOrder,setMinSumOrder] = useState(0);
    const [minSumOrderFree,setMinSumOrderFree] = useState(0);
    const [sumDeliveryOrder,setSumDeliveryOrder] = useState(0);
    // const [komment,setKomment] = useState(null);
    const komment = useSelector(state=>state.post.textKomment);

    const sumItogBasket = useSelector(state => state.post.basketSum);
    const sumDiscountPromokode = useSelector(state => state.post.dataPromokodeDiscoun.allSumDiscount);

    useEffect(()=> {
        if (statusDelivery === true) {
            setMinSumOrder(AdresData.diliveryData.min_sum_order);
            setMinSumOrderFree(AdresData.diliveryData.min_sum_free_dilivery);
            // console.log('Доставка должна быть ',AdresData.diliveryData.min_sum_free_dilivery,sumItogBasket,(AdresData.diliveryData.min_sum_free_dilivery*1)>(sumItogBasket*1));
            if((AdresData.diliveryData.min_sum_free_dilivery*1)>(sumItogBasket*1)){
                setSumDeliveryOrder(AdresData.diliveryData.priceDilivery);
            }else{
                setSumDeliveryOrder(0);
            }
            // console.log('Условие по стоимости платной доставки получено', AdresData.diliveryData);

        } else {
            // console.log('Условие по стоимости платной доставки не получено');
            setMinSumOrder(300);
            setMinSumOrder(0);
            setSumDeliveryOrder(0);
        }
    },[statusDelivery,sumItogBasket]);
    // console.log(statusDelivery);
    // console.log(AdresData.diliveryData.min_sum_order);
    // console.log(AdresData.diliveryData.min_sum_free_dilivery);
    // console.log(AdresData.diliveryData.priceDilivery);


    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);
    const TimeValueShow = useSelector(state=>state.post.preorderData.timeDelivery);


    let [animateModal, setanimateModal] = useState(false);

    const allPointsSaleGlobal = useSelector(state => state.post.allPointsSale);


    ///////Данные для отправуи на сервер

    const basket = useSelector(state => state.post.basket);
    const basketTopping = useSelector(state => state.post.basketTopping);
    const idUserSystem = useSelector(state => state.post.AuthUserData.id);

    const priceDelivery = useSelector(state => state.post.priceDeliverySelected);

    const promokodeActive = useSelector(state => state.post.promokodeActive);
    const promokodeData = useSelector(state => state.post.promokodeData);
    const dataPromokodeDiscoun = useSelector(state => state.post.dataPromokodeDiscoun);

    const typePayment = useSelector(state => state.post.typePayment);
    const preorderData = useSelector(state => state.post.preorderData);
    const commentOrder = useSelector(state => state.post.commentOrder);


    const PromokodeErrorInfo = useSelector(state=> state.post.promokodeErrorInfo);

    const PreorderCategoryDopsLimit = useSelector(state => state.post.PreorderCategoryDopsLimit.list);
    const allProducts = useSelector(state => state.post.allProducts);
    const BonusPointsActive = useSelector(state => state.post.bonusPointsActive);





    let sumItogBasketCheckDiscount = 0;

    if(PromokodeErrorInfo.showIconError === true) {
        sumItogBasketCheckDiscount = sumItogBasket;
    }else{
        if(promokodeActive){
            // sumItogBasketCheckDiscount = sumItogBasket-sumDiscountPromokode;
            sumItogBasketCheckDiscount = sumItogBasket;
        }else{
            sumItogBasketCheckDiscount = sumItogBasket;
        }
    }

    const activeGift = useSelector(state => state.post.activeGift);



    const handleSetOrderServer = async ()=>{
        let sumPreorderTopping = GetAllPriceBasketPreorderToppingSum(PreorderCategoryDopsLimit,basket,allProducts);
        let typePaymentSet = typePayment;
        if(typePayment == 3){
            typePaymentSet = 0;
        }
        let ArrayOrder = {
            "typeOrderDevice":6,
            "basketData": {
                'basket': basket,
                'basketTopping': basketTopping,
            },
            "userData":{
                'idUser':idUserSystem,
            },
            "orderData":{
                'typePayment':typePaymentSet,
                'allPointsSale':allPointsSaleGlobal,
                'sumItog':(sumItogBasketCheckDiscount*1)+(sumDeliveryOrder*1),
                'sumDiscountPromokode':sumDiscountPromokode,
                'commentOrder':komment,
                'sumPreorderTopping':sumPreorderTopping
            },
            "deliveryData":{
                'typeDelivery':statusDelivery,
                'adresDelivery':AdresData,
                'adresAffiliate':AffiliateData,
                'priceDelivery':sumDeliveryOrder,
                'preorderData':preorderData
            },
            "promokodeData":{
                'promokodeActive':promokodeActive,
                'showIconError':PromokodeErrorInfo.showIconError,
                'promokodeData':promokodeData,
                'dataPromokodeDiscoun':dataPromokodeDiscoun
            },
            "giftData":activeGift
        };

        //console.log('Отправляем данные на сервер ',ArrayOrder);

        const ResultOrder = await ServerDB.setOrderServer(CityData,idUserSystem,ArrayOrder);

        await dispatch(setDefaultStateOrder());
        await dispatch(loadAuthInfo( false, false, null,  false, null, {}, idUserSystem));

        if(ResultOrder.onlineModal === true){
            navigation.navigate('OnlinePayments', {
                title: 'Онлайн оплата заказа №'+ResultOrder.orderId,
                idOrder:ResultOrder.orderId*1,
                sumPayment:ResultOrder.sumPayment,
                idPointsPaymentToken:ResultOrder.idPointsPaymentToken
            });
        }else {
            let statusReloadOrderScreen = await dispatch(setActiveOrderScreenData(ResultOrder.orderId,true));
            navigation.navigate('OrderListProfile', {
                title: 'Список заказов',
                productData: [],
                idOrderNow: ResultOrder.orderId,
                reloads: true
            });
        }

        //console.log('Нажали завершение заказа');
        handleSetStatusModal("MODAL_ORDER_PRELOADER", false);

    };


    ////////


    //console.log(StatusAllModal);

    const getSumSalePoints = (allSum,allPoints)=> {
            let itog = 0;

            itog = allSum - allPoints;
            if (itog < 0) {
                itog = 0;
            }

            return itog;
        }
    ;

    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const FlatListData = [
        <View style={styles.modalContent}>


            <ModulInformerWorkTimeInBasket navigation={navigation} route={route}/>

            <View style={styles.TitleModal} key={"PaymentBlock"}>
                <Text style={styles.modalTextTitle}>Оплата</Text>
            </View>
            <View style={styles.ChildModal} key={"DeliveryPayment"}>
                <View style={styles.ChildBlock}>
                    <RecivingOrder navigation={navigation} route={route}/>
                </View>
                <View style={styles.ChildBlock} key={"PaymentTypeBlock"}>
                    <Text style={styles.titleChildBlock}>Способ оплаты</Text>
                    <PaymentType/>
                </View>
                {BonusPointsActive === true ?
                <View style={styles.ChildBlock} key={"PointSaleBlock"}>
                    <Text style={styles.titleChildBlock}>Списание бонусов</Text>
                    <PointsBlock/>
                </View>
                    : false
                }
                {/*<View style={styles.ChildBlock} key={"PointSaleBlock"}>*/}
                {/*    <Text style={styles.titleChildBlock}>Комментарий к заказу</Text>*/}
                {/*    <TextInput*/}
                {/*        editable*/}
                {/*        style={styles.inputKomments}*/}
                {/*        onChangeText={setKomment}*/}
                {/*        value={komment}*/}
                {/*    />*/}
                {/*</View>*/}
                <View style={styles.ChildBlock} key={"NoneBlock"}>
                </View>
            </View>
        </View>,

    ];

    const getOkOrderButtonDateDelivery = ()=>{
        if(DateValueShow === 0){
            return true;
        }else{
            if(TimeValueShow === 0){
                return false;
            }else{
                return true;
            }
        }
    };

    // console.log('Что и итоговой переменной на отправку для сервера ',dataPromokodeDiscoun);


    return (
        <ModalPage
            modalVisible={StatusModal}
            PressToanimate={animateModal}
            ContentModal={
                <View
                    scrollEnabled={false}
                    style={styles.containerContent}
                >
                    <FlatList
                        data={FlatListData}
                        listKey={(item,n)=>"ModalContent_Order_"+n}
                        renderItem={({item, index}) => (
                            item
                        )}
                        keyExtractor={(item,n) => "OrderModalContent_"+n}
                    />
                    <Group
                        styles={{
                            position:"absolute",
                            // top:Dimensions.get('window').height*0.6,
                            bottom:0,
                            // bottom:Dimensions.get('window').height*0.18,
                            backgroundColor:"#ffffff",
                            // paddingBottom: "10%", старое значение
                            paddingBottom: "5%",
                            width:"100%",
                            paddingHorizontal:"3%",
                            left:0
                        }} shadow={false}
                    >
                        {statusDelivery === true ?
                            <>
                                <View style={[styles.ChildBlock2,{flexDirection: "row",justifyContent:"space-between",alignItems:"center",marginVertical:0}]}>
                                    <Text >Стоимость заказа</Text>
                                    <Text >{NumberFormatPrice(getSumSalePoints(sumItogBasketCheckDiscount,allPointsSaleGlobal),0,'.',' ')} ₽</Text>
                                </View>
                                <View style={[styles.ChildBlock2,{flexDirection: "row",justifyContent:"space-between",alignItems:"center",marginVertical:0}]}>
                                    <Text>Доставка</Text>
                                    <Text>{NumberFormatPrice(sumDeliveryOrder,0,'.',' ')} ₽</Text>
                                </View>
                                <View style={[styles.ChildBlock2,{flexDirection: "row",justifyContent:"space-between",alignItems:"center",marginVertical:0}]}>
                                <Text style={styles.titleChildBlock}>Итого</Text>
                                <Text style={styles.titleChildBlock}>{NumberFormatPrice((sumItogBasketCheckDiscount*1)+(sumDeliveryOrder*1)-allPointsSaleGlobal,0,'.',' ')} ₽</Text>
                            </View>
                            </>
                            :
                            <View style={[styles.ChildBlock2,{flexDirection: "row",justifyContent:"space-between",alignItems:"center",marginVertical:0}]}>
                                <Text style={styles.titleChildBlock}>Стоимость заказа</Text>
                                <Text style={styles.titleChildBlock}>{NumberFormatPrice(getSumSalePoints(sumItogBasketCheckDiscount,allPointsSaleGlobal),0,'.',' ')} ₽</Text>
                            </View>
                        }

                        {(sumItogBasket)>=minSumOrder ?
                            <ButtonEbee
                                onPress={()=>{
                                    if((sumItogBasket)>=minSumOrder) {
                                        setanimateModal(true);
                                        handleSetStatusModal("MODAL_ORDER", false);
                                        handleSetStatusModal("MODAL_ORDER_PRELOADER", true);
                                        handleSetOrderServer();
                                    }else{
                                        Alert.alert('Заказ не принят','Сумма Вашего заказа меньше, чем минимальная стоимость приема заказа ('+minSumOrder+'р). Добавьте в корзину еще товаров. \nБесплатная доставка от '+minSumOrderFree+'р');
                                    }
                                }}
                                size="xl"
                                borderRadius={20}
                                shadow={false}
                                active={
                                    (sumItogBasket)>=minSumOrder ?
                                        getOkOrderButtonDateDelivery()
                                        :
                                        false
                                }
                            >
                                Оформить заказ
                            </ButtonEbee>
                            :
                            <View style={{textAlign:"center",backgroundColor:"#eeeeee",borderRadius:10,paddingVertical:5}}>
                                <View style={{textAlign:"center"}}><Text style={{textAlign:"center"}}>Минимальная сумма заказа на этот адрес, без учета платной доставки составляет: <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,}}>{NumberFormatPrice(minSumOrder,0, '.',' ')} ₽</Text></Text></View>
                                <View style={{textAlign:"center"}}><Text style={{textAlign:"center"}}>Для совершения заказа нужно еще: <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,}}>{NumberFormatPrice(minSumOrder-(sumItogBasket),0, '.',' ')} ₽</Text></Text></View>
                            </View>
                                    }
                    </Group>
                </View>
            }
            HeaderStyle={styles.headerContent}
            ContentModalStyle={styles.Modal}
            HeaderContent={
                <View style={styles.containerHeader}>
                    <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? styles.topIndicatorWeb : Dimensions.get('window').width >460 ? styles.topIndicatorWeb : styles.topIndicator}>
                        {/*{Dimensions.get('window').width >= 421 ? */}
                        {/*    <ButtonEbee*/}
                        {/*    onPress={()=>{*/}
                        {/*        handleSetStatusModal("MODAL_ORDER",false);*/}
                        {/*        //setModalVisible(!modalVisible);*/}
                        {/*        setanimateModal(false);*/}
                        {/*    }}*/}
                        {/*    size={'l'}*/}
                        {/*    borderRadius={20}*/}
                        {/*>*/}
                        {/*    Закрыть*/}
                        {/*</ButtonEbee>*/}
                        {/*    : */}
                        {/*    false}*/}
                        {getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ?
                            <AntDesign name="closecircleo" size={20} color={THEME.MAIN_COLOR}
                                       onPress={()=>{
                                           handleSetStatusModal("MODAL_ORDER",false);
                                           //setModalVisible(!modalVisible);
                                           setanimateModal(false);
                                       }}
                                       style={{
                                           alignSelf:'end',
                                           // paddingRight:'2%',
                                           paddingTop:10,
                                           // paddingRight:15
                                       }}
                            />
                            : Dimensions.get('window').width >460 ?
                            <AntDesign name="closecircleo" size={20} color={THEME.MAIN_COLOR}
                                       onPress={()=>{
                                           handleSetStatusModal("MODAL_ORDER",false);
                                           //setModalVisible(!modalVisible);
                                           setanimateModal(false);
                                       }}
                                       style={{
                                           alignSelf:'end',
                                           // paddingRight:'2%',
                                           paddingTop:10,
                                           // paddingRight:15
                                       }}
                            />
                            :
                            false}
                    </View>
                </View>
            }
            onClose={() => {
                handleSetStatusModal("MODAL_ORDER",false);
                //setModalVisible(!modalVisible);
                setanimateModal(false);
            }}

        />
    );
};

const styles = StyleSheet.create({
    centeredViewActive: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        // backgroundColor:"rgba(0,0,0,0.40)"
    },
    centeredView: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 10,
        //backgroundColor:"rgba(0,0,0,0.40)"
    },
    PressableClose:{
        height:"100%",
        width:"100%"
    },

    ChildBlock:{
        marginVertical:10
    },
    ChildBlock2:{
        marginVertical:5,
        paddingLeft:"2%"
    },
    titleChildBlock:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16,
        marginBottom:10
    },

    centeredViewWindow: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        backgroundColor:"rgba(0,0,0,0.40)"
    },
    modalView: {
        width:"100%",
        height:"80%",
        margin: 0,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 0,
        position:"absolute",
        alignItems: "center",
        bottom:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5
    },

    topIndicator:{
        width:40,
        height:3,
        borderRadius:10,
        marginVertical:4,
        backgroundColor:"#dddddd"
    },
    topIndicatorWeb:{
        // width:40,
        width:'32%',
        // height:3,
        height:'30%',
        borderRadius:10,
        marginVertical:4,
        alignSelf:'end',
        paddingRight:15
        // backgroundColor:"#dddddd"
    },
    modalContent:{
        width:"100%",
        paddingHorizontal:"3%",
        paddingVertical: 3,
        // paddingBottom:Dimensions.get('window').height*0.2,
        paddingTop:'4%',
        overflow: 'hidden'
        //paddingBottom:Platform.OS === 'android' ? 0 : 0,
        // height:Dimensions.get('window').height*0.8
    },

    TitleModal:{
        width:"100%",
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        fontSize:18,
        //backgroundColor:"#ff7300"
    },
    modalTextTitle:{
        // backgroundColor:"#ff7300",
        textAlign:"left",
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:18
    },

    ChildModal:{
        marginVertical:0
    },






    containerContent: {
        flex: 1,
        marginTop: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 20 : 0,
        paddingHorizontal:"1%",
        paddingVertical:"1%",
        // paddingBottom:"30%", старое значение
        paddingBottom:"18%",
        backgroundColor:"#fafafa",
        //height:Dimensions.get('window').height*0.84
        overflow:'hidden'
    },
    containerHeader: {
        flex: 1,
        alignContent: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        height: 30,
        backgroundColor: "#fafafa",
        borderTopLeftRadius:10,
        borderTopRightRadius:10
    },
    headerContent:{
        marginTop: 0,
        height:20,
        // right:0
    },
    Modal: {
        backgroundColor: '#005252',
        marginTop: 0,
    },
    inputKomments:{
        backgroundColor:"#ffffff",
        paddingVertical:10,
        paddingHorizontal:15,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5,
        borderRadius:7
        // height:70
    }
});