import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    Text,
    TouchableOpacity,
    Pressable, Alert
} from 'react-native'
import {Group} from "../../templateComponents/Group/GroupMain";
import {TypeDataReturnRender} from "../../components/TypeDataReturnRender";
import {THEME} from "../../theme";
import { MaterialIcons } from '@expo/vector-icons';
import {setActiveCategoryId, setStatusReloadCategoryCatalog} from "../../store/actions/post";
import {useDispatch, useSelector} from "react-redux";

export const OneAdress = ({ dataAdress, onPress , onOpen, onOpenProduct = null , statusDelivery = false}) => {

    const dispatch = useDispatch();

    const SelectAdres = useSelector(state=>state.post.adressSelect);

    const TitleAdress = ({data})=>{
        return(
            <Group
                // title={data.comment != '' ? data.comment : null}
                bottomLabel={<><StreenAdress dataAdress={data} fontSize={13}/><GetDeliveryPriceInfo data={data}/></>}
                styles={{
                    paddingVertical: "3%",
                    paddingHorizontal: "3%",
                    shadowOffset: {
                        width: 0,
                        height: 0,
                    },
                    shadowOpacity: 0,
                    shadowRadius: 0,
                    elevation: 0,
                    borderBottomWidth:1,
                    borderTopWidth:0,
                    borderColor:"#dddddd",
                    marginVertical: 0,
                    marginBottom:0
                }}
            >
                <Text>
                    {
                        data.comment != '' ?
                            <CommentAdres title={data.comment} fontWeight="bold" streetStop={true} fontSize={14}/> :
                            <StreenAdress dataAdress={data} fontWeight="bold" streetStop={true} fontSize={14}/>
                    }
                </Text>
            </Group>
        )
    };

    const CommentAdres = ({title, fontWeight = 'regular', streetStop = false, fontSize = 14}) =>{
        return (
            <Text style={fontWeight === 'regular' ? [styles.Fontregular,{fontSize:fontSize}] : [styles.Fornbold,{fontSize:fontSize}] }>
                {title}
            </Text>
        )
    };

    const StreenAdress = ({dataAdress, fontWeight = 'regular', streetStop = false, fontSize = 14}) =>{
        return (
            <Text style={fontWeight === 'regular' ? [styles.Fontregular,{fontSize:fontSize}] : [styles.Fornbold,{fontSize:fontSize}] }>
                {!streetStop ?
                    <>
                        <TypeDataReturnRender data={dataAdress.city} type="" separator={false}/>
                        <TypeDataReturnRender data={dataAdress.street} type="ул. " separator=", "/>
                        <TypeDataReturnRender data={dataAdress.home} type="д. " separator=", "/>
                        <TypeDataReturnRender data={dataAdress.kvartira} type="кв. " separator=", "/>
                    </>
                    :
                    <>
                        <TypeDataReturnRender data={dataAdress.city} type="" separator={false}/>
                        <TypeDataReturnRender data={dataAdress.street} type="ул." separator=", "/>
                    </>
                }
            </Text>
        )
    };

    // const TypeDataReturn = ({data = null, type = null, separator = null})=>{
    //     let returnData = null;
    //   if(data === null || data == '' || data == ' '){
    //       returnData = null;
    //   }else{
    //
    //       data = data.replace(type,'').trim();
    //
    //
    //       separator === null || separator === false ? returnData = type+''+data : returnData = separator+''+type+''+data
    //   }
    //
    //   return (returnData);
    // };

    const allCategory = useSelector(state => state.post.allCategory);

    const returnInfoCheckAdres = (id)=>{
        if(SelectAdres === null){return false;}else {
            if (SelectAdres.id === id) {
                return true;
            } else {
                return false;
            }
        }
    };

    const GetDeliveryPriceInfo = ({data})=>{
        if(data === null){return false;}
      return(
          <View style={styles.ViewInfoAdresPriceAll}>
            {/*<GetListOneData title="Мин. заказ" value={data.diliveryData.min_sum_order} />*/}
            <GetListOneData title="Доставка: " value={data.diliveryData.priceDilivery} />
            <GetListOneData title="Бесплатно от: " value={data.diliveryData.min_sum_free_dilivery} />
          </View>
      )
    };

    const GetListOneData = ({title,value})=>{
        return(
            <View style={styles.ViewInfoAdresPrice}><Text style={styles.Titles}>{title}</Text><Text style={styles.Datas}>{value} ₽</Text></View>
        )
    };



    if(statusDelivery === true) {

        return (
            <TouchableOpacity activeOpacity={0.5} onPress={() => {
                // onOpen(dataAdress);
                // console.log('Нажали на адрес ',dataAdress);
                onPress(dataAdress);
                let ActiveCategorysCheck = allCategory[0].id;
                let OkAddress = [];
                let DataOff = dataAdress?.OffCategoryData?.OffCategory;
                if(typeof (dataAdress?.OffCategoryData?.OffCategory) === "object") {
                    DataOff = Object.keys(dataAdress?.OffCategoryData?.OffCategory);
                }

                allCategory.forEach((dc, nc) => {
                    let AddressOk = true;
                    DataOff?.forEach((ad, nd) => {
                        if (dc.id == ad){
                            // console.log(dc.id+' == '+ad);
                            AddressOk = false;
                        }
                    });
                    if(AddressOk === true) {
                        OkAddress.push(dc.id);
                    }
                });
                // console.log('OkAddress',OkAddress);
                if(OkAddress.length > 0) {
                    dispatch(setActiveCategoryId(OkAddress[0]));
                }else{
                    dispatch(setActiveCategoryId(ActiveCategorysCheck));
                }
                //dispatch(setStatusReloadCategoryCatalog(status));
            }}>
                <View style={styles.adressBlock}>
                    <TitleAdress data={dataAdress}/>
                    {returnInfoCheckAdres(dataAdress.id)
                        ?
                        <View style={styles.miniInfoBlokRight}>
                            <MaterialIcons name="check" size={24} color="#16A085" />
                        </View>
                        :
                        false
                    }
                </View>
            </TouchableOpacity>
        )
    }else{
        return (
            <Pressable
                activeOpacity={1}
                onPress={()=>
                    Alert.alert(
                        "Доставка не возможна",
                        "К сожалению, на данный адрес, доставка не осуществляется.",
                        [
                            { text: "OK", onPress: () => console.log("OK Pressed") }
                        ]
                    )}
                style={({ pressed }) => [
                    {
                        opacity: pressed
                            ? 1
                            : 0.97
                    }
                    ]
                }
            >
                <View style={styles.adressBlock}>
                    <TitleAdress data={dataAdress}/>
                    <View style={styles.errorDelivery}>
                            <MaterialIcons name="error" size={24} color={THEME.MAIN_COLOR} />
                            <Text style={styles.textErrorDelivery}> Не доставляем на этот адрес!</Text>
                    </View>
                </View>
            </Pressable>
        )
    }
};

const styles = StyleSheet.create({
    adressBlock:{
        paddingVertical:0,
        paddingHorizontal:0,
        margin:0
    },
    TitleAdress:{
        fontFamily:"open-bold"
    },
    Fontregular:{
        fontFamily: "open-regular"
    },
    Fornbold:{
        fontFamily:"open-bold"
    },
    errorDelivery:{
        position:"absolute",
        top:0,
        right:0,
        backgroundColor:"rgba(255,255,255,0.96)",
        width:"100%",
        height:"99%",
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center"
    },
    textErrorDelivery:{
        color:THEME.MAIN_COLOR,
        fontSize:12,
        textAlign:'center'
    },
    miniInfoBlokRight:{
        position:"absolute",
        top:0,
        right:0,
        backgroundColor:"rgba(255,255,255,0.5)",
        width:40,
        height:"99%",
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center"
    },
    miniTextBlockDelivery:{
        color:THEME.MAIN_COLOR,
        fontSize:12,
        textAlign:'center'
    },
    ViewInfoAdresPriceAll:{
        paddingVertical:0,
        paddingHorizontal:0,
        margin:0,
        flexDirection:'row',
        justifyContent:'flex-start',
        alignItems:'center',
        width:"100%"
    },
    ViewInfoAdresPrice:{
        paddingVertical:0,
        paddingHorizontal:0,
        margin:0,
        marginRight:10,
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center',
    },
    Titles:{
        fontFamily:'open-regular',
        fontSize:12
    },
    Datas:{
        fontSize:12
    }
});
