import React from 'react'
import {View, StyleSheet, FlatList, Text, ActivityIndicator, ScrollView,Dimensions} from 'react-native'
import { CategoryView } from './CategoryView'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
import Slider from "react-slick";
import './sliderCssCustom.css';
import {getTypeDevice} from "../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const CategoryList = ({ data = [], onClick , handlReloadCatalogStatus }) => {
    //console.log(data,"Этот текст должен грузится после того, как будет загружен каталог");
    if (!data.length) {
        return (
            <View style={styles.wrapper}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
                <Text style={styles.noItems}>Категорий нет</Text>
            </View>
        )
    }

    const allCategory = useSelector(state => state.post.allCategory);

    let SortCategory = allCategory.sort(( a, b ) => a.orders - b.orders);

    let SortCategory2 = allCategory.sort(( a, b ) => a.orders - b.orders);
    //console.log('ЧТо в категориях ',SortCategory);

    const SampleNextArrow = (props)=>{
        const { className, style, onClick } = props;
        return (
            <div
                className={className}
                style={{ ...style, display: "block", zIndex:99, '::after': {
                        color: THEME.MAIN_COLOR
                    } }}
                onClick={onClick}
            />
        );
    }

    const SamplePrevArrow = (props)=>{
        const { className, style, onClick } = props;
        return (
            <div
                className={className}
                style={{ ...style, display: "block",  zIndex:99, '::before': {
                        color: THEME.MAIN_COLOR
                    } }}
                onClick={onClick}
            />
        );
    }

    const settings3 = {
        dots: false,
        infinite: true,
        // arrows:true,
        speed: 1000,
        responsiveTo:"980px",
        swipeToSlide:true,
        variableWidth:true,
        // rows:1,
        // rowCount:1,
        // variableHeght:true,
        // slidesToShow: 7,
        // slidesToScroll: 1,
        display:'flex',
        responsive: [
            {
                breakpoint: 300,
                settings: {
                    arrows: true,
                    centerMode: true,
                    centerPadding: '1%',
                    slidesToShow: 1
                }
            },
            {
                breakpoint: 480,
                settings: {
                    arrows: true,
                    // centerMode: true,
                    centerPadding: '2%',
                    slidesToShow: 4
                }
            },
            {
                breakpoint: 1200,
                settings: {
                    // arrows: true,
                    // centerMode: true,
                    nextArrow: (<SampleNextArrow />),
                    prevArrow: (<SamplePrevArrow />),
                    // centerPadding:0,
                    centerPadding: '0',
                    // centerPadding: '7%',
                    slidesToShow: 5,
                    width:"90%",
                    slidesToScroll:2
                }
            }
        ]
    }

    const typeDeviceVK = useSelector(state => state.post.typeDeviceVK);

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);
    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    let CheckNulls = null;
    let CheckedOffCategorys = [];

    if(TypeDeliverySystem === true){
        CheckNulls = DeliveryAdresSystem;
    }else{
        CheckNulls = AffiliateAdresSystem;
    }


    //console.log("typeDeviceVK", typeDeviceVK);

    return (
        <View style={styles.wrapper} key={"Cats_Views"}>
            {getTypeDevice(Dimensions.get('window').width, Dimensions.get('window').height,typeDeviceVK) ?
                <ScrollView horizontal
                            scrollEnabled
                            showsHorizontalScrollIndicator={false}
                            contentContainerStyle={styles.HeaderMenu}
                            key={"ScrCats"}
                            style={{outline:'none'}}
                    //disableIntervalMomentum={true}
                    //snapToInterval={Dimensions.get('window').width/1.17 }
                >
                    {
                        SortCategory.map((item, number) => (
                            <CategoryView category={item} setActiveCategory={onClick}
                                          handlReloadCatalogStatus={handlReloadCatalogStatus}
                                          key={"Key_Cat_View_" + number}/>
                        ))
                    }
                    <View style={{width:100}}><Text>                </Text></View>
                </ScrollView> :
                <View style={{paddingHorizontal: "3%"}}>
                    {CheckNulls !== null ? <Slider {...settings3}>
                        {/*<View><h3>1</h3></View>*/}
                        {/*<View><h3>2</h3></View>*/}
                        {/*<View><h3>3</h3></View>*/}
                        {/*<View><h3>4</h3></View>*/}
                        {/*<View><h3>5</h3></View>*/}
                        {/*<View><h3>6</h3></View>*/}
                        {
                            SortCategory.map((item, number) => (
                                <View>
                                    <View style={[
                                        {
                                            marginHorizontal:5,
                                            paddingHorizontal: 0,
                                            // boxSizing: "border-box",
                                        }]}>
                                        <CategoryView category={item} setActiveCategory={onClick}
                                                      handlReloadCatalogStatus={handlReloadCatalogStatus}
                                                      key={"Key_Cat_View_" + number}/>
                                    </View>
                                </View>
                            ))}
                        {/*{SortCategory2.map((item, number) => (*/}
                        {/*    <CategoryView category={item} setActiveCategory={onClick} handlReloadCatalogStatus={handlReloadCatalogStatus} key={"Key_Cat_View_"+number}/>*/}
                        {/*    ))*/}
                        {/*}*/}
                    </Slider> : false
                    }
                </View>
            }
        </View>
    )
}

const styles = StyleSheet.create({
    wrapper: {
        padding: 5,
        paddingVertical:10,
        backgroundColor:THEME.CATEGORYMENU_COLOR
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 20
    },
    HeaderMenu: {
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    }
})
