export const CheckCategoryOff = (allCategoryOff,Items,type = true)=>{
    if(allCategoryOff === null){return true;}
    let StatusOff = false;
    // console.log(type,Items,allCategoryOff);
    if(type === true) {
        if("OffCategoryData" in allCategoryOff){
            if("OffCategory" in allCategoryOff.OffCategoryData){
                if(allCategoryOff.OffCategoryData.OffCategory !== null && allCategoryOff.OffCategoryData.OffCategory!= 'null'){}else{return false;}
            }else{return false;}
        }else{return false;}
        Object.keys(allCategoryOff.OffCategoryData.OffCategory).forEach((id, n) => {
            if (id === Items) {
                StatusOff = true;
            }
        });
    }else{
        // console.log('allCategoryOff = = =',allCategoryOff);
        if("JsonNoneCheck" in allCategoryOff){
            if("category_none" in allCategoryOff?.JsonNoneCheck){
                if(allCategoryOff?.JsonNoneCheck?.category_none !== null && allCategoryOff?.JsonNoneCheck?.category_none!= 'null'){}else{return false;}
            }else{
                return false;
            }
        }else{return false;}

        Object.keys(allCategoryOff?.JsonNoneCheck?.category_none).forEach((id, n) => {
            if (id === Items) {
                StatusOff = true;
            }
        });
    }
    return StatusOff;
};