import React, { Component } from 'react';
import { View, Text } from 'react-native';

import { ProgressSteps, ProgressStep } from 'react-native-progress-steps';
import {THEME} from "../../../theme";

class ProgressStepOrder extends Component {
    static navigationOptions = {
        header: null
    };

    defaultScrollViewProps = {
        keyboardShouldPersistTaps: 'handled',
        contentContainerStyle: {
            flex: 1,
            justifyContent: 'center'
        }
    };

    onNextStep = () => {
        //console.log('called next step');
    };

    onPrevStep = () => {
        //console.log('called previous step');
    };

    onSubmitSteps = () => {
        //console.log('called on submit step.');
    };

    getNumbers = ()=>{
        if(this.props.stepActive >4){
            return 4;
        }
        return this.props.stepActive;
    };

    render() {
        const progressStepsStyle = {
            activeStepIconBorderColor: THEME.MAIN_COLOR,
            activeLabelColor: THEME.MAIN_COLOR,
            activeStepNumColor: THEME.MAIN_COLOR,
            activeStepIconColor: THEME.MAIN_COLOR,
            completedStepIconColor: THEME.MAIN_COLOR,
            completedProgressBarColor: THEME.MAIN_COLOR,
            completedCheckColor: THEME.MAIN_COLOR,
            activeStep:this.getNumbers(),
            topOffset:0,
            marginBottom:0,
            marginTop:0,
            disabledStepNumColor:"#dddddd",
            labelColor:"#dddddd",
            progressBarColor:"#dddddd",
            disabledStepIconColor:"#dddddd",
            labelFontSize:9
        };

        const buttonTextStyle = {
            color: THEME.MAIN_COLOR,
            fontWeight: 'bold'
        };

        return (
            <View style={{ flex: 1, marginTop: 5 }}>
                <ProgressSteps {...progressStepsStyle}>
                    <ProgressStep
                        //label="First"
                        onNext={this.onNextStep}
                        onPrevious={this.onPrevStep}
                        scrollViewProps={this.defaultScrollViewProps}
                        nextBtnTextStyle={buttonTextStyle}
                        previousBtnTextStyle={buttonTextStyle}
                        removeBtnRow={true}
                        previousBtnDisabled={true}
                        nextBtnDisabled={true}
                    >
                    </ProgressStep>
                    <ProgressStep
                        //label="Second"
                        onNext={this.onNextStep}
                        onPrevious={this.onPrevStep}
                        scrollViewProps={this.defaultScrollViewProps}
                        nextBtnTextStyle={buttonTextStyle}
                        previousBtnTextStyle={buttonTextStyle}
                        removeBtnRow={true}
                        previousBtnDisabled={true}
                        nextBtnDisabled={true}
                    >
                    </ProgressStep>
                    <ProgressStep
                       // label="Third"
                        onNext={this.onNextStep}
                        onPrevious={this.onPrevStep}
                        scrollViewProps={this.defaultScrollViewProps}
                        nextBtnTextStyle={buttonTextStyle}
                        previousBtnTextStyle={buttonTextStyle}
                        removeBtnRow={true}
                        previousBtnDisabled={true}
                        nextBtnDisabled={true}
                    >
                        {/*<View style={{ alignItems: 'center' }}>*/}
                        {/*    <Text>This is the content within step 3!</Text>*/}
                        {/*</View>*/}
                    </ProgressStep>
                    <ProgressStep
                        //label="Fourth"
                        onNext={this.onNextStep}
                        onPrevious={this.onPrevStep}
                        scrollViewProps={this.defaultScrollViewProps}
                        nextBtnTextStyle={buttonTextStyle}
                        previousBtnTextStyle={buttonTextStyle}
                        removeBtnRow={true}
                        previousBtnDisabled={true}
                        nextBtnDisabled={true}
                    >
                        {/*<View style={{ alignItems: 'center' }}>*/}
                        {/*    <Text>This is the content within step 4!</Text>*/}
                        {/*</View>*/}
                    </ProgressStep>
                    <ProgressStep
                        //label="Fifth"
                        onPrevious={this.onPrevStep}
                        onSubmit={this.onSubmitSteps}
                        scrollViewProps={this.defaultScrollViewProps}
                        nextBtnTextStyle={buttonTextStyle}
                        previousBtnTextStyle={buttonTextStyle}
                        removeBtnRow={true}
                        previousBtnDisabled={true}
                        nextBtnDisabled={true}
                    >
                        {/*<View style={{ alignItems: 'center' }}>*/}
                        {/*    <Text>This is the content within step 5!</Text>*/}
                        {/*</View>*/}
                    </ProgressStep>
                </ProgressSteps>
            </View>
        );
    }
}

export default ProgressStepOrder;