import React from 'react';
import {Text} from "react-native";
import { Feather } from '@expo/vector-icons';
import {THEME} from "../../../theme";
import {useSelector} from "react-redux";


export const ErrorPromokodeIconInfo = ()=>{

    const Shows = useSelector(state => state.post.promokodeErrorInfo);

  //  console.log(Shows);

    if(Shows.showIconError){
        return(
            <Text style={{textAlign:"right",position:"absolute",right:"3%"}}>
                <Feather name="info" size={24} color={THEME.DANGER_COLOR} />
            </Text>
        )
    }else{
        return(
            <Text>
            </Text>
        )
    }
}