export const loadCatalogProduct = (CityData) => {
  return async dispatch => {
    const arrayCategory = await ServerDB.getCatalogProducts(CityData.cityId);
    const arrayDopProduct = await ServerDB.getCatalogDopProducts(CityData.cityId);

    // console.log('Загружаем слайды')
    const newArrayCategory = Object.keys(arrayCategory.list).map((key,data)=>{
      return {
        id:key,
        img:URL_SITE+"/images/API_IMG/"+arrayCategory.list[key].imgCategory,
        title:arrayCategory.list[key].Title,
        orders:arrayCategory.list[key].position,
        FilterCategory:arrayCategory.list[key]?.filterCategory
      };
     });

     //console.log(newArrayCategory,"Категории загружены");
    dispatch({
      type: LOAD_CATALOG,
      payload: {
        Category:newArrayCategory,
        Catalog:arrayCategory.list,
        CatalogDopProduct:arrayDopProduct,
        CatalogRecommend:arrayCategory.listRecommend,
        PreorderCategoryDopsLimit:arrayCategory.listPreorderCategoryDopProduct
      }
    });

    // setTimeout(()=>{
    //   dispatch(loadCatalogProduct(CityData))
    // });
  }
};


