export const loadPushNoticeList = (idUser,pushKey,reloads = false)=>{
  // console.log('Запрашиваем обновление списка push уведомлений с сервера');
  return async dispatch => {
    const ServerPushList = await ServerDB.getAllNoticeUser(idUser,pushKey);
    // console.log('Пришел ответ по списку push уведомлений ', ServerPushList);
    try {
      switch (ServerPushList.error) {
        case false:
          dispatch({
            type: UPDATE_PUSHNOTICE_LIST,
            payload: ServerPushList.result
          });
          break;
        case true:
            //console.log('Ошибка при получении pushList с сервера');
          break;
        case "Push not found":
          // console.log('Push уведомления для пользователя не найдены');
          break;
        default: console.log("Ошибка обработки error pushList. = ",ServerPushList.error, idUser,pushKey);
      }


        // if(reloads === true){
        //   setTimeout(() => {
        //     // dispatch(loadPushNoticeList(idUser,pushKey,reloads));
        //     // console.log('Запускаем запрос на обновление ush листа спустя 5 секунд');
        //   }, 5000);
        // }


    }catch (e) {
        //console.log('Ошибка получения pushList',e);
    }
  };
};


export const loadGiftsList = (idCity)=>{
  // console.log('Запрашиваем обновление списка push уведомлений с сервера');
  return async dispatch => {
    const ServerPushList = await ServerDB.getAllGiftCity(idCity);
    // console.log('Пришел ответ по подаркам с сервера ', ServerPushList);
    try {
      switch (ServerPushList.ready) {
        case true:
          dispatch({
            type: UPDATE_GIFTS_LIST,
            payload: ServerPushList
          });
          break;
        case false:
          console.log('Ошибка при получении gifts с сервера');
          dispatch({
            type: UPDATE_GIFTS_LIST,
            payload: ServerPushList
          });
          break;
        default: console.log("Ошибка обработки error gifts. ",ServerPushList.error);
      }

    }catch (e) {
      console.log('Ошибка получения gifts',e);
    }
  };
};


