import React, { useState } from "react";
import {View,  StyleSheet,  Pressable,  Text,  Modal,  Dimensions} from "react-native";
import {THEME} from "../../theme";

export const Select = ({
                           defaultValue = 0, //Значение по умолчанию valueId
                           item = [], //список option компонентов (массив value, title)
                           onChange = ()=>{},
                           fontSize = 16,
                           keys = null
                }) => {
    const [selectedValue, setSelectedValue] = useState(defaultValue);
    const [selectedValueKey, setSelectedValueKey] = useState(defaultValue);
    const [modalVisibles, setModalVisibleS] = useState(false);

    // console.log(defaultValue);
    // console.log(selectedValueKey);


    const GetModalOption = ({item, selecteds})=>{
        // console.log(selecteds);
        const ListOption = item.map((data,n)=>{
            return (<Pressable
                onPress={()=>{
                    setSelectedValue(data.value);
                    setSelectedValueKey(data.value);
                    onChange(data.value);
                    setModalVisibleS(false);
                }}
                key={"Pressable_Select_"+keys+"_"+n}
                >
                    {selecteds === n ?
                        <View style={styles.optionSelectActive}><Text style={styles.optionSelectActiveText}>{data.title}</Text></View>
                        :
                        <View style={styles.optionSelect}><Text style={styles.optionSelectText}>{data.title}</Text></View>
                    }
                </Pressable>
                )
        });

        return (
            <Modal
                animationType="none"
                transparent={true}
                visible={modalVisibles}
                onShow={()=> {
                    // Alert.alert("Модальное окно было открыто");
                }}
                onRequestClose={() => {
                    // Alert.alert("Модальное окно было закрыто");
                    setModalVisibleS(!modalVisibles);
                }}
                key={"Modal_"+keys}
            >
                <View style={styles.modalBlock} key={"SelectBlock_"+keys}>
                    <View style={styles.modalContent}>
                        {ListOption}
                    </View>
                </View>
            </Modal>
        )
    };

    const GetSelectedValue = ()=>{

        let selecteds = selectedValueKey-1;
        selecteds <0 ? selecteds = 0 : false;

        return (
            <Pressable
            onPress={()=>{
                setModalVisibleS(true);
            }}
            style={{
                width:"100%"
            }}
            >
                <GetModalOption item={item} selecteds={selecteds}/>
                <Text style={[styles.defaultValue,{fontSize:fontSize}]}>{item[selecteds].title}</Text>
            </Pressable>
        )
    };


    return (
        <View style={styles.container}>
            <GetSelectedValue />
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"rgba(0,0,0,0.4)",
        height: Dimensions.get('window').height,
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    modalContent:{
        width:"88%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"5%",
        borderRadius:25
    },
    optionSelect:{
        marginVertical: "2%",
        fontSize:16,
        borderBottomWidth:0,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        backgroundColor:"#dddddd",
        paddingHorizontal:"5%",
        borderRadius: 10
    },
    optionSelectActive:{
        marginVertical: "2%",
        fontSize:16,
        borderBottomWidth:0,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        backgroundColor:THEME.MAIN_COLOR,
        color:THEME.TEXT_COLOR,
        paddingHorizontal:"6%",
        borderRadius: 10
    },
    optionSelectText:{
       color:THEME.TEXT_DESCRIPTION_PRODUCT,
        textAlign: "center",fontSize:15
    },
    optionSelectActiveText:{
        color:THEME.TEXT_COLOR,
        textAlign: "center",
        fontSize:15
    }
});