    static getSlideHeader(CityData){
      //  console.log('Запрашиваем данные слайдов у сервера ->');
        return new Promise((resolve, reject) => {

            let RequestParams = URL_API+'getSlideHeader&apiKey='+API_KEY+'&CityIdSystem='+CityData;
           // console.log(RequestParams);
            axios(RequestParams,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    data: {
                        typeSlide: 'mobile',
                    },
                    responseType: 'text'
                })
                .then(({data}) => {
                    if ("error" in data) {
                        console.log(data.error, '|', data.errorInfo);
                        resolve({});
                    } else {
                        //console.log(data.listSlide);
                        resolve(data.listSlide);
                    }
                })
                .catch((err) => {
                    console.log("ERROR: --- | Получение слайдов | ---  ", err);
                    reject("ERROR: ---Получение слайдов---  ", err);
                });
        });
    }


