    static setOrderServer (CityData,idUser,array){
        return new Promise((resolve, reject) => {
            axios({
                method:'post',
                url:URL_API+'setOrderMobileApp&apiKey='+API_KEY+'&CityIdSystem='+CityData,
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data:{
                    idUser:idUser,
                    dataOrder:array
                },
            }).then(({ data }) => {
                if('error' in data){
                    console.log("Ошибка ",data);
                    resolve({error:true,errorInfo:data.errorInfo});
                }else{
                    // console.log("Пришли данные ",data.result);
                    resolve({
                        error:null,
                        result:data.result,
                        orderId:data.orderId,
                        sumPayment:data.sumPayment,
                        idPointsPaymentToken:data.idPointsPaymentToken,
                        onlineModal:data.onlineModal
                    });
                }

            })
                .catch((err) => {
                    console.log("ERROR: ------  ",err);
                    reject({error:true,errorInfo:"Внутренняя ошибка приложения"});
                });
        });
    }

