    static getStreetList(S,CityData){
        if(S.length<3){
           // console.log('Менее 3х букв ',S);
            return false;
        }
       // console.log(S,CityData,'Передали');

        return new Promise((resolve, reject) => {
            axios({
                method:'GET',
                url:URL_API+'getStreetList&apiKey='+API_KEY+'&CityIdSystem='+CityData,
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                params:{
                    nameStreet:S,
                    city_users_id:CityData
                },
                responseType: 'text'
            }).then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                    if("result" in data){
                        //console.log('Получили список вариантов',data.result);
                        resolve(data);
                        //this.setStreetList(data.result);
                    }else{
                        console.log('Ошибка ',data);
                    }
                }
                resolve(data);

            })
                .catch((err) => {
                    console.log("ERROR: ---Получение списка улиц---  ",err);
                    reject("ERROR: ---Получение списка улиц---  ",err);
                });
        });
    }


    static saveNewAdresServer = (Info,CityData,idUser)=>{
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url: URL_API + 'setAddressUserMobile&apiKey=' + API_KEY + '&CityIdSystem=' + CityData,
                headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                data: {
                    addressInfo: Info,
                    idUser: idUser
                },
                responseType: 'text'
            }).then(({data}) => {
                if ("error" in data) {
                    console.log(data.error, '|', data.errorInfo);
                    resolve(false);
                } else {
                    if (data.result.result_add === true) {
                        //console.log('Обновляем адреса', data);
                        resolve(true);
                    } else {
                        console.log('Ошибка ', data);
                        resolve(false);
                    }
                }


            }).catch((err) => {
                console.log("ERROR: ---Запрос Новый Адрес---  ", err);
                reject("ERROR: ---Запрос Новый Адрес---  ", err);
            });
        });
    };



