export const AdressUserListScreen = ({ navigation, route }) => {

    const CityLoadPhone =  useSelector(state => state.post.cityLoadPhone);
    const AuthUserStatus =  useSelector(state => state.post.AuthUserStatus);

    if (CityLoadPhone || (!AuthUserStatus)) {
        //console.log('Переводим на страницу');
        // navigation.navigate('Authorization');
        return (
            <View style={styles.center}>
                <Text style={{fontSize:14,textAlign:"center",marginBottom:20}}>Адреса доставки сохраняются в Вашем аккаунте, для того, чтобы добавить или выбрать уже добавленный адрес, Вам необходимо войти в аккаунт!</Text>
                <Pressable
                    style={styles.buttonCity}
                    color={THEME.MAIN_COLOR}
                    onPress={() => {
                        navigation.navigate('Authorization');
                    }}
                >
                    <Text style={{color:THEME.MAIN_COLOR,fontFamily:"open-bold",marginVertical:20}}>Авторизоваться</Text>
                </Pressable>
            </View>
        )
    } else {
        return (
            <GoodPages navigation={navigation} route={route}/>
        )
    }

}

export const GoodPages = ({ navigation, route }) => {

    const dispatch = useDispatch();

    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    const [loaded,setLoaded] = useState(true);
    const [loadedFC,setLoadedFC] = useState(true);

    const loadAdressUser1 = useSelector(state => state.post.loadAdressUser);

    const ReloadListAdres = async ()=>{

        const Results = await dispatch(loadAdressUser(idUserSystem,[],{cityId:CityIds}));

        setLoaded(false);
        //setLoadedFC(false);
        //console.log('Обновили адреса');
    };


    useEffect(() => {
        // Put Your Code Here Which You Want To Refresh or Reload on Coming Back to This Screen.
        if('reloads' in route.params){
            ReloadListAdres()
        }else{}
        //console.log('Обновляемся ',navigation.state.params);
    }, [route.params]);

    if(loadedFC) {
        useEffect(()=> {
                ReloadListAdres()
            }
        ,[dispatch]);
    }else{

    }

    // if(route.params.reload === true){
    //     ReloadListAdres();
    // }else{
    //
    // }

    const Loadings = ()=>{
        if(loaded){
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <Text>Загрузка {"\n"}</Text>
                    <ActivityIndicator color={THEME.MAIN_COLOR} />
                </SafeAreaView>
            )
        }else{
            return(
                <AdressUserListAwait navigation={navigation} route={route}/>
            )
        }
    };

    return(
        <Loadings />
    )

};


export const AdressUserListAwait = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const postId = route.params.postId;

    const product = route.params.productData;
    const AdresList = useSelector(state => state.post.adressUser.listAddress);
    const AdresListNoDelivery = useSelector(state => state.post.adressUser.ListAddressNoDelivery);


    //console.log(AdresList);

    if(AdresList === null || typeof AdresList !== 'object'){
        useEffect(() => {
            dispatch(loadAuthInfo());
        }, [dispatch]);
        return (
            <SafeAreaView style={styles.centeredViewWindowTop}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
            </SafeAreaView>
        )
    }


    const ListAdresForRender = Object.keys(AdresList).map(
        (id,n)=>{
            return AdresList[id]
        }
    );

    const ListAdresNoDeliveryForRender = Object.keys(AdresListNoDelivery).map(
        (id,n)=>{
            return AdresListNoDelivery[id]
        }
    );



    // const toggleHandler = useCallback(() => {
    //     dispatch(toogleBooked(product))
    // }, [dispatch, product]);


    // useEffect(() => {
    //     navigation.setParams({ toggleHandler })
    // }, [toggleHandler]);

    const removeHandler = () => {
        Alert.alert(
            'Удаление поста',
            'Вы точно хотите удалить пост?',
            [
                {
                    text: 'Отменить',
                    style: 'cancel'
                },
                {
                    text: 'Удалить',
                    style: 'destructive',
                    onPress() {
                        navigation.navigate('Main')
                        dispatch(removePost(postId))
                    }
                }
            ],
            { cancelable: false }
        )
    };




    const RenderAdresList = ({dataAdres,onPress,statusDelivery = false})=>{
       // console.log(dataAdres);
        return (
            <OneAdress dataAdress={dataAdres} onPress={onPress} statusDelivery={statusDelivery}/>
        )
    };

    if (!product) {
        return null
    }



    const DataScreen = ()=>(
        <View key={"Info_Adress_"+product.id}>

        </View>
    );

    const loadAdressUser = useSelector(state => state.post.loadAdressUser);

    if(!loadAdressUser){
        return(
            <View style={styles.center}>
                <Text>Загрузка...</Text>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
            </View>
        );
    }

    const handlPressOneAdres = (idAdres)=>{
        //console.log('Нажали на адрес - ',idAdres);
        // CheckBasketOffCategoryReload(dispatch,basket,idAdres,true);
        dispatch(setSelectAdres(idAdres));
        // DB.setAdresData( idAdres.id, idAdres);
        navigation.navigate('Main');
    };

    const ButtonNewAdres = ()=>{
        return(
            <TouchableOpacity
                onPress={()=>{
                   // console.log('Нажали добавить адрес');
                    onPressNewAdresHandler();
                }}
            >
                <View style={styles.newAdresButton}><Text style={styles.newAdresText}>Добавить новый адрес</Text><Feather name="plus" size={24} color={THEME.MAIN_COLOR} /></View>
            </TouchableOpacity>
        )
    };

    const onPressNewAdresHandler = product => {
        //console.log('Открываем товар');
        navigation.navigate('NewAdres', {
            title: 'Добавление адреса',
            productData:[]
        });
    };

    return (
        <ScrollView style={styles.BodyView}>
            <FlatList
                data={ListAdresForRender}
                keyExtractor={item => "Blocks_Adress_"+item.id}
                renderItem={
                    ({item})=>(
                        <RenderAdresList dataAdres={item} onPress={handlPressOneAdres} statusDelivery={true}/>
                    )
                }>
            </FlatList>
            <FlatList
                data={ListAdresNoDeliveryForRender}
                keyExtractor={item => "Blocks_Adress_"+item.id}
                renderItem={
                    ({item})=>(
                        <RenderAdresList dataAdres={item} statusDelivery={false}/>
                    )
                }>
            </FlatList>
            <ButtonNewAdres />
        </ScrollView>
    )
};

AdressUserListScreen.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;
    const booked = route.params.booked;
    const toggleHandler = route.params.toggleHandler;
    const iconName = booked ? 'ios-star' : 'ios-star-outline'
    return {
        headerTitle: 'Ваши адреса',
    }
};

