import React, { useState } from "react";
import {
    View,
    StyleSheet,
    Pressable,
    Text,
    Modal,
    Dimensions,
    ActivityIndicator,
    ScrollView,
    Platform
} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {setListDataDelievryList, setModalStatus, setPreorderData} from "../../../store/actions/post";
import {THEME} from "../../../theme";
import {ServerDB} from "../../../serverDb";
import {Ionicons} from "@expo/vector-icons";

export const ContentModalTimeDelivery = ({
                                       defaultValue = 0, //Значение по умолчанию valueId
                                       item = [], //список option компонентов (массив value, title)
                                       onChange = ()=>{},
                                       fontSize = 16,
                                       keys = null
                                   }) => {

    const dispatch = useDispatch();
    const listDataDelivery =  useSelector(state=>state.post.listDataDelivery);

    const statusShowDateList = useSelector(state=>state.post.statusLoadServerPreorderData);

    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);



    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const handleSetDatePreorder = (time)=>{
        let dataReport = {
            dateDelivery: DateValueShow,
            timeDelivery: time
        };
        dispatch(setPreorderData(dataReport));
    };



    const GetModalOption = ()=>{

        let ListOption;

        if(!statusShowDateList){
            ListOption = (
                <Pressable
                    onPress={()=>{
                        //setModalVisibleS(false);
                        handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                        //setStatusShowDateList(false);
                    }}
                    key={"Pressable_Select_"+keys+"_loading"}
                >
                    <ActivityIndicator color={THEME.MAIN_COLOR}/>
                </Pressable>
            );
        }else{
            let ArrayList = [];
            let ArrayTime = [];

            if(DateValueShow in listDataDelivery){
                ArrayTime = listDataDelivery[DateValueShow].timeList;
            }

            Object.keys(ArrayTime).forEach((dates,n)=>{
                if(ArrayTime[dates]['false'] === false) {
                    ArrayList.push({title: ArrayTime[dates].title, value: dates});
                }
            });

            if(ArrayList.length === 0){
                ListOption = false;
            }else{
                ListOption = ArrayList.map((data,n)=>{
                    return (<Pressable
                            onPress={()=>{
                                onChange(n);
                                //setModalVisibleS(false);
                                handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                                handleSetDatePreorder(data.value);
                            }}
                            style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}
                            key={"Pressable_Select_"+keys+"_"+n}
                        >
                            <Text style={styles.optionSelect}>{data.title}</Text>{n !== 0 ? false : false}
                        </Pressable>
                    )
                });
            }


        }

        return (
            <View style={styles.modalBlock} key={"SelectBlock_"+keys}>
                    <View style={styles.modalContent}>
                        <View style={styles.backArrowBlock}>
                            <Pressable
                                onPress={()=>{
                                    handleSetStatusModal("MODAL_TIME_DELIVERY",false);
                                }}
                                style={styles.iconBack}>
                                <Ionicons name="arrow-back" size={28} color="black" />
                            </Pressable>
                            <Text style={styles.textTitleModal}>Время доставки</Text>
                        </View>
                        <ScrollView style={{paddingHorizontal:"10%",paddingTop:"5%"}}>
                            <View style={{flexDirection:"row",justifyContent:"center",alignItems:"center",flexWrap:"wrap"}}>
                                {ListOption !== false ? ListOption : <Text>К сожалению на данную дату предзаказа нет</Text>}
                            </View>
                        </ScrollView>
                    </View>
                </View>
        )
    };


    return (
        <GetModalOption item={item}/>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:Platform.OS === "android" ? 40 : 60,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"5%",
        padding: 5
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
        paddingTop:"3%"
    },
    modalContent:{
        width:"100%",
        height:"100%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:73,
        marginVertical: "5%",
        marginHorizontal:"3%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        paddingHorizontal:"4%",
        borderRadius: 10,
        textAlign:"center"
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});