import {Button, StyleSheet, Text, Pressable} from "react-native";
import React from "react";
import {BUTTONTHEME} from "../../theme";

export const ButtonEbee = ({children,
                               onPress = console.log(''),
                               size = 'xl', //l - мини, xl - средняя , xxl - большая,
                               active = true, //true - активная, false - не активная
                               borderRadius = 0,
                               marginBottom = 0,
                               shadow = false,
                               style = {}
})=>{

    const getSizeButton = (size)=>{
        switch (size) {
            case 'l' : return styles.lButton;
            case 'xl' : return styles.xlButton;
            case 'xxl' : return styles.xxlButton;
            default: return styles.xlButton;
        }
    };

    //console.log(active);

    const getTypeButton = (active)=>{
        switch (active) {
            case true : return styles.allButtonStyle;
            case false : return styles.noActiveButton;
            default: return styles.allButtonStyle;
        }
    };

    let Shadows = styles.shadow;

    if(shadow === false){
        Shadows = [styles.shadowOff,{marginBottom:marginBottom}];
    }else{
        Shadows = [styles.shadow,{marginBottom:marginBottom}];
    }

    return(
        <Pressable
            onPress={onPress}
            style={[styles.overflowHidden]}
        >
            <Text style={[Shadows,styles.allButtonStyle,{borderRadius:borderRadius},getSizeButton(size),getTypeButton(active),style]}>
                {children}
            </Text>
        </Pressable>
    )
};

const styles = StyleSheet.create({
    allButtonStyle:{
        backgroundColor:BUTTONTHEME.BACKGROUND_COLOR,
        color:BUTTONTHEME.COLOR_TEXT,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        textAlign:"center",
        marginVertical: 0,
        overflow: "hidden",
        width:"99%"
    },
    noActiveButton:{
        backgroundColor:BUTTONTHEME.BACKGROUND_COLOR_NO_ACTIVE,
        color:BUTTONTHEME.COLOR_TEXT_NO_ACTIVE,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        textAlign:"center",
        marginVertical: 0,
        overflow: "hidden",
        width:"99%"
    },
    overflowHidden:{
        overflow:"hidden",
        paddingVertical:5,
        justifyContent:"center",
        alignItems:"center"
    },
    lButton:{
        fontSize:12,
        paddingHorizontal:20,
        paddingVertical:12
    },
    xlButton:{
        fontSize:16,
        paddingHorizontal:20,
        paddingVertical:12
    },
    xxlButton:{
        fontSize:20,
        paddingHorizontal:20,
        paddingVertical:12
    },

    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 3,
        },
        shadowOpacity: 0.20,
        shadowRadius: 0,
        elevation: 3,
        marginBottom:10
    },
    shadowOff:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 0,
        marginBottom:10
    }
});