import React, { useState, useRef } from 'react'
import {
    View,
    Text,
    StyleSheet,
    TextInput,
    Button,
    ScrollView,
    TouchableWithoutFeedback,
    Keyboard,
    Dimensions
} from 'react-native'
import { useDispatch } from 'react-redux'
import { HeaderButtons, Item } from 'react-navigation-header-buttons'
import { AppHeaderIcon } from '../../components/AppHeaderIcon'
import { THEME } from '../../theme'
import { addPost } from '../../store/actions/post'
import { PhotoPicker } from '../../components/PhotoPicker'

export const SelectDataDeliveryScreen = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const [text, setText] = useState('')
    const imgRef = useRef()

    const saveHandler = () => {
        const post = {
            date: new Date().toJSON(),
            text: text,
            img: imgRef.current,
            booked: false
        }
        dispatch(addPost(post))
        navigation.navigate('Main')
    }

    const photoPickHandler = uri => {
        imgRef.current = uri
    }

    return (
        <ScrollView>
            <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
                <View style={styles.wrapper}>
                    <Text style={styles.title}>Создай новый пост</Text>
                    <TextInput
                        style={styles.textarea}
                        placeholder='Введите текст заметки'
                        value={text}
                        onChangeText={setText}
                        multiline
                    />
                    <PhotoPicker onPick={photoPickHandler} />
                    <Button
                        title='Создать пост'
                        color={THEME.MAIN_COLOR}
                        onPress={saveHandler}
                        disabled={!text}
                    />
                </View>
            </TouchableWithoutFeedback>
        </ScrollView>
    )
}

SelectDataDeliveryScreen.navigationOptions = ({ navigation, route }) => {
    const title = "Доставить"
    const booked = route.params.booked;
    const toggleHandler = route.params.toggleHandler;
    const iconName = booked ? 'ios-star' : 'ios-star-outline'
    return {
        headerTitle: title,
    }
}

const styles = StyleSheet.create({
    wrapper: {
        padding: 10
    },
    title: {
        fontSize: 20,
        textAlign: 'center',
        fontFamily: 'open-regular',
        marginVertical: 10
    },
    textarea: {
        padding: 10,
        marginBottom: 10
    },
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:30,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"3.1%"
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    modalContent:{
        width:"100%",
        height:"100%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:"100%",
        marginVertical: "5%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "5%",
        paddingHorizontal:"5%",
        borderRadius: 10
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});
