import React,{useRef} from 'react'
import {
    View,
    StyleSheet,
    FlatList,
    Text,
    ScrollView,
    SafeAreaView,
    Platform,
    Dimensions,
    ActivityIndicator
} from 'react-native'
import { OneProduct } from './Product/ProductCatd/OneProduct'
import {THEME} from "../../theme";
import {useSelector} from "react-redux";
import {CheckCategoryOff} from "../CheckCategoryOff";

import { AntDesign } from '@expo/vector-icons';
import {getTypeDevice} from "../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const CatalogActiveCategory = ({ data = [], onOpen, onOpenProduct = ()=>{} , flatListRef, scrollChildren = false, handlScrollEnabled, style={}}) => {
    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);

    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    let CheckNulls = null;
    let CheckedOffCategorys = [];


    if (!data.length) {
        //console.log(data);
        return (
            <SafeAreaView style={styles.wrapper}>
                <View style={styles.wrapper}>
                    <Text style={styles.noItems}>Товары отсутсвуют</Text>
                </View>
            </SafeAreaView>
        )
    };

    if(TypeDeliverySystem === true){
        CheckNulls = DeliveryAdresSystem;
        CheckedOffCategorys = DeliveryAdresSystem;
    }else{
        CheckNulls = AffiliateAdresSystem;
        CheckedOffCategorys = AffiliateAdresSystem;
    }


    const reloadedCatalog = useSelector(state => state.post.reloadedCatalog);
    const CategoryOffStatus = CheckCategoryOff(CheckedOffCategorys,data.id,TypeDeliverySystem);


    const renderItem = ({ item, index }) => {
        return (
            <Item
                item={item}
                onPress={() => handleItemPress(index)}
            />
        );
    };

    if (reloadedCatalog || CheckNulls === null) {
        return (
            <SafeAreaView style={styles.center}>
                {/*<ActivityIndicator color={THEME.MAIN_COLOR} />*/}

                {CheckNulls === null ? <>
                        <AntDesign name="exclamationcircleo" size={72} color={THEME.MAIN_COLOR} />
                            <Text style={{textAlign:"center"}}>
                                {"\n"}Выберите адрес {"\n"}для доставки или самовывоза
                            </Text>
                         </>
                :
                    <ActivityIndicator color={THEME.MAIN_COLOR} />}
            </SafeAreaView>
        )
    }


    if(CategoryOffStatus === true){
        return (
            <SafeAreaView style={styles.wrapper}>
                <View style={styles.wrapper}>
                    <Text style={styles.noItems}>Товары отсутсвуют</Text>
                </View>
            </SafeAreaView>
        )
    }

    if(!data){data = []}

    return (
        <SafeAreaView style={[styles.wrapper,style]}>
            <FlatList
                scrollEnabled={scrollChildren}
                showsVerticalScrollIndicator={false}
                ref={flatListRef}
                numColumns={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ?  2 : 1}
                data={data}
                nestedScrollEnabled={scrollChildren}
                keyExtractor={product => product.id.toString()+"_"+product.type_product.toString()}
                renderItem={({ item }) => (<OneProduct product={item} onOpen={onOpen} onOpenProduct={onOpenProduct}/>)}
                columnWrapperStyle={ getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {paddingHorizontal:'1%',height:'100%'} : false}
                style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {marginBottom:'50px'} : {paddingHorizontal:'2%',marginBottom:'50px'}}
                // listKey={item => "ActivePrOne_"+item.id.toString()+"_"+item.type_product.toString()}
                // scrollToOffset={{animated:true,offset:30}}
                //refreshing={true}

                // onTouchStart={() => {
                //     handlScrollEnabled( true );
                // }}
                // onMomentumScrollEnd={() => {
                //     handlScrollEnabled( false );
                // }}

            />
            {/*<FCatalogList allProducts={data}/>*/}
        </SafeAreaView>
    )


};

const styles = StyleSheet.create({
    wrapper: {
        padding: 5,
        paddingHorizontal:"1.8%",
         // height:Platform.OS === 'android' ? Dimensions.get('window').height : Dimensions.get('window').height,
         // height:Platform.OS === 'android' ? Dimensions.get('window').height : "100%"
         //  height:Platform.OS === 'android' ? "100%" : "100%"
    },
    center:{
        width:"100%",
        height:Dimensions.get('window').height*0.5,
        alignItems:"center",
        justifyContent:"center",
        // backgroundColor:"#ff7300"
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 18
    }
})
