import React, {useCallback} from 'react'
import {
    View,
    StyleSheet,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
import {addProductBasket} from "../../store/actions/post";
// import {GetAllPriceBasket} from "../Basket/GetAllPriceBasket";

export const ButtonInCartBasket = ({ Product, titleButton = 'В корзину', SizeButton = 'normal',typeProductModify = false,modifyList = [], typeButton = false, numberProductInCart = null, numberTypeProductInCart = null, countProductInCart = 1}) => {
    const dispatch = useDispatch();
    const basketProduct = useSelector(state => state.post.basket);

    if(typeButton === true) {
        // console.log('typeProductModify = ', typeProductModify, numberProductInCart, numberTypeProductInCart, basketProduct);
    }

    //numberProductInCart - ключ товара в массиве корзина
    //numberTypeProductInCart - ключ товара в конкретном ключе корзина [0][1] - то есть первый товар в корзине, но 2 в списке одинаковых товаров (с модификаторами например)


    let inCart = true;
    let countInCart = 0;


    let PrArray =[];

    const checkCartProduct = (idProduct,TypeProduct,numberProductInCart = 0,numberTypeProductInCart = 0)=> {
        // console.log("Добавление товара");

        if (basketProduct.length) {
            //Если товары есть в корзине

            let NewBasketProductArray = [];

            basketProduct.forEach((product, i) => {

                //Проходимся по товарам и ищем нужный нам
                if(i !== numberProductInCart){
                    //Если товар нам не подходит, то есть не его изменяем, то оставляем его как он есть
                    NewBasketProductArray.push(product);
                }else{
                    //Если товар нам подходит, то проверяем какой конкретно его тип или номер нам нужно изменить
                    let NewProductNumber = [];
                    product.type.forEach((typeId, numb) => {
                        if(numb !== numberTypeProductInCart){
                            //Если товар нам не подходит, оставляем его как есть и заносим в массив типов товара
                            NewProductNumber.push(typeId);
                        }else{
                            //Если товар нам подошел, то есть это тот, что нам нужен по номеру типа
                            let UpdateProductData = {...typeId,...{count:(typeId.count+1)}};
                            NewProductNumber.push(UpdateProductData);
                        }
                    });

                    let NewProductType = {...product,...{type:NewProductNumber}};
                    NewBasketProductArray.push(NewProductType);
                }
            });

            PrArray = [...NewBasketProductArray];

        } else {
            //Корзина пуста, добавляем первый товар
            // PrArray = [setOneProduct(idProduct, TypeProduct, idCategory)];
        }

        /////
    };

    const checkCartProductRemove = (idProduct,TypeProduct,numberProductInCart = 0,numberTypeProductInCart = 0)=>{
        // console.log("Добавление товара");

        if (basketProduct.length) {
            //Если товары есть в корзине

            let NewBasketProductArray = [];

            basketProduct.forEach((product, i) => {

                //Проходимся по товарам и ищем нужный нам
                if(i !== numberProductInCart){
                    //Если товар нам не подходит, то есть не его изменяем, то оставляем его как он есть
                    NewBasketProductArray.push(product);
                }else{
                    //Если товар нам подходит, то проверяем какой конкретно его тип или номер нам нужно изменить
                    let NewProductNumber = [];
                    product.type.forEach((typeId, numb) => {
                        if(numb !== numberTypeProductInCart){
                            //Если товар нам не подходит, оставляем его как есть и заносим в массив типов товара
                            NewProductNumber.push(typeId);
                        }else{
                            //Если товар нам подошел, то есть это тот, что нам нужен по номеру типа

                            //Проверяем сколько товаров лежит в корзине, если больше 1, то отнимаем 1, если 1 или меньше, то не записываем его в массив, то есть удаляем полностью
                            if(typeId.count > 1){
                                //Товаров больше 1, значи можно отнять 1
                                let UpdateProductData = {...typeId,...{count:(typeId.count-1)}};
                                NewProductNumber.push(UpdateProductData);
                            }else{
                                //Товаров 1 или меньше, значит мы его не записыаем
                            }

                        }
                    });

                    //Проверяем скольк товаров у нас лежит в типе товара
                    if(NewProductNumber.length>0){
                        //Если товаров больше 0, то есть они есть, значит записываем
                        let NewProductType = {...product,...{type:NewProductNumber}};

                        NewBasketProductArray.push(NewProductType);
                    }else{
                        //Если товаров 0 - то есть их нет, мы удалили все, значит не записываем его в общий массив корзины
                    }
                }
            });

            PrArray = [...NewBasketProductArray];

        } else {
            //Корзина пуста, добавляем первый товар
            // PrArray = [setOneProduct(idProduct, TypeProduct, idCategory)];
        }


        //////
    };



    const addProductBasketHandler = async (id,type_id,numberProductInCart = 0,numberTypeProductInCart = 0) => {
       await checkCartProduct(id,type_id,numberProductInCart,numberTypeProductInCart);
        await dispatch(addProductBasket(PrArray));
    };


    const removeProductBasketHandler = async (id,type_id,numberProductInCart = 0,numberTypeProductInCart = 0) => {
       await checkCartProductRemove(id,type_id,numberProductInCart,numberTypeProductInCart);
        await dispatch(addProductBasket(PrArray));
    };


    const returnButtonIncart = (typeProductModify = false)=>{
        // reloadBasketData(Product.id,Product.type_product);

        if(inCart){
            return (<View style={styles.BlockButtonCount} >
                    <TouchableOpacity activeOpacity={0.7} onPress={() => removeProductBasketHandler(Product.id,Product.type_product,numberProductInCart,numberTypeProductInCart)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>-</Text>
                        </View>
                    </TouchableOpacity>
                    <View style={styles.CountProductInCart}>
                        <Text style={styles.CountProductInCartText}>{countProductInCart}</Text>
                    </View>
                    <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id,Product.type_product,numberProductInCart,numberTypeProductInCart)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>+</Text>
                        </View>
                    </TouchableOpacity>
                </View>
            )
        }else{
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id,Product.type_product,numberProductInCart,numberTypeProductInCart)}>
                    <View style={
                        SizeButton == 'normal' ? styles.ButtonBlock : styles.ButtonBlockMini}>
                        <Text style={SizeButton == 'normal' ?  styles.buttonText : styles.buttonTextMini }>{titleButton}</Text>
                    </View>
                </TouchableOpacity>
            )
        }
    };

    return returnButtonIncart(typeProductModify)
};

const styles = StyleSheet.create({
    BlockButtonCount:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        // width: 100
    },
    CountProductInCart:{
        color: "#252622",
        paddingHorizontal: 10
    },
    CountProductInCartText:{
        color: "#252622",
    },
    ButtonBlock: {
        paddingHorizontal:13,
        paddingVertical:5,
        paddingBottom:7,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:10
    },
    ButtonBlockMini: {
        paddingHorizontal:10,
        paddingVertical:2,
        paddingBottom:4,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:7
    },
    buttonText:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:14,
        color:THEME.TEXT_COLOR
    },
    buttonTextMini:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:10,
        color:THEME.TEXT_COLOR
    }
});
