import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity,
    Dimensions
} from 'react-native'
import {THEME} from "../../../theme";
import {ButtonInCart} from "./../ButtonInCart";
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";
import {useSelector} from "react-redux";



export const OneKomboProduct = ({ product, onOpen }) => {
    product = product[0];
    const typeDeviceVK = useSelector(state => state.post.typeDeviceVK)
    //console.log("Вот: ",product[0].title, "Что пришло в комбо One");
    // return null;
    return (
            <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? [styles.productBlock,{width:'48%',marginHorizontal:'1%'}] : styles.productBlock}>
                <View style={styles.textWrap}>
                    <View style={{flexDirection:'row', width:'85%'}}>
                        <Image style={styles.image} source={{ uri: product.img }} />
                        <View style={styles.TextBlockProduct}>
                            <View style={styles.BlockTitle}>
                                <Text style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? [styles.title,{fontSize:16}] :styles.title}>
                                    {product.title.trim()}
                                </Text>
                            </View>
                            {product.description !== '' ? <View style={styles.ViewDescription}>
                                <Text style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? [styles.description,{fontSize:13}] : styles.description}>
                                    {product.description.trim()}
                                </Text>
                            </View> : <Text></Text>}
                        </View>
                    </View>
                    <View style={{width:getTypeDevice(Dimensions.get('window').width, Dimensions.get('window').height,typeDeviceVK) ?'11%' : '7%'}}><Text style={styles.weight}>{product.count} шт.</Text></View>
                </View>
            </View>
    )
};

const styles = StyleSheet.create({
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden'
    },
    image: {
        width: THEME.WIDTH_COMBO_PRODUCT_IMG,
        height: THEME.HEIGHT_COMBO_PRODUCT_IMG,
        // marginRight:5,
        marginRight:10,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"55%"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch",
        width:"99%"
    },
    textWrap: {
        flexDirection:"row",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 5,
        alignItems: 'center',
        width: '100%'
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize: 13,
        width:"70%",
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    price: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:15
    },
    ViewPrice:{
        width:'98%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:1,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:3,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'80%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    }
})
