import {Dimensions, Image, ImageBackground, StyleSheet, Text, View} from "react-native";
import {AntDesign, Ionicons} from "@expo/vector-icons";
import React, {useEffect,useState} from "react";
import { useDispatch, useSelector } from 'react-redux';
import {URL_IMG_BACKGROUND_GIFT_SCALE_PASSIVE, URL_IMG_BACKGROUND_GIFT_SCALE_ACTIVE, URL_SITE} from "../../store/types";
import {THEME} from "../../theme";
import {ButtonEbee} from "../Button/ButtonEbee";
import {NumberFormatPrice} from "../NumberFormat/NumberFormat";
import {updateActiveGifts} from "../../store/actions/post";
import {ThreeForTwoComponent, ThreeForTwoComponentBool} from "../ThreeForTwo/ThreeForTwo";
import {getTypeDevice} from "../GetTypeDeviceSize/GetTypeDeviceSize";


export const ScaleGift = (props)=>{
    let Options = {...{
            title:false,
            bottomLabel:false
        },
        ...props
    };
    const dispatch = useDispatch();


    const GiftDataServer = useSelector(state => state.post.giftsList);
    const sumItogBasket = useSelector(state => state.post.basketSum);
    const activeGift = useSelector(state => state.post.activeGift);

    const PromokodeActiveStatus = useSelector(state=>state.post.promokodeActive);
    const SettingsModul = useSelector(state => state.post.TreeForTwoList);
    const ActiveThreeForTwo = useSelector(state => state.post.ThreeForTwoActive);




    const imagePassive = { uri: URL_IMG_BACKGROUND_GIFT_SCALE_PASSIVE };
    const imageActive = { uri: URL_IMG_BACKGROUND_GIFT_SCALE_ACTIVE };


    const getSumActiveGift = (sumProduct)=>{
        if(activeGift !== false){
            let MaxValue = activeGift.min_sum;
            let NowValue = MaxValue-sumItogBasket;

            if(NowValue<=0) {
                return true;
            }else{
                setActiveGift(false);
                return false;
            }
        }else{

        }

    };

    const getWidthScale = ()=>{
        let MinValue = 0;
        // console.log('GiftDataServer',GiftDataServer);
        let MaxValue = GiftDataServer.settingsScale.max_summa;
        let NowValue = (sumItogBasket*100)/MaxValue;
        if(NowValue>100){NowValue = 100;}

        // console.log('Число '+sumItogBasket+' от числа '+MaxValue+' составляет '+NowValue);
        return NowValue+"%";
    };


    const getCountNoSum = ()=>{
        let NoSum = 0;

        let AllGiftStep = GiftDataServer.ListGift;

        let NumbersGiftStep = Object.keys(AllGiftStep);

        NoSum = AllGiftStep[NumbersGiftStep[0]].min_price - sumItogBasket;

        if(NoSum<0){NoSum = 0;}

        return NoSum;
    };

    const setActiveGift = (dataGift)=>{
        if(activeGift !== false) {
            dispatch(updateActiveGifts(dataGift));
        }
    };

    const getActiveGiftBlock = ()=>{
      return(
          <View style={{width:"100%",flexDirection:"row",justifyContent:"center",alignItems:"center",paddingVertical:5,flexWrap:"wrap",borderBottomColor:"#fafafa",borderBottomWidth:3}}>
              <View style={{width:"95%",flexDirection:"row",justifyContent:"space-between",alignItems:"center",paddingVertical:5}}>
                  <View style={{}}>
                      <Image style={{width:THEME.WIDTH_GIFT_IN_CART,height:THEME.HEIGHT_GIFT_IN_CART,borderRadius:THEME.BORDER_RADIUS_DEFAULT}} source={{ uri: URL_SITE+'/images/tovars/'+activeGift.img }} />
                  </View>
                  <View style={{width:"88%",textAlign:"left"}}>
                      <View style={{width:"90%",paddingHorizontal:10}}>
                          <View style={{width:"87%",flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}>
                              <View style={{width:"70%"}}>
                                  <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:14}}>{activeGift.title.trim()}</Text>
                              </View>
                              <View style={{width:"25%"}}>
                                  <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:12}}>{activeGift.type_product_text}</Text>
                              </View>
                          </View>
                          <View>
                              <Text style={{fontFamily:"open-bold, segoe ui, Arial",fontSize:10,paddingRight:20,flexWrap:"wrap"}}>{activeGift.text.trim()}</Text>
                          </View>
                      </View>
                      <View style={{width:"90%",paddingHorizontal:10}}>
                          <Text style={{fontFamily:"open-bold, segoe ui, Arial",fontSize:12,paddingRight:20,flexWrap:"wrap",paddingVertical:2}}>Кол-во: {activeGift.count_product}</Text>
                      </View>
                  </View>
              </View>
              <View style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center",width:"90%",paddingVertical:7,borderTopColor:THEME.BORDER_COLOR_DEFAULT,borderTopWidth:1}}>
                  <View style={{}}>
                      <Text><AntDesign name="gift" size={THEME.SIZE_GIFT_ICON_CART} color={THEME.MAIN_COLOR} /> Подарок!</Text>
                  </View>
                  <View style={{width:120}}>
                      <ButtonEbee
                          onPress={()=>{
                              setActiveGift(false);
                          }}
                          size="l"
                          borderRadius={20}
                          shadow={false}
                          active={true}
                          style={{paddingVertical:8,marginVertical:0}}
                      >
                          Удалить
                      </ButtonEbee>
                  </View>
              </View>
          </View>
      )
    };


    const getGiftNoSum = ()=>{

        let NoSum = getCountNoSum();

            if (NoSum == 0) {
                return (
                    <View style={styles.textGiftNoSum}>
                        {activeGift !== false ?
                            getActiveGiftBlock()
                            :
                            PromokodeActiveStatus ?
                                <Text style={styles.textGiftNoSumText}>Промокоды и подарки не суммируются!</Text>
                                :
                                <Text style={styles.textGiftNoSumText}>Отлично, теперь можно выбрать подарок!</Text>
                        }
                    </View>
                );
            } else {
                setActiveGift(false);
                return (
                    <View style={styles.textGiftNoSum}>
                        <Text style={styles.textGiftNoSumText}>Чтобы выбрать подарок к заказу не хватает: {NoSum}₽</Text>
                    </View>
                );
            }

    };

    const checkActivePromokode =()=>{
        if(PromokodeActiveStatus === true){
            if(activeGift !== false){
                setActiveGift(false);
            }
        }else{

        }
    };


    const checkActiveThreeForTwo =()=>{
        if(SettingsModul?.dopSettings?.additionGifts === false){
            if(SettingsModul?.dopSettings?.priorityGift === false){
                // console.log('Не суммируется с подарками и подарки не в приоритете');
                setActiveGift(false);
            }
        }else{

        }
    };

    useEffect(()=>{
        getSumActiveGift();
        checkActivePromokode();
        checkActiveThreeForTwo();
        getWidthScale();
    },[sumItogBasket,PromokodeActiveStatus]);

    const getButtonGift = ()=>{
        let NoSum = getCountNoSum();
        if(NoSum == 0){
            return (
                <View style={styles.ButtonBlock}>
                    {PromokodeActiveStatus ?
                        <ButtonEbee
                            onPress={() => {
                                // console.log('Опа опа Подарок не можешь выбери а');
                            }}
                            size="l"
                            borderRadius={20}
                            shadow={false}
                            active={false}
                            style={{color: THEME.TEXT_DESCRIPTION_PRODUCT}}

                        >
                            Выбрать
                        </ButtonEbee>
                        :
                        <ButtonEbee
                            onPress={() => {
                                // console.log('Опа опа Подарок выбери а');
                                Options.handleSetStatusModal('MODAL_GIFTS', true);
                            }}
                            size="l"
                            borderRadius={20}
                            shadow={false}
                            style={{color: "#252527", backgroundColor: "#ffffff"}}
                        >
                            {activeGift !== false ? "Заменить" : "Выбрать"}
                        </ButtonEbee>
                    }
                </View>
            )
        }else{
            return (
                <View style={styles.ButtonBlock}>
                    <ButtonEbee
                    onPress={()=>{
                        // console.log('Опа опа Подарок не можешь выбери а');
                    }}
                    size="l"
                    borderRadius={20}
                    shadow={false}
                    active={false}
                    style={{color:THEME.TEXT_DESCRIPTION_PRODUCT}}

                    >
                    Выбрать
                    </ButtonEbee>
                </View>
            )
        }

    };


    if("ready" in GiftDataServer){
        if(GiftDataServer.ready){

            // console.log('РЕЗУЛЬТАТ ВЫПОЛНЕНИЯ ПРОВЕРКИ НА АКТИВНОСТЬ МОДУЛЯ 3=2 ТАКОЙ = ',ActiveThreeForTwo);
            let statusActiveThreeForTwo = true;
            if(SettingsModul?.dopSettings?.additionGifts === false & ActiveThreeForTwo === true){
                if(SettingsModul?.dopSettings?.priorityGift === false){
                    // console.log('Должен быть отключен блок с выбором подарка!');
                    statusActiveThreeForTwo = false;
                }else{

                }

            }else{

            }

            if(statusActiveThreeForTwo === true) {

                getWidthScale();
                // console.log("ШИРИНА БОНУСА", getWidthScale(),imageActive,imagePassive)
                // getGiftNoSum();
                return (
                    <View>
                        <View style={[styles.View, props.styles]}>
                            <ImageBackground source={imagePassive} resizeMode="repeat" style={styles.imagePassive}>
                                <View style={{
                                    position: "absolute",
                                    backgroundColor: "#fafafa",
                                    // background:"url(https://суши-тайм.рф/images/system_icons/api_gift/pattern_gift_mobile_active1.jpg) center center repeat;",
                                    // overflow: "hidden",
                                    height:"100%",
                                    width: getWidthScale()
                                }}>
                                    {/*<View style={[styles.image,{height:"100%",width:"100%",background:"url(https://суши-тайм.рф/images/system_icons/api_gift/pattern_gift_mobile_active1.jpg) center center repeat;"}]}>*/}
                                    {/*    dgfgdf*/}
                                    {/*</View>*/}
                                    {/*<ImageBackground source={imageActive} resizeMode="cover" style={styles.image}>*/}
                                    {/*</ImageBackground>*/}
                                    <div style={{width:"100%",height:"100%",background:"url("+URL_IMG_BACKGROUND_GIFT_SCALE_ACTIVE+") center left repeat-x",backgroundSize:"300px"}}></div>
                                </View>
                                <View style={{
                                    width: "100%",
                                    flexDirection: "row",
                                    justifyContent: "center",
                                    alignItems: "center"
                                }}>
                                    {getButtonGift()}
                                </View>
                            </ImageBackground>
                        </View>
                        {getGiftNoSum()}
                    </View>
                );
            }else{
                return false;
            }
        }else{
            return false;
        }
    }else{
        return false;
    }


};


const styles = StyleSheet.create({
    View:{
        backgroundColor:THEME.MAIN_COLOR,
        // width:Dimensions.get('window').width,
        width:"100%",
        // height:201, // старое значение
        height:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 80 : 100,
        justifyContent:"space-between",
        alignItems:"center",
        flexDirection:"row",
        zIndex:99
    },
    h2:{
        fontSize:18,
        textTransform:"uppercase"
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 3,
        marginBottom:10
    },
    shadowOff:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 0,
        marginBottom:10
    },
    image:{
        flex: 1,
        flexDirection:"row",
        justifyContent: "flex-start",
        // alignItems:"center",
        // alignSelf:'center',
        // width:Dimensions.get('window').width,
        // width:Dimensions.get('window').width,
        // height:201, // старое значение
        height:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 80 : 100,
        // inset:'-101px 0 0 0' // старое значение
        inset: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? '-40px 0 0 0' : '-50px 0 0 0'
    },
    imagePassive:{
        flex: 1,
        flexDirection:"row",
        justifyContent: "flex-start",
        alignItems:"center",
        // width:Dimensions.get('window').width,
        // height:201, // старое значение
        height:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 80 : 100,
        alignSelf: "flex-start",

    },
    textGiftNoSum:{
        flexDirection:"row",
        textAlign: "center",
        justifyContent:"center",
        alignItems:"center"
    },
    textGiftNoSumText:{
        width:"92%",
        textAlign:"center",
        fontSize: 13,
        paddingVertical:10,
        borderBottomColor:THEME.BORDER_COLOR_DEFAULT,
        borderBottomWidth:1
    },
    ButtonBlock:{
        width:140
    }
});