import React, { useState, useRef, useEffect } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {
    KeyboardAvoidingView,
    Platform,
    Pressable,
    SafeAreaView,
    ScrollView,
    StyleSheet,
    Text,
    TextInput,
    View,
    Alert, ActivityIndicator
} from "react-native";
import {THEME} from "../../theme";
import {ServerDB} from "../../serverDb";
import {loadAdressUser} from "../../store/actions/post";


export const NewAdresScreen = ({ navigation, route }) => {

    const dispatch = useDispatch();
    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    const [ShowHelpListStreet,setShowHelpListStreet] = useState(false);
    const [ListStreetPlaceholder,setListStreetPlaceholder] = useState({});
    const [loadNewAdresServer,setLoaderServer] = useState(false);

    const [cityStreetDataSet,setCityStreetUser] = useState({
        city: '',
        street: '',
        streetSearch: '',
        home: '',
        entrance: '',
        level: '',
        flat: '',
        comment:'',
        streetCityVisual:'',
        streetName:null,
        streetList:{},
        search:'',
        pauseLoadStreet:true
    });

    const GetListAdresStreet = async (text)=>{
        if(text.length<3){
            setShowHelpListStreet(false);
        }else {
            const listServerReturn = await ServerDB.getStreetList(text, CityIds);
            // console.log('Получили список улиц ', listServerReturn.result);
            setListStreetPlaceholder(listServerReturn.result);
            setShowHelpListStreet(true);
        }
    };

    const SetStreetCityAdres = (Localitytype,LocalityName,LocalityId,typeStreet,streetName)=>{
        setCityStreetUser({
            ...cityStreetDataSet,
            ...{
                city: LocalityName,
                street: typeStreet+' '+streetName,
                streetSearch: streetName,
                streetCityVisual: Localitytype+' '+LocalityName+', '+typeStreet + ' ' + streetName,
                pauseLoadStreet:false
            }
    });
        setShowHelpListStreet(false);
    };

    const GetListStreetPlaceholder = ()=>{
        return (
            Object.keys(ListStreetPlaceholder).length > 0 ?
                    Object.keys(ListStreetPlaceholder).map((number,id) =>
                            <Pressable
                                style={({ pressed }) => [

                                        styles.OneStreetBlock,
                                        {
                                            opacity: pressed
                                                ? 0.5
                                                : 1
                                        }

                                ]
                                }
                                onPress={
                                    ()=>{
                                        SetStreetCityAdres(
                                            ListStreetPlaceholder[number].typeLocality,
                                            ListStreetPlaceholder[number].LocalityName,
                                            ListStreetPlaceholder[number].idLocality,
                                            ListStreetPlaceholder[number].typeStreet,
                                            ListStreetPlaceholder[number].name);
                                    }
                            }
                                  key={ListStreetPlaceholder[number].id}
                            >
                                <Text style={styles.TitleCityStreet}>{ListStreetPlaceholder[number].LocalityName}</Text>
                                <Text >{ListStreetPlaceholder[number].typeStreet} {ListStreetPlaceholder[number].name}</Text>
                            </Pressable>
                    )
                :
                <View>
                    <Text>Ничего не найдено</Text>
                </View>
        )
    };

    const AlertErrorNewAdres = ()=>{
        Alert.alert('Не все поля адреса заполнены','Внимательно заполните все поля, для сохранения адреса доставки!');
    };

    const SaveNewAdres = async ()=>{
        if(cityStreetDataSet.city === '' || cityStreetDataSet.street === '' || cityStreetDataSet.home === ''){
            AlertErrorNewAdres();
        }else{
            setLoaderServer(true);
            const ResultSave = await ServerDB.saveNewAdresServer(cityStreetDataSet,CityIds,idUserSystem);
            // Alert.alert('Все в порядке, сохраняем');
            if(ResultSave){
                ReloadListAdres();
            }else{
                setLoaderServer(false);
                Alert.alert('Ошибка','К сожалению не удалось сохранить адрес, попробуйте через пару минут!');
            }
        }
    };

    const ReloadListAdres = async (CityIds)=>{
        // const Results = await loadAdressUser(idUserSystem,[],{cityId:CityIds});
        navigation.navigate('AdressList',{reloads:true});
        return true;
    };

    const ButtonSaveAdres = ()=>{
        if(cityStreetDataSet.city === '' || cityStreetDataSet.street === '' || cityStreetDataSet.home === ''){
            return (
                <Pressable
                    style={[styles.buttonCity,styles.errorButton]}
                >
                    <Text
                        style={styles.textRefreshButton}
                    >Сохранить</Text>
                </Pressable>
            )
        }else{
            return (
                <Pressable
                    style={styles.buttonCity}
                    onPress={() => {
                        SaveNewAdres();
                    }}
                >
                    <Text
                        style={styles.textRefreshButton}
                    >Сохранить</Text>
                </Pressable>
            )
        }
    }

    if(loadNewAdresServer){
        return (
            <View style={styles.center}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
                <Text>Сохранение...</Text>
            </View>
        )
    }

    return(
        <KeyboardAvoidingView style={styles.centeredViewWindowTop} behavior={Platform.OS === 'android' ? false : "height"} keyboardVerticalOffset={70}>
        <ScrollView style={styles.centeredViewWindowTop}>
            <View style={styles.modalView}>
                {/*<Text style={styles.modalText}>Улица</Text>*/}
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        // console.log('Запрашиваем список улиц');
                        GetListAdresStreet(text);
                        setCityStreetUser({...cityStreetDataSet,...{street:text}});
                    }}
                    //maxLength={5}
                    value={cityStreetDataSet.pauseLoadStreet ? cityStreetDataSet.street : cityStreetDataSet.streetCityVisual}
                    multiline={false}
                     placeholder={'Улица'}
                    textAlign={'left'}
                    editable={cityStreetDataSet.pauseLoadStreet ? true : false}
                   // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
                {ShowHelpListStreet ? <View style={styles.BlockStreets}><GetListStreetPlaceholder /></View>: false}
            </View>
            <View style={styles.modalView}>
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        setCityStreetUser({...cityStreetDataSet,...{home:text}});
                        //console.log('Вводим код из смс');
                    }}
                    //maxLength={5}
                    //value={pincodeNumber}
                    multiline={false}
                     placeholder={'Дом/Строение'}
                    textAlign={'left'}
                   // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
            </View>
            <View style={styles.modalView}>
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        setCityStreetUser({...cityStreetDataSet,...{flat:text}});
                        //console.log('Вводим код из смс');
                    }}
                    //maxLength={5}
                    //value={pincodeNumber}
                    multiline={false}
                    placeholder={'Квартира'}
                    textAlign={'left'}
                    // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
            </View>
            <View style={styles.modalView}>
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        setCityStreetUser({...cityStreetDataSet,...{entrance:text}});
                        //console.log('Вводим код из смс');
                    }}
                    //maxLength={5}
                    //value={pincodeNumber}
                    multiline={false}
                     placeholder={'Подъезд'}
                    textAlign={'left'}
                   // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
            </View>
            <View style={styles.modalView}>
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        setCityStreetUser({...cityStreetDataSet,...{level:text}});
                        //console.log('Вводим код из смс');
                    }}
                    //maxLength={5}
                    //value={pincodeNumber}
                    multiline={false}
                     placeholder={'Этаж'}
                    textAlign={'left'}
                   // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
            </View>
            <View style={styles.modalView}>
                <TextInput
                    style={[styles.phoneInput, styles.codeInputDefault]}
                    onChangeText={(text) => {
                        setCityStreetUser({...cityStreetDataSet,...{comment:text}});
                        //console.log('Вводим код из смс');
                    }}
                    //maxLength={5}
                    //value={pincodeNumber}
                    multiline={false}
                    placeholder={'Название адреса'}
                    textAlign={'left'}
                    // keyboardType={Platform.OS === 'android' ? "phone-pad" : "phone-pad"}
                />
            </View>
            <ButtonSaveAdres/>
        </ScrollView>
        </KeyboardAvoidingView>
    )
};

NewAdresScreen.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;
    const toggleHandler = route.params.toggleHandler;
    return {
        headerTitle: title,
    }
};

