import React, { useState, useEffect, useCallback } from 'react'
import {
    View,
    Text,
    StyleSheet,
    Image,
    Button,
    ScrollView,
    SafeAreaView,
    Alert, FlatList, ActivityIndicator, Dimensions
} from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import { Item, HeaderButtons } from 'react-navigation-header-buttons'
import { AppHeaderIcon } from '../components/AppHeaderIcon'
import { THEME } from '../theme'
import {toogleBooked, removePost, updateModifyTmp} from '../store/actions/post'
import {ButtonInCart} from "../components/Catalog/ButtonInCart";
import {KomboProductList} from "../components/Catalog/Kombo/KomboProductList";
import {FeacturesList} from "../components/Catalog/Features/FeaturesList";
import {Price} from "../components/Catalog/Product/ProductCatd/Price";
import {ModyficateBlock} from "../components/Catalog/Product/Modyficate/ModyficateBlock";
import {NumberFormatPrice} from "../templateComponents/NumberFormat/NumberFormat";
import {ButtonInCartullWidth} from "../components/Catalog/ButtonInCartFullWidth";
import {ProductModify} from "../components/Catalog/Product/ProductModify/ProductModify";
import {PriceProductFromModify} from "../components/Catalog/Product/ProductModify/PriceProductFromModify";
import {ModifyComboModal} from "../components/Catalog/Product/ModifyComboModal/ModifyComboModal";
import {getTypeDevice} from "../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const ProductScreen = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const postId = route.params.postId;

    const product = route.params.productData;

    if (!product) {
        return null
    }

      //console.log('Что внутри товара которйы открыли ',product);

    const FeatureMap = ({featureL})=>{
         // console.log("ТО ЧТО НУЖНО В ЦИКЛ ПОДАТЬ",featureL, "ЗАКОНЧИЛСЯ");
        const RenderRes = Object.keys(featureL).map((id,t)=>{
            //console.log(id,t,featureL[id].show);
            if(featureL[id].show === 'show'){
                return (<FeacturesList data={[featureL[id]]} key={"FeatureListNumber_"+id}/>);
            }else{
            }
        });
        // console.log("То что отдаем из рендера ",RenderRes);
        return RenderRes;
    };

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);

    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);


    let CheckNulls = null;

    if(TypeDeliverySystem === true){
        CheckNulls = DeliveryAdresSystem;
    }else{
        CheckNulls = AffiliateAdresSystem;
    }

    const GetTextCHeckAdress = ()=>{
        return (
            <Text style={{height:"100%",color:THEME.DANGER_COLOR}}>
                Для добавления товара в корзину, нужно выбрать адрес доставки или точку самовывоза
            </Text>
        )
    }

    const DataScreen = ()=>(
     <View key={"Info_Products_"+product.id}>
         <View style={styles.productBlock}>
             <View style={styles.BlockImg}>
                 <Image style={styles.image} source={{ uri: product.img }} />
             </View>
             <ModyficateBlock blockStyle={{left: 5}} ModyficateProduct={product.modyficate} styleImg={styles.styleImgModyficate}/>
             <View style={styles.textWrap}>
                 <View style={styles.TextBlockProduct}>
                     <View style={styles.BlockTitle}>
                         <Text style={styles.title}>
                             {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                         </Text>
                         <Text style={styles.weight}>{NumberFormatPrice(product.weight,0,'.',' ')}{product.weight_type}</Text>
                     </View>
                     {product.description !== '' ? <View style={styles.ViewDescription}>
                         <Text style={styles.description}>
                             {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                         </Text>
                     </View> : <View style={styles.ViewDescription}><Text></Text></View>}
                     {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                         <Text style={styles.TypeInfo}>
                             {product.type_info.trim()}
                         </Text>
                     </View> : false}
                     <View style={styles.ViewPrice}>
                         <Price ProductData={product} stylePrice={[styles.price,{color:THEME.TEXT_DESCRIPTION_COLOR}]} styleNewPrice={[styles.price,{color:THEME.MAIN_COLOR}]} styleOldPrice={{fontSize:12}}/>
                         <View style={{marginRight:"-2vw"}}>
                             {CheckNulls !== null ?
                                 <ButtonInCart Product={product}/>
                                 :
                                 false
                             }
                         </View>
                     </View>
                     {CheckNulls === null
                         ? <View style={{paddingVertical:"3%"}}><GetTextCHeckAdress /></View> : false
                     }
                 </View>
             </View>
         </View>
         <View>
             <FeatureMap featureL={product.featuresProduct} key={"Features_Block"}/>
         </View>
         <View>
             {typeof product.komboPoduct === 'object' ? <KomboProductList data={[product.komboPoduct]} key={"kombo_BlockList"}/> : false }
         </View>
     </View>
    );

    const DataScreenModifyProduct = ()=>(
        <View key={"Info_Products_"+product.id} style={{flex:1,paddingBottom:55}}>
            <FlatList
                data={[{id: "erer", title: "titlere"}]}
                keyExtractor={feature => "Blockswe_" + feature.id}
                //listKey={(feature,index) => "Blocks_"+index}
                renderItem={
                    (item) => (
                        <View>
                            <View style={styles.productBlock}>
                                <View style={styles.BlockImg}>
                                    <Image style={styles.image} source={{uri: product.img}}/>
                                </View>
                                <ModyficateBlock blockStyle={{left: 5}} ModyficateProduct={product.modyficate}
                                                 styleImg={styles.styleImgModyficate}/>
                                <View style={styles.textWrap}>
                                    <View style={styles.TextBlockProduct}>
                                        <View style={styles.BlockTitle}>
                                            <Text style={styles.title}>
                                                {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                            </Text>
                                            <Text
                                                style={styles.weight}>{NumberFormatPrice(product.weight, 0, '.', ' ')}{product.weight_type}</Text>
                                        </View>
                                        {product.description !== '' ? <View style={styles.ViewDescription}>
                                            <Text style={styles.description}>
                                                {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                            </Text>
                                        </View> : <Text></Text>}
                                        {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                                            <Text style={styles.TypeInfo}>
                                                {product.type_info.trim()}
                                            </Text>
                                        </View> : false}
                                        {/*<View style={styles.ViewPrice}>*/}
                                        {/*    <Price ProductData={product} stylePrice={styles.price} styleNewPrice={styles.price} styleOldPrice={{fontSize:12}}/>*/}
                                        {/*    <ButtonInCart Product={product}/>*/}
                                        {/*</View>*/}
                                    </View>
                                </View>
                            </View>
                            {/*<View>*/}
                            {/*    {typeof product.komboPoduct === 'object' ? <KomboProductList data={[product.komboPoduct]} key={"kombo_BlockList"}/> : false }*/}
                            {/*</View>*/}
                            <View style={{paddingHorizontal:'2%',paddingBottom:'2%'}}>
                                <ProductModify product={product}
                                               data={[product?.modifyListProduct || []]}
                                               key={"kombo_BlockList"}
                                               typeModify={product.typeModalModify} //Тип модификаторов 0 - список, 1 - комбо карточка
                                               countMustModify={product.count_must_modify} //кол-во обязательных модификаторов
                                               // onPress={onPress}
                                               // modificationActive={modificationActive}
                                />
                            </View>
                            <View>
                                <FeatureMap featureL={product.featuresProduct} key={"Features_Block"}/>
                            </View>
                        </View>
                    )
                }
                >
            </FlatList>
            <View style={styles.fixedBlockButton}>
                {CheckNulls !== null ?
                    <ButtonInCartullWidth
                        Product={product}
                        titleButton={'В корзину за'}
                        priceTextButton={
                            <PriceProductFromModify
                                ProductData={product}
                                stylePrice={{fontSize: 18, color: "#ffffff",fontFamily:"open-bold, segoe ui, Arial", fontWeight:700}}
                                styleNewPrice={[styles.price, {fontSize: 13}]}
                                styleOldPrice={{fontSize: 9, color: THEME.TEXT_COLOR}}
                            />
                        }
                        typeProductModify={true}
                        goBack={() => navigation.goBack()}
                    />
                    :
                   <GetTextCHeckAdress />
                }
            </View>
        </View>
    );

    const loadingProduct = useSelector(state => state.post.loadingProductScreen);

    if(loadingProduct){
        return(
            <View style={styles.center}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
            </View>
        );
    }

    useEffect(()=>{
        dispatch(updateModifyTmp({"ModifyList":[],"ModifyPrice":0}));
    })

    return (
        <SafeAreaView style={styles.BodyView}>
            {product?.typeProductModify ? <DataScreenModifyProduct key={"DataScreenModifyProduct_"}/> :
                <FlatList
                    data={[{id: "er", title: "title"}]}
                    keyExtractor={feature => "Blocks_" + feature.id}
                    //listKey={(feature,index) => "Blocks_"+index}
                    renderItem={
                        (item) => (
                            <View>
                                <DataScreen key={"DataScreen_"}/>
                            </View>
                        )
                    }>
                </FlatList>
            }
            <ModifyComboModal navigation={navigation} route={route}/>
        </SafeAreaView>
    )
}

ProductScreen.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;
    // const booked = route.params.booked;
    // const toggleHandler = route.params.toggleHandler;
    // const iconName = booked ? 'ios-star' : 'ios-star-outline'
    return {
        headerTitle: title,
        headerLeft: (
            <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
            <Item
             title='Toggle Drawer'
              iconName='arrow-back'
             color={"#333333"}
              onPress={() => navigation.goBack()}
             />
           </HeaderButtons>
     ),
        drawerLockMode: 'locked-closed',
    }
}

const styles = StyleSheet.create({
    center: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center'
    },
    BodyView:{
        // backgroundColor:"#ff7300",
        flex:1,
        // minHeight:"90%",
        height:"auto",
        paddingHorizontal:'2%'
    },
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden',
    },
    BlockImg:{
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center"
    },
    textWrap: {
        flexDirection:"row",
        justifyContent:"center",
        backgroundColor: '#ffffff',
        paddingVertical: 5,
        alignItems: 'center',
        // width: '100%',
        width: '97vw',
        alignSelf:'center',
        marginBottom: 10
    },
    image: {
        width: THEME.WIDTH_PRODUCT_IMG,
        height: THEME.HEIGHT_PRODUCT_IMG,
        marginRight:0,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"95%"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch",
        width:"98%"
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        // fontSize: 17,
        fontSize:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 20 : Dimensions.get('window').width >460 ? 20 : 17,
        width:"80%",
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        // fontSize:12,
        fontSize:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 14 : Dimensions.get('window').width >460 ? 14 : 12,
        marginRight: "-2vw"
    },
    ViewDescription:{
        width:'99%',
        paddingBottom:10
    },
    price: {
        // color: THEME.TEXT_DESCRIPTION_COLOR,
        // fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:18,
        color:THEME.TEXT_COLOR
    },
    ViewPrice:{
        width:'98%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:12,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:1,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:3,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'99%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    },
    styleImgModyficate:{
        width:40,
        height:40
    },
    fixedBlockButton:{
        width:"100%",
        // height:55,
        height:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 58 : Dimensions.get('window').width >460 ? 58 : 56,
        position:"absolute",
        bottom:0,
        textAlign: "center",

        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 22
        },
        shadowOpacity: 0.60,
        shadowRadius: 40,
        elevation: 6,
        marginBottom:0,
        borderTopWidth:1,
        borderTopColor:"#eeeeee",
        backgroundColor:"#ffffff",
        paddingTop:8
    }
})
