export const GoodPage = ({ navigation, expoPushToken, route }) => {

  if (
      Platform.OS === "android" &&
      UIManager.setLayoutAnimationEnabledExperimental
  ) {
    UIManager.setLayoutAnimationEnabledExperimental(true);
  }

  let CustomLayoutAnimation = {
    duration: 200,
    create: {
      type: LayoutAnimation.Types.easeOut,
      property: LayoutAnimation.Properties.opacity,
    },
    update: {
      type: LayoutAnimation.Types.easeOut,
      property: LayoutAnimation.Properties.opacity,
    },
    delete: {
      type: LayoutAnimation.Types.easeOut,
      property: LayoutAnimation.Properties.opacity,
    },
  };



  //console.log('ЗАГРУЗКА........');

  const [statusScroll,setStatusScroll] = useState(true);

  const openPostHandler = post => {
    navigation.navigate('Post', {
      postId: post.id,
      date: post.date,
      booked: post.booked
    })
  }

  const HashParam =  parseQueryString(window.location.hash);

  const openProductScreenHashLink = (productId,productIdCategory)=>{
    let product = null;

    Object.keys(allProduct).map((key,n)=>{
      //Проходимся по списку категорий
        let ProductsListActive = allProduct[key].ProductList;
        Object.keys(ProductsListActive).map((id,num)=>{
          //Проходим по списку товаров в категории
          ProductsListActive[id].map((type)=>{
            //Получаем данные товара
            if(id == productId){
              product = {...type,
                id:id,
                img:"https:"+type.img
              };
            }
          });

        });
    });

    if(product !== null){
      openProductHandler(product);
      loadProductScreenHandler(product);
    }else{
        //console.log('Товар не найден в списке - страница товара не открывается');
      return false;
    }

  }


  const openProductHandler = product => {
    //console.log('Открываем товар');
    navigation.navigate('Product', {
      productId: product.id,
      title: product.title,
      booked: product.booked,
      productData:product
    });
  };

  const dispatch = useDispatch();

  let catIdNew = 0;
  let productForScreen = [];



  const flatListRef = useRef();

  const handleItemPress = () => {
   // console.log('Прокрутка');
    flatListRef.current.scrollToOffset({ animated: false, offset:0});
  };


  const setCategoryActiveHandler = useCallback((categoryId) => {
    LayoutAnimation.configureNext(CustomLayoutAnimation);
    //handleItemPress(0);
    dispatch(setActiveCategoryId(categoryId));

    catIdNew = categoryId
  }, [dispatch,catIdNew]);


  const loadProductScreenHandler = useCallback((product) => {
    dispatch(loadProductScreen(product))
    productForScreen = product
  }, [dispatch,productForScreen]);

  const handlReloadCatalogStatus = useCallback((status,tho = false) => {
    LayoutAnimation.configureNext(CustomLayoutAnimation);

    dispatch(setStatusReloadCategoryCatalog(status));
    if(tho === true){
      LayoutAnimation.configureNext(CustomLayoutAnimation);

      setTimeout(()=>handlReloadCatalogStatus(false,false),1000);
    }
  }, [dispatch]);


  const CityData = useSelector(state => state.post.cityData);
  const ReloadCityData = useSelector(state => state.post.cityReloadSystem);

  // console.log('Город этот ',CityData);


  const basket = useSelector(state => state.post.basket);
  const idUserSystem = useSelector(state => state.post.AuthUserData.id);
  const UserSystem = useSelector(state => state.post.AuthUserData);
  const AuthUserStatus = useSelector(state => state.post.AuthUserStatus);

  // useEffect(() => {
  //   dispatch(loadAdressUser(idUserSystem,basket,CityData))
  // }, [dispatch]);


  const allPosts = useSelector(state => state.post.allPosts);
  const loading = useSelector(state => state.post.loading);
  const loadingSlider = useSelector(state => state.post.loadingSlider);
  const loadingCatalog = useSelector(state => state.post.loadingCatalog);

  const allSlide = useSelector(state => state.post.allSlide);
  const allProduct = useSelector(state => state.post.allProducts);
  const allCategory = useSelector(state => state.post.allCategory);
  const activeCategory = useSelector(state => state.post.activeCategoryProductList);
  const adressUserSelect = useSelector(state => state.post.adressSelect);
  const activeFilter = useSelector(state => state.post.activeFilterCategory);



  useEffect(() => {
if(ReloadCityData) {
  LayoutAnimation.configureNext(CustomLayoutAnimation);
  dispatch(ReloadCityDataSet(false));
  dispatch(loadSlideHeader(CityData));


  dispatch(loadAdressUser(idUserSystem, basket, CityData));
  dispatch(loadAdressAffiliate(CityData));

  //Нужно предусмотреть обновление каталога, по разным условиям
  dispatch(loadCatalogProduct(CityData));
  dispatch(loadGiftsList(CityData));
  dispatch(loadTreeForTwoList(CityData));
}

    // if(expoPushToken !== null & expoPushToken !== undefined) {
    //   console.log('Ключ ushToken существует, делаем запрос списка уведомлений.');
    //   dispatch(loadPushNoticeList(idUserSystem,expoPushToken,true));
    // }

    if('getPageProduct' in HashParam) {
      // this.state.loadingCatalog = false;
      // this.setHashNew('notifications');
      // console.log('Показать модалку товара - ', HashParam.idProduct, 'из категории - ', HashParam.idCategory);
      openProductScreenHashLink(HashParam?.idProduct,HashParam?.idCategory);
    }

  },[ReloadCityData]);

  // useEffect(()=>{
  //   if(expoPushToken !== null & expoPushToken !== undefined) {
  //     // console.log('Ключ pushToken существует, делаем запрос списка уведомлений.',UserSystem,AuthUserStatus);
  //     dispatch(loadPushNoticeList(idUserSystem,expoPushToken,true));
  //   }
  // },[]);

  let reloadedCatalog = false;


  if (loadingCatalog) {
    return (
      <View style={styles.center}>
        <ActivityIndicator color={THEME.MAIN_COLOR} />
      </View>
    )
  }

  const setActiveProductList = ()=>{
    let activeCategorys = activeCategory;
    let ActiveFilterCategory = activeFilter;
    if(ActiveFilterCategory === 'all'){
      ActiveFilterCategory = null;
    }

    if(activeCategorys === null){
      let SortCategoryActives = allCategory.sort(( a, b ) => a.orders - b.orders);
      activeCategorys = SortCategoryActives[0].id;
      // console.log('adressUserSelect',adressUserSelect);

      ///


      let ActiveCategorysCheck = SortCategoryActives[0].id;
      let OkAddress = [];
      let DataOff = adressUserSelect?.OffCategoryData?.OffCategory;
      if(typeof (adressUserSelect?.OffCategoryData?.OffCategory) === "object") {
        DataOff = Object.keys(adressUserSelect?.OffCategoryData?.OffCategory);
      }

      SortCategoryActives.forEach((dc, nc) => {
        let AddressOk = true;
        DataOff?.forEach((ad, nd) => {
          if (dc.id == ad){
            // console.log(dc.id+' == '+ad);
            AddressOk = false;
          }
        });
        if(AddressOk === true) {
          OkAddress.push(dc.id);
        }
      });
      // console.log('OkAddress',OkAddress);
      if(OkAddress.length > 0) {
        dispatch(setActiveCategoryId(OkAddress[0]));
        // console.log('Активная категория и отсортирвоанный каталог ',OkAddress[0],SortCategoryActives[0].id);
      }else{
        // console.log('Активная категория и отсортирвоанный каталог ',ActiveCategorysCheck,SortCategoryActives[0].id);
        dispatch(setActiveCategoryId(ActiveCategorysCheck));
      }

      // console.log(adressSelect);
      // handlReloadCatalogStatus(true,true);
      // dispatch(setActiveCategoryId(SortCategoryActives[0].id));
    }

    let ProductList = [];
        Object.keys(allProduct).map((key,n)=>{
          if(key === activeCategorys){
            let ProductsListActive = allProduct[key].ProductList;
            Object.keys(ProductsListActive).map((id,num)=>{
              // console.log(ProductsListActive[id]);
              ProductsListActive[id].map((type)=>{
                if(ActiveFilterCategory !== null){
                    //console.log('Отсортировали каталого по ',ActiveFilterCategory,type.filters);
                  if(type.filters.length > 0){
                    let statusPush = false;
                    type.filters.map((d,n)=>{
                      if (d.toLowerCase().includes(ActiveFilterCategory.toLowerCase().trim())) {
                        statusPush =true;
                      }
                    });
                    statusPush === true ?
                        ProductList.push({
                          ...type,
                          id: id,
                          img: "https:" + type.img
                        })
                    : false
                  }
                }else {
                  ProductList.push({
                    ...type,
                    id: id,
                    img: "https:" + type.img
                  });
                }

              });
              // ProductList.push(OneProducts);
              //return OneProducts;
            });
          }
        });
        //console.log(ProductList);

    let SortCategory = ProductList.sort(( a, b ) => a.position - b.position);
    // console.log('Должны показать товары из массива',activeCategorys,allCategory[0].id,allCategory[0]);

    return SortCategory;
  };


  const handleScroll = (event)=>{
    //console.log(event.nativeEvent.contentOffset.y);
    if(event.nativeEvent.contentOffset.y <=Dimensions.get('window').height/4){
      setStatusScroll(false);
    }else{
      setStatusScroll(true);
    }
  };

  const handlScrollEnabled = (event)=>{
    //console.log('Состояние скрола родителя ',event);
    // setStatusScroll(event);
    // console.log('Состояние скрола родителя ',statusScroll);
    // scrollChildpren = event;
  };

  return (
      <SafeAreaView style={Platform.OS === 'android' ? {paddingBottom:"0%"} : false}>
        <View style={Platform.OS === 'android' ? {height:"0%"} : {height:0}}>
            {Platform.OS === 'android' ?
                <StatusBar style="light" translucent={false}/>
                : false
            }
        </View>
        <ScrollView
            onScroll={handleScroll}
            scrollEventThrottle={16}
            stickyHeaderIndices={[4]}
            scrollEnabled={true}
            nestedScrollEnabled={true}
            // contentContainerStyle={styles.contentContainer}
            disableIntervalMomentum={true}
            // snapToInterval={ 100 }
            // endFillColor="black"
            fadingEdgeLength={1}
            style={{height:Dimensions.get('window').height}}
        >
          <UpdateModalApp/>
          <View>
            <CityHeaderInfo navigation={navigation} route={route}/>
          </View>

          <View>
            <SelectionBlock navigation={navigation} route={route}/>
          </View>

        <View>
          {/*<MyCarousel loadingSlider={ loadingSlider } data={allSlide} onOpen={()=>{console.log('Нажали на слайд')}}/>*/}
          <SliderTry3 loadingSlider={ loadingSlider } data={allSlide} onOpen={()=>{console.log('Нажали на слайд')}}/>
        </View>
          <View>
             <CategoryList data={allCategory} onClick={setCategoryActiveHandler} onOpenProduct={loadProductScreenHandler} handlReloadCatalogStatus={handlReloadCatalogStatus}/>
          </View>
          <FilterCategorys navigation={navigation} route={route}/>
          <View
              // style={{height:Platform.OS === 'android' ? Dimensions.get('window').height*0.85 : Dimensions.get('window').height*0.84,backgroundColor:"#ff7300",overflow:"hidden"}}
          >
            <CatalogActiveCategory
                data={setActiveProductList(allProduct)}
                onOpen={openProductHandler}
                key={"ActiveProducts"}
                onOpenProduct={loadProductScreenHandler}
                flatListRef={flatListRef}
                scrollParent=""
                scrollChildren={true}
                handlScrollEnabled={handlScrollEnabled}
                reloadedCatalog={reloadedCatalog}
            />
          </View>
          </ScrollView>
      </SafeAreaView>
  );
};

MainScreen.navigationOptions = ({ navigation, route }) => ({
  drawerLockMode: 'locked-closed',
  gesturesEnabled: false,
  headerShown: false,
  headerTitle: false,
  headerRight: (
    <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
      <Item
        title='Take photo'
        iconName='ios-menu'
        onPress={() => navigation.navigate('Product')}
      />
    </HeaderButtons>
  ),
  headerLeft: (
      <>
        {/*<HeaderButtons HeaderButtonComponent={AppHeaderIcon}>*/}
        {/*  <Item*/}
        {/*      title='Toggle Drawer'*/}
        {/*      iconName='ios-location'*/}
        {/*      onPress={() => navigation.toggleDrawer()}*/}
        {/*  />*/}
        {/*</HeaderButtons>*/}
          <CityHeaderInfo navigation={navigation} route={route}/>
      </>
  )
});

