import React, {useEffect, useCallback, useState} from 'react'
import {
    View,
    Text,
    StyleSheet,
    Image,
    Button,
    ScrollView,
    SafeAreaView,
    Alert, FlatList, ActivityIndicator, TouchableOpacity, Dimensions
} from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import { THEME } from '../../theme'
import {toogleBooked, removePost, setSelectAdres, loadAdressUser} from '../../store/actions/post';
import {OneAdress} from "../../screens/DeliveryBlock/OneAdress";
import { Feather } from '@expo/vector-icons';
import {DB} from "../../db";
import {OneAdressProfile} from "./OneAdresProfile";
import {ServerDB} from "../../serverDb";
import {HeaderButtons, Item} from "react-navigation-header-buttons";
import {AppHeaderIcon} from "../../components/AppHeaderIcon";



export const UserListScreenProfile = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    const [loaded,setLoaded] = useState(true);
    const [loadedFC,setLoadedFC] = useState(true);
    const ReloadListAdres = async ()=>{

        const Results = await dispatch(loadAdressUser(idUserSystem,[],{cityId:CityIds}));
        setLoaded(false);
        //setLoadedFC(false);
      //  console.log('Обновили адреса');
    };


    useEffect(() => {
        // Put Your Code Here Which You Want To Refresh or Reload on Coming Back to This Screen.
        if('reloads' in route.params){
            setLoaded(true);
            ReloadListAdres()
        }else{}
        //console.log('Обновляемся ',navigation.state.params);
    }, [route.params]);

    if(loadedFC) {
        useEffect(()=> {
                ReloadListAdres()
            }
            ,[dispatch]);
    }else{

    }

    // if(route.params.reload === true){
    //     ReloadListAdres();
    // }else{
    //
    // }

    const Loadings = ()=>{
        if(loaded){
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <ActivityIndicator color={THEME.MAIN_COLOR} />
                    <Text>Обновление...</Text>
                </SafeAreaView>
            )
        }else{
            return(
                <UserListScreenProfileAwait navigation={navigation} route={route}/>
            )
        }
    };

    return(
        <Loadings />
    )

};


export const UserListScreenProfileAwait = ({ navigation, route }) => {
        const dispatch = useDispatch()
        const postId = route.params.postId;
        // const basket = useSelector(state => state.post.basket);


        // const product = useSelector(state =>
        //     state.post.allPosts.find(p => p.id === postId)
        // )

        const product = route.params.productData;
        const AdresList = useSelector(state => state.post.adressUser.listAddress);
        const AdresListNoDelivery = useSelector(state => state.post.adressUser.ListAddressNoDelivery);

        const CityIds = useSelector(state=>state.post.cityData.cityId);
        const idUserSystem = useSelector(state=>state.post.AuthUserData.id);


        //console.log(AdresList);

        const ListAdresForRender = Object.keys(AdresList).map(
            (id,n)=>{
                return AdresList[id]
            }
        );

        const ListAdresNoDeliveryForRender = Object.keys(AdresListNoDelivery).map(
            (id,n)=>{
                return AdresListNoDelivery[id]
            }
        );



        // const toggleHandler = useCallback(() => {
        //     dispatch(toogleBooked(product))
        // }, [dispatch, product])
        //
        // useEffect(() => {
        //     navigation.setParams({ toggleHandler })
        // }, [toggleHandler]);


        const RenderAdresList = ({dataAdres,onPress,statusDelivery = false, removeHandler})=>{
            // console.log(dataAdres);
            return (
                <OneAdressProfile dataAdress={dataAdres} onPress={onPress} statusDelivery={statusDelivery} removeHandler={removeHandler}/>
            )
        };

        if (!product) {
            return null
        }


        const DataScreen = ()=>(
            <View key={"Info_Adress_"+product.id}>

            </View>
        );

        const loadAdressUser = useSelector(state => state.post.loadAdressUser);

        if(!loadAdressUser){
            return(
                <View style={styles.center}>
                    <ActivityIndicator color={THEME.MAIN_COLOR} />
                </View>
            );
        }

        const handlPressOneAdres = (idAdres)=>{
            //console.log('Нажали на адрес - ',idAdres);
            // CheckBasketOffCategoryReload(dispatch,basket,idAdres,true);
            dispatch(setSelectAdres(idAdres));
            // DB.setAdresData( idAdres.id, idAdres);
            navigation.navigate('Main');
        };

        const ButtonNewAdres = ()=>{
            return(
                <TouchableOpacity
                    onPress={()=>{
                        //console.log('Нажали добавить адрес');
                        onPressNewAdresHandler();
                    }}
                >
                    <View style={styles.newAdresButton}><Text style={styles.newAdresText}>Добавить новый адрес</Text><Feather name="plus" size={24} color={THEME.MAIN_COLOR} /></View>
                </TouchableOpacity>
            )
        };

        const onPressNewAdresHandler = product => {
            //console.log('Открываем товар');
            navigation.navigate('NewAdres', {
                title: 'Добавление адреса',
                productData:[]
            });
        };

        const removeHandler = (a,b) => {
              //console.log('ебаный рот');
            // Alert.alert(
            //     'Удаление адреса',
            //     ''+b+'',
            //     [
            //         {
            //             text: 'Отмена',
            //             style: 'cancel'
            //         },
            //         {
            //             text: 'Удалить',
            //             style: 'destructive',
            //             onPress() {
            //                 // navigation.navigate('Main')
            //                 DelitedAdresAdres(a)
            //             }
            //         }
            //     ],
            //     { cancelable: false }
            // )
            let del=confirm('Удалить адрес?')
            if(del){
                DelitedAdresAdres(a)
            }
        };

        const DelitedAdresAdres = async (a)=>{
           // console.log(a,CityIds,idUserSystem);
            const ResultSave = await ServerDB.deliteAdresServer(a,CityIds,idUserSystem);
            // Alert.alert('Все в порядке, сохраняем');
            if(ResultSave){
                ReloadListAdres();
            }else{
                Alert.alert('Ошибка','К сожалению не удалось удалить адрес, попробуйте через пару минут!');
            }
        };

        const ReloadListAdres = async (CityIds)=>{
            // const Results = await loadAdressUser(idUserSystem,[],{cityId:CityIds});
            navigation.navigate('AdresListProfile',{reloads:true});
            return true;
        };


        return (
            <ScrollView style={styles.BodyView}>
                {
                    ListAdresForRender.length >0 || ListAdresNoDeliveryForRender.length > 0 ?

                <><FlatList
                    data={ListAdresForRender}
                    keyExtractor={item => "Blocks_Adress_"+item.id}
                    renderItem={
                        ({item})=>(
                            <RenderAdresList dataAdres={item} onPress={handlPressOneAdres} statusDelivery={true} removeHandler={removeHandler}/>
                        )
                    }>
                </FlatList>
                <FlatList
                    data={ListAdresNoDeliveryForRender}
                    keyExtractor={item => "Blocks_Adress_"+item.id}
                    renderItem={
                        ({item})=>(
                            <RenderAdresList dataAdres={item} statusDelivery={false} removeHandler={removeHandler}/>
                        )
                    }>
                </FlatList></>
                    :
                        <ButtonNewAdres />
                }
            </ScrollView>
        )
    };

UserListScreenProfile.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;

    return {
        headerTitle: 'Ваши адреса',
        headerLeft: (
            <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                <Item
                    title='Toggle Drawer'
                    iconName='arrow-back'
                    color={"#333333"}
                    onPress={() => navigation.goBack()}
                />
            </HeaderButtons>
        )
    }
};

const styles = StyleSheet.create({
    newAdresButton:{
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center',
        width:"100%",
        // backgroundColor:"#333333",
        paddingHorizontal:"3%",
        paddingVertical:"4%"
    },
    newAdresText:{
        fontFamily:"open-bold",
        fontSize:18,
        color:THEME.MAIN_COLOR,
    },
    BodyView:{
        height:Dimensions.get('window').height,
        overflow:'hidden'
    },
    centeredViewWindowTop:{
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
    }
});
