import React , {useEffect,useState}from 'react';
import {Image, StyleSheet, Text, View} from "react-native";
import {Group} from "../../../templateComponents/Group/GroupMain";
import {ModyficateBlock} from "../../Catalog/Product/Modyficate/ModyficateBlock";
import {GetPriceProductBasketDiscount} from "../getPriceProductBasket";
import {ButtonInCart} from "../../Catalog/ButtonInCart";
import {ConditionsPromokod} from "../PromokodeModal/ConditionsPromokod";
import {useSelector} from "react-redux";
import {THEME} from "../../../theme";

export const InformerTop = ()=>{

    const ErrorInfo = useSelector(state=>state.post.promokodeErrorInfo.errorProduct);

    //console.log(ErrorInfo);

    if(!ErrorInfo){return false;}

    if(ErrorInfo.status === true){
        return (
            <View style={styles.wrapper}>
                <View style={styles.Between}><Text style={styles.TextBorderInfo}>{ErrorInfo.message}</Text></View>
            </View>
        )
    }else{
        return false;
    }
};

const styles = StyleSheet.create({
    wrapper:{
        paddingVertical: 5,
    },
    Between:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        paddingVertical:2,
        paddingHorizontal:"3%",
    },
    TextBorderInfo:{
        borderWidth: 0,
        paddingVertical:"3%",
        paddingHorizontal: "2%",
        textAlign: "center",
        backgroundColor:THEME.MAIN_COLOR,
        color:"#FFFFFF",
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        borderRadius:10
    }
});