import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    FlatList,
} from 'react-native'

export const ModyficateBlock = ({ ModyficateProduct=[],blockStyle={} ,styleBlock={},styleImg={}, idProduct = null}) => {
    //console.log(ModyficateProduct);

    const OneModifcate = ({name,img})=>{
        return (
            <View style={[styles.imgModyficateBlock,styleBlock]}>
                <Image source={{uri:"https:"+img}} style={[styles.img,styleImg]} />
            </View>
        );
    };

    const returnBlock = ()=>{
        if(ModyficateProduct.length){
            return (
                <View style={[styles.blocks, blockStyle]}>
                    <FlatList
                        horizontal
                        data={ModyficateProduct}
                        renderItem={({item})=> <OneModifcate name={item.mod} img={item.mod_img} />}
                        keyExtractor={(item) => "ModificateProducts_"+idProduct+"_"+item.mod}
                    />
                </View>
            )
        }else{
            return (null)
        }
    };

    return returnBlock()
};

const styles = StyleSheet.create({
    blocks:{
        position: 'absolute', top: 10, left: 0, right: 0, bottom: 0, justifyContent: 'flex-start', alignItems: 'flex-start',width:"50%"},
    imgModyficateBlock:{
        marginHorizontal:4,
        marginTop:7
    },
    img:{
        width:21,
        height:21
    }
});
