import {StyleSheet, Text, View} from "react-native";
import {Ionicons} from "@expo/vector-icons";
import React from "react";

export const Group = (props)=>{
    let Options = {...{
        title:false, bottomLabel:false
        },
        ...props
    };

    let Shadows = styles.shadow;

    if(props.shadow === false){
        Shadows = [styles.shadowOff,{marginBottom:props.marginBottom}];
    }else{
        Shadows = [styles.shadow,{marginBottom:props.marginBottom}];
    }


    return(
        <View style={[styles.View,Shadows,props.styles]}>
            {Options.title !== false ? <TopLabel text={Options.title} /> : false}
            {props.children}
            <BottomLabel text={Options.bottomLabel} />
        </View>
    );
};

const BottomLabel = ({text})=>{
    if(text === false || text === null){
        return false;
    }else{
        return(<View><Text>{text}</Text></View>);
    }
};

const TopLabel = ({text})=>{
    if(text === false || text === null){
        return false;
    }else{
        return(<Text style={[styles.h2]}>{text}</Text>);
    }
};

const styles = StyleSheet.create({
    View:{
        paddingHorizontal:"2%",
        paddingVertical:"2%",
        fontSize:24,
        color:"#333333",
        textAlign: "left",
        backgroundColor:"#ffffff",
    },
    h2:{
        fontSize:18,
        textTransform:"uppercase"
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 3,
        marginBottom:10
    },
    shadowOff:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 0,
        marginBottom:10
    }
});