import React, { useEffect, useCallback } from 'react'
import { WebView } from 'react-native-webview';
import { API_KEY, URL_API, CITY_DATA,URL_SITE } from './../store/types';
import {useSelector} from "react-redux";
import {HeaderButtons, Item} from "react-navigation-header-buttons";
import {AppHeaderIcon} from "../components/AppHeaderIcon";
import {Platform} from "react-native";

export const InfoDeliveryScreen = ({ navigation, route }) => {

    const CityData = useSelector(state => state.post.cityData);

    const URL = URL_SITE+"api/getPageInfo&apiKey="+API_KEY+"&getPageUrl=dostavka&CityIdSystem="+CityData.cityId+"&fontSizeVh=2.1";

    return  Platform.OS === 'web' ?  (

            <iframe
                src={URL}
                height={'100%'}
                width={'100%'}
                style={{border:'none'}}
             />

    ) :  (

        <WebView  source={{
            uri: URL ,
            headers:{
                'user-agent':'Mozilla/5.0 (iPhone; CPU iPhone OS 13_2_3 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/13.0.3 Mobile/15E148 Safari/604.1'
            }
        }} />

    )
};


InfoDeliveryScreen.navigationOptions = ({ navigation }) => ({
    headerTitle: 'Доставка и оплата'
});