export const CheckModifyProductAccordance = (modify1,modify2)=>{
    // console.log('Что пришло в сравнение модификаторов ',modify1,modify2);
    let StatusAddNewProduct = true; //Статус сравнения модификаторов если true - они одинаковые
    let numberOldProduct = 0;
    let statuSimilar = []; //Массив со статусами схожести каждого модификатора старого товара

    modify1.forEach((dataOldModify,numOldModify)=>{
        //Запустили цикл прохождения по модификаторам в товаре из сессии
        statuSimilar[numOldModify] = false; //Устанавливаем статус дефолт похожести модификаторов проверяемого товара и нового товара, false - не похожи, true - похожи

        modify2.forEach((dataNewModify,numNewModify)=>{
            //Проходим циклом по новым модификаторам и ищем похожие со старыми
            if (dataOldModify['idModify'] == dataNewModify['idModify']) {
                //Есть похожий, значит утсанавливаем ему значение как true
                statuSimilar[numOldModify] = true;
                // console.log('Модификаторы совпали, значит они одинаковые, что проверяли',dataOldModify,dataNewModify);
            }
        });
    });

    let allStatus = true; //Статус схожести всех проверенных модификаторов, по дефолту true - похожи, значит товар старый

    if(modify1.length !== modify2.length){
        allStatus = false; //Если кол-во модифкаторов не совпадает, то ставим false - товары не равны
    }

    statuSimilar.forEach((status,n)=>{
        if(status == false){
            allStatus = false;// не совпадает модификатор, а знеачит товары не похожи, то есть это новый товар
            // console.log('Не совпадает, значит должны поставить false');
        }else{
            // console.log('Совпадает, ничего не делаем');
        }
    });

    // console.log('После сравнения модификаторов, статус allStatus=',allStatus);

    if(allStatus == false ){ //Если все модификаторы одинаковы, значит устанавливаем добавление нового товара в false, то есть товар новый
        StatusAddNewProduct = false; //устанавливаем общий статус как добавить новый товар
        numberOldProduct = 0; //Старый товар, устанавливае номер ключа массива, для того чтобы увеличить или уменьшить кол-во товаров
    }

    return StatusAddNewProduct;
}