import React, { useState } from "react";
import {View, StyleSheet, Pressable, Text, Modal, Dimensions, ActivityIndicator} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {
    setListDataDelievryList,
    setModalStatus,
    setPreorderData,
    setPreorderDataStatus
} from "../../../store/actions/post";
import {THEME} from "../../../theme";
import {ServerDB} from "../../../serverDb";
import { Ionicons } from '@expo/vector-icons';
import { FontAwesome5 } from '@expo/vector-icons';
import {ContentModalDateDelivery} from "./ContentModalDataDelivery";

export const SelectDataDelivery = ({fontSize = 16, keys = null}) => {

    const dispatch = useDispatch();
    const StatusModalDelivery = useSelector(state=>state.post.modalStateStatus.MODAL_DATE_DELIVERY);

    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);

    const WorkTimePointsCity = useSelector(state=>state.post.AuthUserData.WorkTimePointsCity);



    const loadingDataListServer = async ()=>{
        await dispatch(setPreorderDataStatus(false));
        let ListDateServerResult = await ServerDB.getDateList();
        //console.log(ListDateServerResult);
        await dispatch(setListDataDelievryList(ListDateServerResult));
    };



    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };


    const getTitleMountRus = (nMounts)=>{
      switch (nMounts) {
          case "01": return 'Января';
          case "02": return 'Февраля';
          case "03": return 'Марта';
          case "04": return 'Апреля';
          case "05": return 'Мая';
          case "06": return 'Июня';
          case "07": return 'Июля';
          case "08": return 'Августа';
          case "09": return 'Сентября';
          case "10": return 'Октября';
          case "11": return 'Ноября';
          case "12": return 'Декабря';
          default: return nMounts;
      }
    };

    const GetModalOption = ({})=>{
        return (
                <Modal
                    animationType="none"
                    transparent={true}
                    visible={StatusModalDelivery}
                    onShow={()=> {
                        // Alert.alert("Модальное окно было открыто");

                    }}
                    onRequestClose={() => {
                        //setModalVisibleS(!StatusModal);
                        handleSetStatusModal("MODAL_DATE_DELIVERY",false);
                    }}
                    key={"Modal_"+keys}
                >
                    <ContentModalDateDelivery />
                </Modal>
        )
    };

    const getNowTypeDelivery = ()=>{
        if(DateValueShow === 0){
            if(WorkTimePointsCity.resultWorkTime === false){
                return 'Доставить после открытия'
            }else{
                return 'Как можно скорее';
            }
        }else{
            let TitlesDef = DateValueShow.split('-');
            return TitlesDef[2]+' '+getTitleMountRus(TitlesDef[1])+' '+TitlesDef[0];
        }
    };

    const GetSelectedValue = ()=>{
        return (
            <Pressable
                onPress={()=>{
                    //setModalVisibleS(true);
                    handleSetStatusModal("MODAL_DATE_DELIVERY",true);
                    loadingDataListServer();
                }}
                style={{
                    width:"100%"
                }}
            >
                <GetModalOption />
                <Text style={[styles.defaultValue,{fontSize:fontSize}]}>
                    {/*{item[selectedValueKey].title}*/}
                    {getNowTypeDelivery()}
                </Text>
            </Pressable>
        )
    };


    return (
        <View style={styles.container}>
            <GetSelectedValue />
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:30,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"3.1%"
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
    },
    modalContent:{
        width:"100%",
        height:Dimensions.get('window').height,
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:"100%",
        marginVertical: "5%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "5%",
        paddingHorizontal:"5%",
        borderRadius: 10
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});