import React, {useEffect, useState} from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView, ScrollView
} from "react-native";
import {THEME} from "../../../theme";
import {MaterialCommunityIcons, MaterialIcons} from "@expo/vector-icons";
// import MultiSlider from "@ptomasroos/react-native-multi-slider";
import {useDispatch, useSelector} from "react-redux";
import {getNoun} from "../../../templateComponents/Noun/GetNoun";
import {setPointsSaleUpdate} from "../../../store/actions/post";
import {Slider} from '@miblanchard/react-native-slider'

export const PointsBlock = ()=>{

    const dispatch = useDispatch();

    const sumBasket = useSelector(state => state.post.basketSum);

    const allPointsUser = useSelector(state => state.post.AuthUserData.UserDataServer.points);
    const CityIdActive = useSelector(state=>state.post.cityData.cityId);
    const PercentPaymentBonus = useSelector(state=>state.post.AuthUserData.BasketServerSettings[CityIdActive].percentPaymentBonus);

    const [salePoints,setSalePoints] = useState(0);

    const allPointsSaleGlobal = useSelector(state => state.post.allPointsSale);
    const BonusPointsText = useSelector(state => state.post.bonusPointsText);
    const BonusPointsActive = useSelector(state => state.post.bonusPointsActive);

    const [countPointsMaxPay,setCountPointsMaxPay] = useState(0);

    const handleSetPointsSaleUpdate = (count)=>{
        dispatch(setPointsSaleUpdate(count));
    };


    const getMaxSumPayBonusPoint = (SumOrder = 0, bonusPoints = 0)=>{
        let NewPercentPaymentBonus = PercentPaymentBonus;
        if(PercentPaymentBonus === undefined){
            NewPercentPaymentBonus = 100;
        }
        //console.log('Переданы данные для просчета суммы ',SumOrder,bonusPoints,NewPercentPaymentBonus);
        // let maxSumPay = Math.round(SumOrder - (SumOrder*(NewPercentPaymentBonus/100)));
        let maxSumPay = Math.round((SumOrder*(NewPercentPaymentBonus/100))); // Замена на верную формулу получения % сколько списать можно баллами
        if(maxSumPay > bonusPoints){
            if(isNaN(bonusPoints)){bonusPoints = 0}
            setCountPointsMaxPay(bonusPoints);
        }else{
            if(isNaN(bonusPoints)){bonusPoints = 0}
            if(isNaN(maxSumPay)){maxSumPay = bonusPoints}
            setCountPointsMaxPay(maxSumPay);
        }
        //console.log(maxSumPay);
    }

    if(BonusPointsActive === false){
        return false;
    }

    useEffect(()=>{
        getMaxSumPayBonusPoint(sumBasket,allPointsUser);
    },[]);

    const Points =
        allPointsUser > 0 & countPointsMaxPay > 0 ?
            <View
                style={{flexDirection:"column",justifyContent:"space-between",alignItems:"center",width:"100%"}}
                onPress={
                    ()=>{}
                }
            >
                <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"100%"}}>
                    <Text style={styles.textIconCenter}>Всего на счету: <Text style={styles.colorOrangeText}>{allPointsUser}</Text> {getNoun(allPointsUser,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                </View>
                <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"100%"}}>
                    <Text style={styles.textIconCenter}>Доступно для списания: <Text style={styles.colorOrangeText}>{countPointsMaxPay}</Text> {getNoun(countPointsMaxPay,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                </View>
                {/*<MultiSlider*/}
                {/*    //sliderLength={176} // Длинна общего блока ползунка*/}
                {/*    // touchDimensions={{height: 50,width: 50,borderRadius: 15,slipDisplacement: 0}}*/}
                {/*    customMarker={()=>(<View style={styles.Marker}></View>)}*/}
                {/*    onValuesChangeFinish={()=>{}}*/}
                {/*    onValuesChange={(val)=>{*/}
                {/*        //console.log('Изменили значение слайдера таймов', val);*/}
                {/*        handleSetPointsSaleUpdate(val[0]);*/}
                {/*    }}*/}
                {/*    isMarkersSeparated={false}*/}
                {/*    min={0}*/}
                {/*    max={countPointsMaxPay || 1}*/}
                {/*    step={1}*/}
                {/*    selectedStyle={{backgroundColor:THEME.MAIN_COLOR}}*/}
                {/*    markerSize={10}*/}
                {/*    markerOffsetX={1}*/}
                {/*    values={[allPointsSaleGlobal]}*/}
                {/*    // showSteps={true}*/}

                {/*    // onValuesChangeStart={disableScroll}*/}
                {/*    // onValuesChangeFinish={enableScroll}*/}
                {/*/>*/}
                <Slider
                    value={[allPointsSaleGlobal]}
                    minimumValue={0}
                    maximumValue={countPointsMaxPay || 1}
                    step={1}
                    thumbTouchSize={{width:'100%',height:10}}
                    onValueChange={(val)=>{
                        //console.log('Изменили значение слайдера таймов', val);
                        handleSetPointsSaleUpdate(val[0]);
                    }}
                    thumbStyle={{backgroundColor:THEME.MAIN_COLOR}}
                    containerStyle={{width:"100%"}}
                    minimumTrackTintColor={THEME.MAIN_COLOR}
                    // onSlidingStart={disableScroll}
                    // onSlidingComplete={enableScroll}


                />
                <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"100%"}}>
                    <Text style={styles.textIconCenter}>Будет списано: <Text style={styles.colorOrangeText}>{allPointsSaleGlobal}</Text> {getNoun(allPointsSaleGlobal,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                </View>
            </View>
            :
            <View
                style={{flexDirection:"column",justifyContent:"space-between",alignItems:"center",width:"100%"}}
                onPress={
                    ()=>{}
                }>
                <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"100%"}}>
                    <Text style={styles.textIconCenter}>Всего на счету: <Text style={styles.colorOrangeText}>{allPointsUser}</Text> {getNoun(allPointsUser,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                </View>
                <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"100%"}}>
                    <Text style={styles.textIconCenter}>Доступно для списания: <Text style={styles.colorOrangeText}>{countPointsMaxPay}</Text> {getNoun(countPointsMaxPay,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                </View>
            </View>
    ;

    return (
        <View style={[styles.BoxWhiteBlock,styles.shadow]}>
            {Points}
        </View>
    )
};

const styles = StyleSheet.create({
    Marker:{
        width:20,
        height:20,
        backgroundColor: THEME.MAIN_COLOR,
        borderRadius:25,
        zIndex:10,
    },

    BoxWhiteBlock:{
        backgroundColor:"#ffffff",
        borderRadius:10,
        paddingHorizontal:10,
        paddingVertical:10,
    },
    AdressStr:{
        width:"100%",
        flexDirection:'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingHorizontal:2,
        paddingVertical:10,
        marginVertical:0,
        borderColor:"#d6d6d6",
        borderTopWidth:0,
        // backgroundColor: "#ff7300"
    },
    textIconCenter:{
        flexDirection:'row',
        // width:"100%",
        maxWidth:"84%",
        marginLeft:5,
        alignItems:'center',
        justifyContent:'space-between',
        fontSize:12,
        textAlign:"left",
        // backgroundColor:"#ff7300"
    },
    arrowRight:{
        width:"5%",
        marginLeft:0,
        paddingTop:Platform.OS === 'ios' ? 0 : 2
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.22,
        shadowRadius: 40,
        elevation: 3,
        marginBottom:0
    },
    colorOrangeText:{
        fontSize: 14,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        color:THEME.MAIN_COLOR
    }
});