import React from 'react'
import {View, StyleSheet, FlatList, Text, ScrollView, SafeAreaView, Platform, TextInput} from 'react-native'
import { OneProduct } from '../Product/ProductCatd/OneProduct'
import {OneFeatures} from "./OneFeatures";

export const FeacturesList = ({ data = [], onOpen }) => {
    if (!data.length) {
        //console.log(data);
        return (
            <View style={styles.wrapper}>
                <Text style={styles.noItems}></Text>
            </View>
        )
    }

//    console.log(data);
    //console.log('До или после',data);

    let statusShow = false;

    data.forEach((d,n)=>{
       if(d.text != ''){
           //console.log('дп дадпдадпд ');
        statusShow = true;
       }
    });

    if(statusShow === true) {
        return (
            <SafeAreaView style={styles.wrapper}>
                <FlatList
                    data={data}
                    keyExtractor={feature => "features_" + feature.features.id}
                    listKey={({feature, index}) => index.toString()}
                    renderItem={({item}) => <OneFeatures feature={item}/>}
                />
            </SafeAreaView>
        )
    }else{
        return false;
    }
}

const styles = StyleSheet.create({
    wrapper: {
        paddingVertical: Platform.OS === "android" ? 0 : 0,
        paddingHorizontal:10,
        width:'92vw',
        alignSelf:"center"
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 18
    }
})
