import React, {useCallback, useEffect, useRef,useState} from 'react';
import {
    View,
    StyleSheet,
    Image,
    Text,
    FlatList,
    SafeAreaView,
    ScrollView,
    Pressable,
    LayoutAnimation, Platform, TouchableOpacity, UIManager, TextInput, KeyboardAvoidingView
} from 'react-native';
import {useDispatch, useSelector} from "react-redux";
import {THEME} from "../../theme";

export const  ModulInformerWorkTimeInBasket = ({ navigation, route }) => {
    const WorkTimePointsCity = useSelector(state=>state.post.AuthUserData.WorkTimePointsCity);

    if(WorkTimePointsCity.resultWorkTime === false){
        return(
            <View style={styles.centerContentBlock}>
                <Text style={styles.titleAlert}>{WorkTimePointsCity.textInformer}</Text>
                {WorkTimePointsCity.pre_order === true ? <Text style={styles.textPreorder}>{WorkTimePointsCity.textInformerPreorder}</Text> : false}
            </View>
        )
    }else{
        return false;
    }
};

const styles = StyleSheet.create({
    centerContentBlock:{
        width:"100%",
        paddingVertical:20,
        paddingHorizontal:20,
        backgroundColor:THEME.MAIN_COLOR,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
        marginTop:20,
        marginBottom:20
    },
    titleAlert:{
        color:THEME.TEXT_COLOR,
        fontSize:16,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        textAlign:"center"
    },
    textPreorder:{
        marginTop:10,
        color:THEME.TEXT_COLOR,
        fontSize:15,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        textAlign:"center"
    }
});