export const getTypeDevice = (W,H,type=null)=>{
    let TypeDevice = true;
    if(W>=H){TypeDevice = false;}

    if(W>=750){TypeDevice = false;}

    if(type !== null){
        switch (type) {
            case "desktop_web": TypeDevice = false; break;
            case "mobile_web": TypeDevice = true; break;
        }
    }


    return TypeDevice;
}