import React from 'react';
import {StyleSheet, Text} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {THEME} from "../../../theme";

export const ConditionsPromokod = ({typeReturn = 1})=>{

    const promokodeErrorInfo = useSelector(state => state.post.promokodeErrorInfo);

    const getCHeckSum = ()=>{
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorSum.countSum>0){
            return (<Text style={styles.TextInfoMini}>Для активации промокода необходимо добавить товаров на сумму: <Text style={styles.BoldText}>{promokodeErrorInfo.errorSum.countSum} ₽</Text></Text>);
        }else{
            return false;
        }
    };


    const getCheckDiscountOtherProduct = ()=>{
        //console.log(promokodeErrorInfo);
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorProduct.status === true){
            return (<Text style={styles.TextInfoMini}>{promokodeErrorInfo.errorProduct.message}</Text>);
        }else{
            return false;

        }
    };


    let ReturnData = false;

    switch (typeReturn) {
        case 1:
            ReturnData = getCHeckSum();
            break;
        case 2:
            ReturnData = getCheckDiscountOtherProduct();
            break;
        default: ReturnData = false;
    }


    return (ReturnData);
};


const styles = StyleSheet.create({
    BoldText:{
    fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    TextInfoMini:{
        backgroundColor:THEME.INFO_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:5,
        marginVertical: 10
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    TextInfoMiniError:{
        backgroundColor:THEME.DANGER_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    titleChildBlock:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16,
        marginBottom:10
    },
});