import React, { useEffect, useCallback } from 'react'
import {useSelector, useDispatch} from "react-redux";
import {
    ActivityIndicator,
    Platform, Pressable,
    SafeAreaView,
    ScrollView,
    StyleSheet,
    Text,
    TouchableOpacity,
    View
} from "react-native";
import {Group} from "../../templateComponents/Group/GroupMain";
import {MaterialIcons} from "@expo/vector-icons";
import {loadAuthInfo, logoutApp, setActiveOrderScreenData} from "../../store/actions/post";
import {THEME} from "../../theme";

export const ProfileScreen = ({ navigation, route }) => {
    const CityLoadPhone = useSelector(state => state.post.cityLoadPhone);
    const AuthUserStatus = useSelector(state => state.post.AuthUserStatus);
    if (CityLoadPhone || (!AuthUserStatus)) {
        //console.log('Переводим на страницу');
        // navigation.navigate('Authorization');
        return (
            <View style={styles.center}>
                <Text style={{fontSize:14,textAlign:"center",marginBottom:20}}>Чтобы просматривать свой профиль, необходимо войти в аккаунт!</Text>
                <Pressable
                    style={styles.buttonCity}
                    color={THEME.MAIN_COLOR}
                    onPress={() => {
                        navigation.navigate('Authorization');
                    }}
                >
                    <Text style={{color:THEME.MAIN_COLOR,fontFamily:"open-bold",marginVertical:20}}>Авторизоваться</Text>
                </Pressable>
            </View>
        )
    } else {
        return (
            <GoodPages navigation={navigation} route={route}/>
        )
    }

}

export const GoodPages = ({ navigation, route }) => {
    const dispatch = useDispatch();
   // const CityData = useSelector(state => state.post.cityData);
    const UserData = useSelector(state=>state.post.AuthUserData.UserDataServer);
    const BonusPointsText = useSelector(state => state.post.bonusPointsText);
    const BonusPointsActive = useSelector(state => state.post.bonusPointsActive);



    // console.log(UserData);

    const AdresList = useSelector(state => state.post.adressUser.listAddress);
    const AdresListNoDelivery = useSelector(state => state.post.adressUser.ListAddressNoDelivery);

    const activeLinkDeletedProfile = useSelector(state => state.post.AuthUserData.activeLinkDeletedProfile);
      //console.log('activeLinkDeletedProfile = ',activeLinkDeletedProfile);

    if(AdresList === null || typeof AdresList !== 'object'){
        useEffect(() => {
            dispatch(loadAuthInfo());
        }, [dispatch]);
        return (
            <SafeAreaView style={styles.centeredViewWindowTop}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
            </SafeAreaView>
        )
    }


    const CountDeliveryAdres = Object.keys(AdresList).length+Object.keys(AdresListNoDelivery).length;

    const getPageAdresListHandl = product => {
            //console.log('Открываем товар');
            navigation.navigate('AdresListProfile', {
                title: 'Ваши адреса',
                productData:[]
            });
    }


    const getPageOrderListHandlE = order => {
        let statusReloadOrderScreen = dispatch(setActiveOrderScreenData(null,false));
            navigation.navigate('OrderListProfile', {
                title: 'Список заказов',
                productData:[],
                idOrderOpen:null
            });
    }

    const getPageDeletedProfileHandlE = order => {
        navigation.navigate('DeletedProfileScreen', {
            title: 'Удаление аккаунта',
            productData:[],
            idOrderOpen:null
        });
    }


    const GetProfileBlock = ()=>{
        return (
            <>
                <Group styles={{paddingHorizontal:"4%"}} shadow={false}>
                    {getListInfo(UserData.name !== '' ? <Text style={{fontFamily:"open-bold",fontSize:18}}>{UserData.name}</Text> : <Text style={{fontFamily:"open-bold"}}>Пользователь</Text>,
                        <View style={styles.ViewInView}>
                            <Text> </Text>
                            <View ></View>
                            {/*<View ><MaterialIcons name="mode-edit" size={24} color="black" /></View>*/}
                        </View>, true)
                    }
                    {BonusPointsActive ? getListInfo('Всего '+BonusPointsText.five,
                        <View style={styles.ViewInView}>
                            <Text> </Text>
                            <View >
                                <Text style={{color:THEME.MAIN_COLOR}}>{UserData.points}</Text>
                            </View>
                        </View>,true) : false}
                    {/*{getListInfo('Фамилия',UserData.last_name)}*/}

                {/*<Group>*/}
                {/*    {getListInfo('Дата рождения',UserData.birthday)}*/}
                {/*    {getListInfo('Email',UserData.email)}*/}
                {/*    {getListInfo('Телефон',UserData.phone)}*/}
                {/*</Group>*/}
                <TouchableOpacity onPress={()=>{
                    getPageAdresListHandl()
                }}>

                        {getListInfo('Адреса доставки',
                            <View style={styles.ViewInView}>
                                {/*<Text>{CountDeliveryAdres}</Text>*/}
                                <Text></Text>
                                <View ><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
                            </View>,true)}

                </TouchableOpacity>
                <TouchableOpacity onPress={()=>{
                    getPageOrderListHandlE()
                }}>

                        {getListInfo('Список заказов',
                            <View style={styles.ViewInView}>
                                <Text></Text>
                                <View ><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
                            </View>,true)}

                </TouchableOpacity>
                    <TouchableOpacity onPress={()=>{
                        dispatch(logoutApp());
                    }}>
                        {getListInfo('Выйти',
                            <View style={styles.ViewInView}>
                                <Text></Text>
                                <View ></View>
                            </View>,true)}
                    </TouchableOpacity>

                    {activeLinkDeletedProfile === true ? <>
                    <TouchableOpacity onPress={()=>{
                    }}>
                        {getListInfo('',
                            <View style={styles.ViewInView}>
                                <Text></Text>
                                <View ></View>
                            </View>,true)}
                    </TouchableOpacity>

                    <TouchableOpacity onPress={()=>{
                        getPageDeletedProfileHandlE()
                    }}>

                        {getListInfo('Удаление аккаунта',
                            <View style={styles.ViewInView}>
                                <Text></Text>
                                <View ><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
                            </View>,true)}

                    </TouchableOpacity>
                    </>
                        :
                        false
                        }
                </Group>
            </>
        );
    };

    const getListInfo = (title,value,type = false)=>{
        if(!type){
            return (
                <View style={styles.list}>
                    <Text style={styles.hTitleList}>{title}</Text>
                    <Text style={styles.hValueList}>{value}</Text>
                </View>
            )
        }else{
            return (
                <View style={styles.list}>
                    <View style={styles.hTitleList}><Text>{title}</Text></View>
                    <View style={styles.hValueList}>{value}</View>
                </View>
            )
        }
    };

    return (
        <ScrollView>
            <GetProfileBlock />
        </ScrollView>
    )
};


ProfileScreen.navigationOptions = ({ navigation, route }) => ({
    headerTitle: 'Ваш профиль'
});

const styles = StyleSheet.create({
    list:{
        flexDirection:'row',
        justifyContent:"flex-start",
        alignItems:'center',
        paddingVertical:5,
        paddingHorizontal:3,
        paddingTop:10,
        marginVertical:2,
        marginTop:10,
        borderTopWidth:0,
        borderColor:"#dddddd"
    },
    hTitleList:{
        fontSize:16,
        fontFamily:"open-bold",
        width:"50%"
    },
    hValueList:{
        fontSize: 16,
        fontFamily: "open-regular",
        width:"50%",
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center'
    },
    ViewInView:{
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        width:"100%",
        paddingVertical: Platform.OS === "IOS" ? "3%" : 0,
        // paddingHorizontal: "4%"
    },
    Absolutes:{
        // position:"absolute",
        // top:0,
        // right:0,
    },
    center: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        paddingHorizontal:"5%",
        textAlign:"center"
    },
});