import React, {useCallback} from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
// import {addProductBasket} from "../../store/actions/post";
// import {GetAllPriceBasket} from "../Basket/GetAllPriceBasket";

export const ButtonOpenProductModify = ({ Product, titleButton = 'Выбрать', SizeButton = 'normal', onOpen = ()=>{}, onOpenProduct = ()=>{}}) => {

    const dispatch = useDispatch();
    const basketProduct = useSelector(state => state.post.basket);
    let inCart = false;
    let countInCart = 0;


    const reloadBasketData = (idProduct,TypeProduct)=>{
        basketProduct.forEach((product)=>{
            //console.log(product.id, idProduct);
            if(product.id === idProduct){
                product.type.forEach((typeId,numb)=>{
                    if(typeId.type_id === TypeProduct){
                        //console.log('Совпанеие есть, товар есть в корзине');
                        inCart = true;
                        countInCart = typeId.count;
                    }else{
                        //console.log(typeId, TypeProduct);
                        //console.log('Товара по типу нет в корзине');
                    }
                });
            }else{
                //console.log('Товара нет в корзине');
            }
        });
    }

    const returnButtonIncart = ()=>{
        reloadBasketData(Product.id,Product.type_product);
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={
                    // () => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category)
                    () => {onOpen(Product);onOpenProduct(Product)}
                }>
                    <View style={
                        SizeButton == 'normal' ? styles.ButtonBlock : styles.ButtonBlockMini}>
                        <Text style={SizeButton == 'normal' ?  styles.buttonText : styles.buttonTextMini }>{titleButton}</Text>
                    </View>
                </TouchableOpacity>
            )
    };

    return returnButtonIncart()
};

const styles = StyleSheet.create({
    BlockButtonCount:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        // width: 100
    },
    CountProductInCart:{
        color: "#252622",
        paddingHorizontal: 10
    },
    CountProductInCartText:{
        color: "#252622",
    },
    ButtonBlock: {
        paddingHorizontal:13,
        paddingVertical:5,
        paddingBottom:7,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:10
    },
    ButtonBlockMini: {
        paddingHorizontal:10,
        paddingVertical:2,
        paddingBottom:4,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:7
    },
    buttonText:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:14,
        color:THEME.TEXT_COLOR
    },
    buttonTextMini:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:10,
        color:THEME.TEXT_COLOR
    }
});
