import React from 'react';

export const CheckWorkTimeAffiliateNew = (StartHh,StartMh,StopHh,StopMh)=>{


    const getNumberPlusNull = (str)=>{
        let returns = 0;

        if(str*1 < 9){
            returns = '0'+str;
        }else{
            returns = str;
        }
        return returns;
    };

    //console.log(Start,Stop);

    const getParseDateStr = (HourH,HourM)=>{
        const d = new Date();
        // let Now = new Date(d.getFullYear(),(d.getMonth()+1),d.getDate(),HourH,HourM,0); //Не правильно считалось время из за +1 к месяцу, время открытия точки (режим работы) до часу ночи сл дня, и время считалось в конце месяца на 1 месяц больше
        let Now = new Date(d.getFullYear(),(d.getMonth()),d.getDate(),HourH,HourM,0);
       //console.log(Now);
        return Now.getTime();
    };


    const getParseDateStrPlus = (dates,HourH,HourM)=>{
        const rd = dates;
        // let Now = new Date(rd.getFullYear(),(rd.getMonth()+1),rd.getDate(),HourH,HourM,0); //Не правильно считалось время из за +1 к месяцу, время открытия точки (режим работы) до часу ночи сл дня, и время считалось в конце месяца на 1 месяц больше
        let Now = new Date(rd.getFullYear(),(rd.getMonth()),rd.getDate(),HourH,HourM,0);
        return Now.getTime();
    };

    const getdayDate = (a = false)=>{
        let dd = new Date();
        //console.log('сегодня', d.getDate());
        //создаем новую дату из дня старой вычитая 1 день
        if(a === false) {
            dd.setDate(dd.getDate() - 1);
        }else{
            dd.setDate(dd.getDate() + 1);
        }
        //console.log('Установили дату на '+d.getDate());
        return dd;
    };

    const getTimeWork = (StartH,StartM,StopH,StopM)=>{
        //Проверка времени работы точек
        //Ваши входные данные
        //Начало работы
        let start_work = StartH;
        //Конец работы
        let end_work = StopH;
        //Текущее время
        let currentTime = new Date();

        //текущее время с датой, даже если оно у вас 12:00 все равно имеет дату
        let currentDateTime = getParseDateStr(currentTime.getHours(),currentTime.getMinutes());

        //console.log('Текущее время в мил сек ',currentDateTime);
       // console.log('StartH,StartM,StopH,StopM ',StartH,StartM,StopH,StopM);

        //Дата и время во сколько мы закрылись вчера
        let previousDayEnd;

        //Дата и время во сколько мы открылись сегодня
        let startDateTime;

        //Дата и время во сколько мы закрылись сегодня
        let endDateTime;

        let stopDateTime;

        startDateTime = getParseDateStr(StartH,StartM);
        stopDateTime = getParseDateStr(StopH,StopM);

        if (startDateTime <= stopDateTime){

            let yesterday = getdayDate(false);

            endDateTime = stopDateTime;
            previousDayEnd = getParseDateStrPlus(yesterday,StopH,StopM);

           //console.log('Вчерашняя дата');
        }
        else{

            let tomorrow = getdayDate(true);

            endDateTime = getParseDateStrPlus(tomorrow,StopH,StopM);
            previousDayEnd = getParseDateStr(StopH,StopM);
           //console.log('Завтрашняя дата');
        }

       //console.log('Текущая дата ',currentTime, 'что просчитывается в функции',currentTime.getHours(),currentTime.getMinutes());
       //console.log('currentDateTime: '+currentDateTime,'previousDayEnd: '+previousDayEnd,'startDateTime: '+startDateTime,'endDateTime: '+endDateTime);

        //проверить полученные результаты
        // echo "Мы закрылись в : " .  date("Y-m-d H:i:s", previousDayEnd). "\n";
        // echo "Открытие : " . date("Y-m-d H:i:s", startDateTime) . "\n";
        // echo "Закрытие  : " .  date("Y-m-d H:i:s", endDateTime). "\n";

        if (currentDateTime >= startDateTime && currentDateTime <= endDateTime) {
            // echo "Сейчас рабочее время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('Сейчас рабочее время 1');
            return true;
        }
        else if(currentDateTime < startDateTime && currentDateTime < previousDayEnd ){
            // echo "Сейчас рабочее время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('Сейчас рабочее время 2');
            return true;
        }
        else {
            // echo "Мы закрыты в это время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
            // console.log('Сейчас Мы закрыты');
            return false;
        }

    };


    return getTimeWork(StartHh,StartMh,StopHh,StopMh);

};