import React, {useCallback, useEffect, useRef} from 'react';
import {Image, StyleSheet, Text, View} from "react-native";
import {GetPriceProductBasket} from "../Basket/getPriceProductBasket";
import {ButtonInCartTopping} from "../Basket/ButtonInCartTopping";
import {ModyficateBlock} from "../Catalog/Product/Modyficate/ModyficateBlock";
import {THEME} from "../../theme";
import {Price} from "../Catalog/Product/ProductCatd/Price";
import {URL_SITE} from "../../store/types";

export const OneToppingCartBasket = ({productTopping = [], product = null, navigation }) => {

   // console.log(" --- ",product);
    if(product === null){
        return "";

    }

    return (
        <View style={styles.productBlock}>
            <View style={[styles.textWrap,styles.shadow]}>
                <Image style={styles.image} source={{ uri: URL_SITE+product.img }} />
                <View style={styles.TextBlockProduct}>
                    <View style={styles.BlockTitle}>
                        <Text style={styles.title}>
                            {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                        </Text>
                        {/*<Text style={styles.weight}>{product.weight}{product.weight_type}</Text>*/}
                        <Text>
                            {/*<GetPriceProductBasket ProductData={product} countProduct={typeProducts.count}/> ₽*/}
                        </Text>
                    </View>
                    <View style={styles.ViewPrice}>
                        {/*<Price ProductData={product}/>*/}
                        <Text style={styles.priceText}>{product.price} ₽</Text>
                        <ButtonInCartTopping Product={product}/>
                    </View>
                </View>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden',
        width:165,
        height:"100%",
        paddingHorizontal: 1,
        marginHorizontal:7,
        marginVertical: 0,
        borderRadius: 10
    },
    image: {
        width: THEME.WIDTH_DOP_PRODUCT_ONE_PRODUCT_IN_CART_IMG,
        height: THEME.HEIGHT_DOP_PRODUCT_ONE_PRODUCT_IN_CART_IMG,
        marginRight:0,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"100%",
        paddingRight:0,
        flexDirection:"column",
        justifyContent:"space-between",
        alignItems:"flex-start",
        flexWrap:"wrap",
    },
    HeaderBlocks:{
        marginBottom:10,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"flex-start"
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"flex-start",
        alignItems:"flex-start",
        width:"70%",
        marginTop:10
    },
    textWrap: {
        flex:1,
        flexDirection:"column",
        justifyContent:"space-between",
        backgroundColor: '#ffffff',
        paddingVertical: 10,
        paddingHorizontal: 10,
        alignItems: 'center',
        width: '100%',
        flexWrap:"wrap",
        borderRadius:10
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize: 12,
        width:"100%",
        textAlign: "left"
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    ViewPrice:{
        width:'100%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        marginTop:10
    },
    priceText:{
        paddingVertical:5,
        fontFamily:"open-bold, segoe ui, Arial"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-regular',
        fontSize:10,
        backgroundColor: THEME.TABS_BACKGROUND_COLOR,
        borderWidth:0,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:2,
        paddingBottom:0,
        paddingHorizontal:7,
        borderRadius:10
    },
    ViewTypeInfo:{
        width:'30%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    },
    shadow:{
        borderBottomWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.12,
        shadowRadius: 5,
        elevation: 2,
        marginBottom:10,
        marginTop:10,
        padding:5,
    }
})