import React from "react";
import {GetPriceProductBasketDiscount, GetPriceProductBasketNumber} from "./getPriceProductBasket";
import {useDispatch, useSelector} from "react-redux";
import {reloadBasketSum, reloadPreorderDops} from "../../store/actions/post";


//Новая версия
export const GetAllPriceBasket = (props)=>{

    const dispatch = useDispatch();

    const basket = useSelector(state => state.post.basket);
    const basketTopping = useSelector(state => state.post.basketTopping);
    const allTopping = useSelector(state => state.post.AllDopProduct);
    const allProducts = useSelector(state => state.post.allProducts);
    const CityId = useSelector(state => state.post.cityData.cityId);
    const ThreeForTwoActive = useSelector(state => state.post.ThreeForTwoActive);
    const SumDiscountThreeForTwo = useSelector(state => state.post.SumDiscountThreeForTwo);
    const ThreeForTwoListProduct = useSelector(state => state.post.ThreeForTwoListProduct);

    const promokodeActive = useSelector(state => state.post.promokodeActive);
    const DataPromokodeDiscountProduct = useSelector(state => state.post.dataPromokodeDiscoun);

    const getPriceModifyProduct = (product,count)=>{
        let sumPrice = 0;
        if(product?.modifyListProduct.length > 0){
            product?.modifyListProduct.forEach((d,n)=>{
                sumPrice += (d.price*count);
            })
        }

        return sumPrice;
    }

    if(allProducts === null){
        return "";
    }

    let AllSum = 0;
    let AllSumDiscountPromokode = 0;
    let SumTopping = 0;

    basketTopping.forEach((prTopping,num)=>{
        let priceTopping = 0;
        allTopping.forEach((dataTopping,number)=>{
            if(dataTopping.id == prTopping.id){
                priceTopping = dataTopping.price;
            }
        });

        SumTopping += prTopping.count*(priceTopping*1);
        //console.log(num,'Сумма допа - ',SumTopping);
    });

    let sumProductDiscount = 0;
    let sumProductNoDiscount = 0;
    let sumProductPromokode = 0;
    let sumProductThreeForTwo = 0;
    let sumModifyProduct = 0;

    DataPromokodeDiscountProduct.arrayProductDiscount.forEach((d,n)=>{

        if(d.tmpData.ProductListDiscount.countProductDiscountBasket > 0){
            sumProductDiscount += d.tmpData.ProductListDiscount.SumProductDiscount*1;
            // console.log('Сумма товаров по скидке ',d.tmpData.ProductListDiscount.SumProductDiscount);
        }

        if(d.tmpData.ProductListNoDiscount.countProductNoDiscount > 0){
            sumProductNoDiscount += d.tmpData.ProductListNoDiscount.SumProductNoDiscount*1;
            // console.log('Сумма товаров без скидки ',d.tmpData.ProductListNoDiscount.SumProductNoDiscount);
        }

        if(d.tmpData.ProductListPromokodeDiscount.countProductDiscountBasket > 0){
            sumProductPromokode += d.tmpData.ProductListPromokodeDiscount.sumProductDiscount*1;
           //console.log('Сумма товаров по промокоду ',d.tmpData.ProductListPromokodeDiscount.sumProductDiscount);
        }else{
           //console.log('Сумма товаров по промокоду которая не будет учтена ',d.tmpData.ProductListPromokodeDiscount.sumProductDiscount, d.tmpData.ProductListPromokodeDiscount);
            // sumProductPromokode =1;
        }

        if(d.tmpData.ProductListThreeForTwo.countProductDiscountBasket > 0){
            sumProductThreeForTwo += d.tmpData.ProductListThreeForTwo.sumProductDiscount*1;
            // console.log('Сумма товаров по 3=2 ',d.tmpData.ProductListThreeForTwo.sumProductDiscount);
        }

        // sumModifyProduct += getPriceModifyProduct(d,d.count);

        // console.log('Сумма модификаторов для товара в подсчете общей суммы = ',sumModifyProduct);

    });

    // console.log('Сумма всех модификаторов равна =',sumModifyProduct);

  //console.log('DataPromokodeDiscountProduct=',DataPromokodeDiscountProduct,'sumProductDiscount='+sumProductDiscount+' sumProductNoDiscount='+sumProductNoDiscount+' sumProductPromokode='+sumProductPromokode+' sumProductThreeForTwo='+sumProductThreeForTwo+' sumModifyProduct='+sumModifyProduct);

    AllSum = sumProductDiscount+sumProductNoDiscount+sumProductPromokode+sumProductThreeForTwo+sumModifyProduct;

    AllSum += SumTopping;
    // if(ThreeForTwoActive === true) {
    //     if(SumDiscountThreeForTwo !== undefined){
    //         AllSum -= SumDiscountThreeForTwo;
    //     }
    // }

    // console.log('Итог сумма в GetAllPrice = ',AllSum,sumProductDiscount,sumProductNoDiscount,sumProductPromokode,sumProductThreeForTwo);

    const reloadBasketSumDispatch = () => {
        dispatch(reloadBasketSum(AllSum));
        // console.log('Записали сумму');
    };

    reloadBasketSumDispatch();


    //return NumberFormatPrice(AllSum,0,'.',' ');
    return AllSum;
};


