import React from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../../../theme";
import {ButtonInCart} from "../../ButtonInCart";
import {NumberFormatPrice} from "../../../../templateComponents/NumberFormat/NumberFormat";
import {useSelector} from "react-redux";

export const PriceProductFromModify = ({ ProductData,stylePrice,styleNewPrice,styleOldPrice, onClick , fontSize = 14, fontFamily = 'bold', priceBlock = "row", whiteColorNewPrice = false}) => {

    const priceProductFromModify = useSelector(state => state.post.modifyTmpPrice);

    // console.log('Что я уже нахимичил блять ',priceProductFromModify);

    if(ProductData.price_discount !== false){
        // console.log('Мы тут',ProductData.price_discount.new_price);
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                <Text style={[styles.Newprice,styleNewPrice,{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                    {NumberFormatPrice((ProductData.price_discount.new_price*1)+(priceProductFromModify*1),0,'.',' ')} ₽
                </Text>
                <Text style={[styles.Oldprice,styleOldPrice]}>
                    {NumberFormatPrice((ProductData.price_discount.old_price*1)+(priceProductFromModify*1),0,'.',' ')} ₽
                </Text>
            </View>
        )
    }else{
        return (
            <View style={[styles.PriceBlock,{flexDirection:priceBlock}]}>
                <Text style={[[styles.price,stylePrice],{fontSize:fontSize},{fontFamily:"open-"+fontFamily+", segoe ui, Arial",fontWeight:700}]}>
                    {NumberFormatPrice((ProductData.price*1)+(priceProductFromModify*1),0,'.',' ')} ₽
                </Text>
            </View>
        )
    }
};

const styles = StyleSheet.create({
    PriceBlock: {
        display:"flex",
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch",
        // backgroundColor:"#fff"
    },
    PriceText:{

    },
    price: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize:15
    },
    Newprice: {
        color: THEME.MAIN_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize:15,
        paddingRight:5
    },
    Oldprice:{
        fontSize: 9,
        textDecorationLine:"line-through"
    }
});
