import React, { useState } from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView
} from "react-native";
import {THEME} from "../../../theme";
import {MaterialCommunityIcons, MaterialIcons} from "@expo/vector-icons";
import {Select} from "../../../templateComponents/Select/Select";
import {useDispatch, useSelector} from "react-redux";
import {setTypePayment} from "../../../store/actions/post";

export const PaymentType = ()=>{
    const dispatch = useDispatch();

    const SelectOption = ()=>{

        const typePayments = useSelector(state=>state.post.typePayment);

        const handleChangeTypePayment = (type)=>{
            dispatch(setTypePayment(type));
        };

        const CityIdActive = useSelector(state=>state.post.cityData.cityId);

        const ActiveOnlinePayment = useSelector(state=>state.post.AuthUserData.BasketServerSettings[CityIdActive].onlinePaymentActive);

        let ItemsType = [];

        if(ActiveOnlinePayment == "1"){
            ItemsType = [
                {title:"Наличными при получении",value:1},
                // {title:"Картой при получении",value:2},
                {title:"Онлайн оплата",value:3}
            ]
        }else{
            ItemsType = [
                {title:"Наличными при получении",value:1},
                // {title:"Картой при получении",value:2},
            ]
        }

        // console.log(typePayments);
        return (<Select
            defaultValue={typePayments}
            fontSize={12}
            item={ItemsType}
            keys="Payment"
            onChange={handleChangeTypePayment}
        />)
    };


    const PaymentsType =
        <View
            style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center",width:"100%"}}
            onPress={
                ()=>{}
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%"}}>
                {/*<Text style={styles.textIconCenter}>Картой при получении</Text>*/}
                {SelectOption()}
            </View>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </View>
    ;

    return (
        <View style={[styles.BoxWhiteBlock,styles.shadow]}>
            {PaymentsType}
        </View>
    )
};

const styles = StyleSheet.create({
    BoxWhiteBlock:{
        backgroundColor:"#ffffff",
        borderRadius:10,
        paddingHorizontal:10,
        paddingVertical:10,
    },
    AdressStr:{
        width:"100%",
        flexDirection:'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingHorizontal:2,
        paddingVertical:10,
        marginVertical:0,
        borderColor:"#d6d6d6",
        borderTopWidth:0,
        // backgroundColor: "#ff7300"
    },
    textIconCenter:{
        flexDirection:'row',
        // width:"100%",
        maxWidth:"84%",
        marginLeft:5,
        alignItems:'center',
        justifyContent:'space-between',
        fontSize:12,
        textAlign:"left",
        // backgroundColor:"#ff7300"
    },
    arrowRight:{
        width:"5%",
        marginLeft:0,
        paddingTop:Platform.OS === 'ios' ? 0 : 2
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.22,
        shadowRadius: 40,
        elevation: 3,
        marginBottom:0
    },
});