import {
    toogleBooked,
    removePost,
    setSelectAdres,
    loadAdressUser,
    setOrderListServer,
    setActiveOrderScreenData
} from '../../../store/actions/post';
import {OneAdress} from "../../../screens/DeliveryBlock/OneAdress";
import { Feather } from '@expo/vector-icons';
import {DB} from "../../../db";
