const OneOrderScreen = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    const [loaded,setLoaded] = useState(true);
    const [loadedFC,setLoadedFC] = useState(true);
    const ActiveOrder = useSelector(state => state.post.activeOrder);

    // console.log(ActiveOrder);

    // const OrderListArray = useSelector(state=>state.post.orderListUser);

    const getOrderList = async ()=>{
        // const Result = await ServerDB.getOrderUserList(CityIds,idUserSystem);
        // dispatch(setOrderListServer(Result));
        return true;
    };

    const ReloadListAdres = async ()=>{

        // const Results = await dispatch(loadAdressUser(idUserSystem,[],{cityId:CityIds}));
        await getOrderList();
        await offReloads();
        setLoaded(false);
    };

    const offReloads = async ()=>{
        await dispatch(setActiveOrderScreenData(ActiveOrder.idOrder,false));
    }


    useEffect(() => {
        // Put Your Code Here Which You Want To Refresh or Reload on Coming Back to This Screen.
        if('reloads' in route.params){
            setLoaded(true);
            // ReloadListAdres()
        }else{}
        //console.log('Обновляемся ',navigation.state.params);
    }, [route.params]);

    if(loadedFC) {
        useEffect(()=> {
                ReloadListAdres()
            }
            ,[dispatch]);
    }else{

    }


    const Loadings = ()=>{
        if(loaded){
            return (
                <SafeAreaView style={styles.centeredViewWindowTop}>
                    <ActivityIndicator color={THEME.MAIN_COLOR} size={50}/>
                    <Text style={styles.TextInfoMini}>Обновление...</Text>
                </SafeAreaView>
            )
        }else{
            return(
                <UserOneOrderScreenAwait route={route} navigation={navigation} OrderList={null}/>
            )
        }
    };

    // console.log(ActiveOrder);

    if(ActiveOrder.idOrder === null){
        return (
            <SafeAreaView style={styles.centeredViewWindowTop}>
                <ActivityIndicator color={THEME.MAIN_COLOR} size={50}/>
                <Text style={styles.TextInfoMini}>Обновление...</Text>
            </SafeAreaView>
        )
    }

    return(
        <Loadings />
    )

};


export const UserOneOrderScreenAwait = ({ navigation, route }) => {
    const dispatch = useDispatch()
    const postId = route.params.postId;
    const ActiveOrder = useSelector(state => state.post.activeOrder);

    // const OrderData = route.params.productData;
    const idOrder = route.params.idOrder;
    // const idOrder = ActiveOrder.idOrder;
    // const basket = useSelector(state => state.post.basket);

    const OrderListArray = useSelector(state=>state.post.orderListUser);
    const OrderData = OrderListArray[idOrder];


    // console.log(idOrder,OrderListArray,OrderData);

    const AdresList = useSelector(state => state.post.adressUser.listAddress);
    const AdresListNoDelivery = useSelector(state => state.post.adressUser.ListAddressNoDelivery);

    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);
    const PhoneCity = useSelector(state=>state.post.phoneNumberCityPoint[CityIds]);
    const BonusPointsText = useSelector(state => state.post.bonusPointsText);
    const BonusPointsActive = useSelector(state => state.post.bonusPointsActive);



    // console.log(OrderData);

    const getOrderList = async ()=>{
        const Result = await ServerDB.getOrderUserList(CityIds,idUserSystem);
        dispatch(setOrderListServer(Result));
    };


    // const toggleHandler = useCallback(() => {
    //     dispatch(toogleBooked(product))
    // }, [dispatch, product])
    //
    useEffect(() => {
        getOrderList();
    }, [dispatch]);

    // const updateStatusOrder = ()=>{
    //     setTimeout(()=>{
    //         console.log('Мы на этом скрине ',navigation.state.routeName );
    //         updateStatusOrder();
    //     },2000);
    // };
    //
    // if(navigation.state.routeName === 'OneOrderPage'){
    //     updateStatusOrder();
    // }


    const OrderNullPage = ()=>{
        return(
            <SafeAreaView style={styles.centeredViewWindowTop}>
                <AntDesign name="closecircleo" size={50} color={THEME.MAIN_COLOR} />
                <Text style={styles.TextInfo}>Информации о заказе нет!</Text>
                <Text style={styles.TextInfoMini}>К сожаления возникла непредвиденная{"\n"}ошибка, свяжитесь с представителем компании!</Text>
            </SafeAreaView>
        );
    };

    const GetStatusOrder = ()=>{
        if(OrderData.status_order_real === null){

            switch (OrderData.status_order) {
                case "3": return "Отменен"; break;
                case "1":

                    if(OrderData.payment.type_int == 0 & OrderData.payStatusSBR == 0){
                        return "Ожидает оплаты";
                    }else{
                        return "Принят в обработку";
                    }

                    break;
                case "0":
                    if(OrderData.payment.type_int == 0 & OrderData.payStatusSBR == 0){
                        return "Ожидает оплаты";
                    }else{
                        return "В обработке";
                    }
                    break;
                default:return "В обработке";
            }

        }else{
            return OrderData.status_order_real.status;
        }
    };

    const getActiveStepProgress = (status)=>{
        if(Number.isInteger(status)){return status}
        switch (status) {
            case "0": return 0;
            case "1": return 5;
            case "2": return 1;
            case "3": return 5;
            case "4": return 0;
            case "5": return 0;
            case "6": return 0;
            case "7": return 0;
            case "8": return 0;
            default: return 0;
        }
    };

    const getCountProductOrder = ()=>{
        let Counts = 0;
        if(OrderData.productList === null || OrderData.productList === ''){
            return 0;
        }
        OrderData.productList.forEach((d,n)=>{
            Counts += (d.count*1);
        });

        return Counts;
    };

    const getCountDopProductOrder = ()=>{
        let Counts = 0;
        if(OrderData.dopProductList === null || OrderData.dopProductList === ''){
            return 0;
        }
        OrderData.dopProductList.forEach((d,n)=>{
            Counts += (d.count*1);
        });

        return Counts;
    };

    const getSeparatorLine = ()=>{
        return (
            <View style={styles.separatorBox}>
                <View style={styles.separatorLine}>
                </View>
            </View>
        )
    };


    const getTypePaymentText =()=>{
        switch (OrderData.payment.type_int) {
            case 0: return "Онлайн оплата";
            case 1: return "Наличными при получении";
            case 2: return "Картой при получении";
            case "0": return "Онлайн оплата";
            case "1": return "Наличными при получении";
            case "2": return "Картой при получении";
            default: return "Тип не установлен"
        }
    };

    const getPointsInOut = ()=>{
        let In = OrderData.point_in;
        let Out = OrderData.point_out;

        return (
            <View style={styles.paddingHorizontals}>
                <Text style={styles.DeliveryOrderProductDescr}>Начислено: {In} {getNoun(In,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
                <Text style={styles.DeliveryOrderProductDescr}>Списано: {Out} {getNoun(Out,BonusPointsText.one,BonusPointsText.two,BonusPointsText.five)}</Text>
            </View>
        )
    };

    const GetModifyListProduct = ({modifyList})=>{
        // console.log('modifyList = - = - = - = -',modifyList);
        let ModifyText = '';
        modifyList?.map((d,n)=>{
            // ModifyText += ' + '+d.title_product+'\n';
            ModifyText += " • "+d.title_product;
        });
        return (<Text style={{fontFamily:"open-bold, segoe ui, Arial",fontSize:10,color:THEME.TEXT_DESCRIPTION_PRODUCT,paddingTop:5}}>{ModifyText}</Text>)
    };

    const getPriceOneProductVisual = (dataProduct)=>{
      let returnVis = false;

      if(dataProduct.product_price !== dataProduct.sale_price){
          returnVis = <Text style={{fontSize:11}}><Text style={{textDecorationLine:"line-through",fontStyle:"italic",fontSize:10}}>{dataProduct?.product_price}</Text> <Text >{dataProduct?.sale_price}</Text> ₽ </Text>;
      }else{
          returnVis = <Text style={{fontSize:10}}>{dataProduct?.sale_price} ₽ </Text>;
      }

      return returnVis;
    };

    const getOrderProductList = ()=>{

        const ListProduct = OrderData.productList?.map((d,n)=>{
            return (<>
                    {n > 0 ?
                        <View style={[styles.separatorBox,{paddingVertical:0}]}>
                            <View style={styles.separatorLine}>
                            </View>
                        </View>
                    : false
                    }
                <View style={styles.productPreview} key={"OrderProduct_"+n}>
                    <View style={{justifyContent:"flex-start",alignItems:"flex-start",height:"100%"}}>
                        {/*<Image style={styles.image} source={{ uri: "https:"+d.product_img }} width={30} height={30}/>*/}
                        <Text>{d.count !== null ? d.count : false} x</Text>
                    </View>
                    <View style={{width:"75%"}}>
                        <Text style={{fontFamily:"open-bold, segoe ui, Arial",textAlign:"left"}}>{d.product_title !== null ? d.product_title.split('&amp;quot;').join('"').split('&quot;').join('"') : "Товар более не актуален"}</Text>
                        <View>{getPriceOneProductVisual(d)}</View>
                        {d?.ModifyListProduct?.length > 0 ?
                            <GetModifyListProduct modifyList={d.ModifyListProduct}/>
                            : false}
                    </View>
                    <View style={{width:"14%",justifyContent:"flex-start",alignItems:"flex-end",height:"100%"}}>
                        {/*<Text>Кол-во: {d.count !== null ? d.count : false}</Text>*/}
                        <Text>{d.cost !== null ? d.cost : false} ₽</Text>
                    </View>
                </View>
                </>
            )
        });

        return (
            <ScrollView
                horizontal={false}
                style={[styles.paddingHorizontals,{paddingHorizontal:0}]}
                showsHorizontalScrollIndicator={false}
            >
                {ListProduct}
            </ScrollView>
        )
    };

    const GetOrderInfo = ()=>{
        // console.log('Инфо о заказе ',OrderData);
        return (
            <ScrollView style={styles.centeredViewWindowTop}>
                <View style={styles.paddingHorizontals} key={"StatusBarOrder"}>
                    <Text style={styles.statusOrderText}><GetStatusOrder /></Text>
                </View>
                <View key={"ProgressBarOrder"}>
                    {/*<ProgressStepOrder stepActive={getActiveStepProgress(OrderData.status_order)}/>*/}
                    {
                        OrderData.status_order_real !== null ?
                            <STepper ActiveStep={getActiveStepProgress(OrderData.status_order_real.numberLine)}/>
                            :
                            OrderData.payStatusSBR == 1  ? <STepper ActiveStep={getActiveStepProgress("0")}/> : <STepper ActiveStep={getActiveStepProgress(OrderData.status_order)}/>
                    }
                </View>
                {getSeparatorLine()}
                <View style={styles.paddingHorizontals} key={"OrderDataList"}>
                    <Text style={styles.CountOrderProductDescr}>Товаров: {getCountProductOrder()}, соусов: {getCountDopProductOrder()}, на сумму {NumberFormatPrice(OrderData.sum_order,0,'.',' ')} ₽</Text>
                    <Text style={styles.DeliveryOrderProductDescr}>
                        {OrderData.dilivery.dilivery_type}: {OrderData.dilivery.dilivery_type !== 'Самовывоз' ? OrderData.dilivery.dilivery_adres : OrderData.affiliate.pickup.affiliate_info}
                    </Text>
                    {OrderData.dilivery.dilivery_price*1 >0 ? <Text style={styles.DeliveryOrderProductDescr}>Стоимость доставки: {OrderData.dilivery.dilivery_price}</Text>:false}
                </View>
                {OrderData.promo !== 'none' ?
                    <View style={styles.paddingHorizontals} key={"PromokodeData"}>
                        <Text style={styles.DeliveryOrderProductDescr}>Промокод: {OrderData.promo.promo_code} - {OrderData.promo.promo_info.title}</Text>
                    </View>
                    :
                    false}
                <View style={styles.paddingHorizontals} key={"TypePayments"}>
                    <Text style={styles.DeliveryOrderProductDescr}>Тип оплаты: {getTypePaymentText()}</Text>
                </View>
                {BonusPointsActive ? getPointsInOut() : false}
                {OrderData.pre_order.status !== 'none' ?
                    <View style={styles.paddingHorizontals} key={"PreorderData"}>
                        <Text style={styles.PreorderInfo}>Предзаказ на: {OrderData.pre_order.date_pre_order}</Text>
                    </View>
                    : false}

                {OrderData.payment.type_int == 0 & OrderData.status_order == 0 & OrderData?.idPayConfig != null & OrderData?.payStatusSBR == 0 ?
                    <Pressable
                        style={{backgroundColor:THEME.MAIN_COLOR,marginTop:"2%",marginHorizontal:"5%",borderRadius:7}}
                        color={THEME.TEXT_COLOR}
                        onPress={() => {
                            navigation.navigate('OnlinePayments', {
                                title: 'Онлайн оплата заказа №'+OrderData.id_order,
                                idOrder:OrderData.id_order*1,
                                sumPayment:OrderData?.sum_order || 0,
                                idPointsPaymentToken:OrderData?.idPayConfig || 0
                            });
                        }}
                    >
                        <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:16,textAlign:"center",color:THEME.TEXT_COLOR,marginVertical:5}}>Оплатить заказ</Text>
                    </Pressable>
                    :
                    false
                }

                {getSeparatorLine()}
                {getOrderProductList()}
                {getSeparatorLine()}

                {OrderData.status_order == 1 || OrderData.status_order == 3 || OrderData.status_order == 6 ?
                    <View style={{flex:1,justifyContent:"center",alignItems:"center",textAlign:"center"}}>
                        <Pressable
                            style={styles.buttonCity}
                            color={THEME.TEXT_COLOR}
                            onPress={() => {
                                navigation.navigate('ReviewsInfo', {
                                    title: 'Список заказов',
                                    idOrderReview:OrderData.id_order
                                });
                            }}
                        >
                            <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:16,textAlign:"center",color:THEME.TEXT_COLOR,marginVertical:5}}>Оставить отзыв</Text>
                        </Pressable>
                    </View>
                    :
                    false
                }


                <View key={"TextOrderData"}>
                    <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:14,textAlign:"center"}}>Изменить или уточнить детали заказа:</Text>
                    <Pressable
                        onPress={()=>{dialCall()}}
                        style={{paddingVertical:"4%"}}
                    >
                        <Text style={{fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,fontSize:16,textAlign:"center",color:THEME.MAIN_COLOR,}}>
                            {PhoneCity !== undefined & PhoneCity !== null ? PhoneCity.telTile : false}
                        </Text>
                    </Pressable>
                </View>
            </ScrollView>
        )
    };


    const dialCall = () => {
        let phoneNumber = '';

        if (Platform.OS === 'android') {
            phoneNumber = 'tel:'+PhoneCity.tel;
        }
        else {
            phoneNumber = 'telprompt:'+PhoneCity.tel;
        }
        Linking.openURL(phoneNumber);
        //console.log(phoneNumber);
    };


    return (
        OrderData === null ?
            <OrderNullPage />
            :
            <GetOrderInfo />
    )
};

OneOrderScreen.navigationOptions = ({ navigation, route }) => {
    const title = route.params.title;
    return {
        headerTitle: title,
        headerLeft: (
            <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                <Item
                    title='Toggle Drawer'
                    iconName='arrow-back'
                    color={"#333333"}
                    onPress={() => navigation.goBack()}
                />
            </HeaderButtons>
        )
    }
};

