import {Platform, StyleSheet, Text} from "react-native";
import {THEME} from "../theme";
import {Ionicons,FontAwesome5,FontAwesome,AntDesign, Feather, MaterialCommunityIcons} from "@expo/vector-icons";
import React from "react";

export const IconLabelEbee = ({name, size, color, label = false, typeIcon="FontAwesome5"})=>{

    let Icons = <FontAwesome5 name={name} size={size} color={color}/>

    switch (typeIcon) {

        case "MaterialCommunityIcons": Icons = <MaterialCommunityIcons name={name} size={size} color={color}/>; break;
        case "Ionicons": Icons = <Ionicons name={name} size={size} color={color}/>; break;
        case "Feather": Icons = <Feather name={name} size={size} color={color}/>; break;
        case "FontAwesome": Icons = <FontAwesome name={name} size={size} color={color}/>; break;
        case "AntDesign": Icons = <AntDesign name={name} size={size} color={color}/>; break;
        case "FontAwesome5":
        default: Icons = <FontAwesome5 name={name} size={size} color={color}/>;
    }
    if(label === false){
        return (Icons);
    }else {
        return (
            <>
                <Text style={styles.i}>
                    {label}
                </Text>
                {Icons}
            </>
        )
    }
};


const styles =

    Platform.OS === 'android' ?

        StyleSheet.create({
        i:{
            position:"absolute",
            top:0,
            right:-10,
            fontSize:10,
            paddingHorizontal:4,
            paddingVertical:1,
            paddingBottom:2,
            backgroundColor:THEME.MAIN_COLOR,
            borderRadius:25,
            zIndex:1,
            minWidth:17,
            minHeight:10,
            display:"flex",
            justifyContent:"center",
            alignItems:"center",
            textAlign: "center",
            color:"#ffffff"
        }
        })
        :
        StyleSheet.create({
            i:{
                position:"absolute",
                top:0,
                right:10,
                fontSize:10,
                paddingHorizontal:4,
                paddingVertical:1,
                paddingBottom:2,
                backgroundColor:THEME.MAIN_COLOR,
                borderRadius:8,
                zIndex:1,
                minWidth:17,
                minHeight:10,
                display:"flex",
                justifyContent:"center",
                alignItems:"center",
                textAlign: "center",
                color:"#ffffff",
                overflow:"hidden"
            }
        })
;