export const getAllFilter = (allCategory,activeCategory)=>{
    let AllFilters = [];
    //console.log('Что нужнео отфильтровать ебана рот',allCategory);

    allCategory.map((d,n)=>{
        d.id == activeCategory ?
            typeof d?.FilterCategory === "object" ?
                Object.keys(d?.FilterCategory).map((dd,nn)=>{
                    AllFilters.push({title:d?.FilterCategory[dd],value:dd})
                })
                : false
            : false
    });

    return AllFilters;
};