import React from 'react'
import {
    View,
    StyleSheet,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native';
import {useSelector } from 'react-redux'

import {THEME} from './../../theme';

import {CheckCategoryOff} from '../../components/CheckCategoryOff';

export const CategoryView = ({ category, setActiveCategory, handlReloadCatalogStatus }) => {
    const activeCategory = useSelector(state => state.post.activeCategoryProductList);

    const DeliveryAdresSystem = useSelector(state => state.post.adressSelect);
    const AffiliateAdresSystem = useSelector(state => state.post.affiliateSelect);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);

    let CheckNulls = null;
    let CheckedOffCategorys = [];

    if(TypeDeliverySystem === true){

        CheckNulls = DeliveryAdresSystem;
        CheckedOffCategorys = DeliveryAdresSystem;
    }else{
        CheckNulls = AffiliateAdresSystem;
        CheckedOffCategorys = AffiliateAdresSystem;
    }


    const setActiveCategoryF = (id)=>{
        handlReloadCatalogStatus(true,true);
        setActiveCategory(id);
    };



    if(CheckNulls === null){
        return(
            false
        )
    }else {
        const StatusCategoryOff = CheckCategoryOff(CheckedOffCategorys,category.id,TypeDeliverySystem);
        if(StatusCategoryOff === true){
            return false;
        }else{
            return (
                <TouchableOpacity
                    activeOpacity={0.7}
                    onPress={
                        () => setActiveCategoryF(category.id)
                    }
                    key={"Cats_Touch" + category.id}
                    style={{outline:'none'}}
                >
                    <View style={activeCategory === category.id ? styles.textActive : styles.textWrap}
                          key={"Cats_view" + category.id}>
                        <Text style={activeCategory === category.id ? styles.titleActive : styles.title}>
                            {category.title}
                        </Text>
                    </View>
                </TouchableOpacity>
            )
        }
    }
};

const styles = StyleSheet.create({
    post: {
        marginBottom: 15,
        overflow: 'hidden'
    },
    image: {
        width: '100%',
        height: 200
    },
    textWrap: {
        paddingHorizontal:2,
        width: '100%',
        // minWidth:120,
        alignItems: "center"
    },

    textActive: {
        paddingHorizontal:2,
        width: '100%',
        // minWidth:120,
        alignItems: "center"
    },
    title: {
        backgroundColor: THEME.BACKGROUND_COLOR_TABS_MENU_DEFAULT,
        color:THEME.TEXT_COLOR_TABS_MENU_DEFAULT,
        paddingVertical: 5,
        paddingBottom:7,
        paddingHorizontal: 25,
        marginRight:5,
        alignItems: 'center',
        borderRadius:15,
        overflow: "hidden",
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:14,
        whiteSpace:'nowrap'
    },
    titleActive:{
        backgroundColor: THEME.BACKGROUND_COLOR_TABS_MENU_ACTIVE,
        color:THEME.TEXT_COLOR_TABS_MENU_ACTIVE,
        paddingVertical: 5,
        paddingBottom:7,
        paddingHorizontal: 25,
        marginRight:5,
        alignItems: 'center',
        borderRadius:15,
        overflow: "hidden",
        fontFamily: 'open-bold, segoe ui, Arial',
        fontSize:14,
        whiteSpace:'nowrap'
    }
})
