import React, { useState } from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView,
    ScrollView,
    Dimensions, KeyboardAvoidingView, ActivityIndicator,

} from "react-native";
import {THEME} from "../../../theme";
import {AntDesign, Ionicons} from '@expo/vector-icons';
import {useDispatch, useSelector} from "react-redux";
import {
    loadProductScreen,
    loadSlideHeader,
    setCitySystem,
    loadCatalogProduct,
    loadSlideHeaderStatus, setCityUserSystemPhone, setModalStatus, setPromokodeErrorUpdate, setPromokodeUpdate
} from "../../../store/actions/post";
import ModalPage from "../../../templateComponents/ModalPage/ModalPage";
import {ButtonEbee} from "../../../templateComponents/Button/ButtonEbee";
import {NumberFormatPrice} from "../../../templateComponents/NumberFormat/NumberFormat";
import {Group} from "../../../templateComponents/Group/GroupMain";
import {PromokodeInput} from "./PromokodeInput";
import {ServerDB} from "../../../serverDb";
import {ConditionsPromokod} from "./ConditionsPromokod";
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";



export const PromokodeModal = ({titleCity,keys}) => {
    const [modalVisible, setModalVisible] = useState(true);
    const dispatch = useDispatch();
    const StatusModal = useSelector(state=>state.post.modalStateStatus.MODAL_PROMOKODE);
    const StatusAllModal = useSelector(state=>state.post.modalStateStatus);

    const ErrorPromokode = useSelector(state=>state.post.promokodeError);
    const PromokodeActiveStatus = useSelector(state=>state.post.promokodeActive);
    const PromokodeDate = useSelector(state=>state.post.promokodeData);

    const CityIds = useSelector(state=>state.post.cityData.cityId);
    const idUserSystem = useSelector(state=>state.post.AuthUserData.id);

    //const sumItogBasket = useSelector(state => state.post.basketSum);


    let [animateModal, setanimateModal] = useState(false);
    let [statusLoadingPromokode, setstatusLoadingPromokode] = useState(false);
    let [kode, setKode] = useState(null);

    //console.log(StatusAllModal);

    const ActiveIndicateLoading = ()=>{
        return(
            <SafeAreaView style={{justifyContent:"center",alignItems:"center",height:"50%"}}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
                <Text>Обновление...</Text>
            </SafeAreaView>
        )
    }

    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const ErrorPromokodeInfo = ()=>{
        if(ErrorPromokode.status === false){
            return false;
        }else{
          return (
              <View style={styles.ChildBlock}>
                  <Text style={[styles.TextInfoMiniError,{textAlign: "center"}]}>Ошибка: {ErrorPromokode.message}</Text>
              </View>
          )
        }
    };

    const handleDeactivatePromokode = ()=>{
        setKode(null);
        dispatch(setPromokodeUpdate(null,false));
    }


    const PromokodeInfoServer = ()=>{
       // console.log(PromokodeDate);

        if(PromokodeActiveStatus === false){
            return false;
        }else{
          return (
              <>
                  <FlatList
                      data={FlatListDataActivePromo}
                      renderItem={({item, index}) => (
                          item
                      )}
                      keyExtractor={item => item.id}
                  />
                          {/*<Group*/}
                          {/*    styles={{*/}
                          {/*        position:"absolute",*/}
                          {/*        // top:Dimensions.get('window').height*0.6,*/}
                          {/*        bottom:Dimensions.get('window').height*0.18,*/}
                          {/*        backgroundColor:"#ffffff",*/}
                          {/*        paddingBottom: "10%",*/}
                          {/*        width:"100%"*/}
                          {/*    }} shadow={false}*/}
                          {/*>*/}
                              <Group
                                  styles={{
                                      position:"absolute",
                                      // top:Dimensions.get('window').height*0.6,
                                      // bottom:Dimensions.get('window').height*0.18,
                                      bottom:0,
                                      backgroundColor:"#ffffff",
                                      // paddingBottom: "10%",
                                      width:"100%",
                                      left:0,
                                      // marginBottom: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 25 : 0
                                      marginBottom: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 25 : Dimensions.get('window').width >460 ? 7 : 0
                                  }} shadow={false}
                              >
                              <ButtonEbee
                                  onPress={()=>{
                                      handleDeactivatePromokode()
                                  }}
                                  size="xl"
                                  borderRadius={20}
                                  shadow={false}
                              >
                                  Деактивировать
                              </ButtonEbee>
                          </Group>
                      </>
          )
        }
    };

    const FlatListData = [
        <KeyboardAvoidingView style={styles.centeredViewWindowTop} behavior={Platform.OS === 'android' ? false : false} keyboardVerticalOffset={70}>
        <View style={styles.modalContent}>
            <View style={styles.TitleModal}>
                <Text style={styles.modalTextTitle}>Введите промокод</Text>
            </View>
            <View style={styles.ChildModal}>
                <View style={styles.ChildBlock}>
                    <PromokodeInput value={kode} setValue={setKode}/>
                </View>
                <View style={styles.ChildBlock}>
                    <Text style={[styles.TextInfoMini,{textAlign: "center",backgroundColor:THEME.INFO_COLOR}]}>Только цифры и буквы латинского алфавита*</Text>
                </View>
                {statusLoadingPromokode ? ActiveIndicateLoading() : ErrorPromokodeInfo()}
            </View>
        </View>
        </KeyboardAvoidingView>,
    ];


    const DataPromokodeInfo = ()=>{
        return (
                PromokodeDate !== null && PromokodeDate !== undefined && PromokodeDate != 'undefined' ?
                <View style={styles.BorderRadiusBox}>
                    <Text style={[styles.TextInfoMiniOk,{textAlign: "center"}]}>
                        Ваш промокод: {PromokodeDate.promokodeInfo.kode}
                    </Text>
                    <Text style={[styles.TextInfoMiniOk,{textAlign: "center"}]}>
                        {PromokodeDate.promokodeInfo.text}
                    </Text>
                </View> :
                <Text style={[styles.TextInfoMiniError,{textAlign: "center"}]}>
                    Ошибка: Не возможно прочитать данные промокода. Пожалуйста, деактивируйте промокод, и введите его снова!
                </Text>
        )
    }

    const FlatListDataActivePromo = [
        <KeyboardAvoidingView style={styles.centeredViewWindowTop} behavior={Platform.OS === 'android' ? false : false} keyboardVerticalOffset={70}>
            <View style={styles.modalContent}>
                <View style={styles.TitleModal}>
                    <Text style={styles.modalTextTitle}>Активный промокод</Text>
                </View>
                <View style={styles.ChildModal}>
                    <View style={styles.ChildBlock}>
                        {DataPromokodeInfo()}
                    </View>
                    <View style={styles.ChildBlock}>
                        <ConditionsPromokod typeReturn={1} />
                        <ConditionsPromokod typeReturn={2} />
                    </View>
                </View>
            </View>
        </KeyboardAvoidingView>,
    ];

    const handleActivePromokode = async (idCity,idUser)=>{
        setstatusLoadingPromokode(true);
        const ReultServer = await ServerDB.getPromokodeActive(idCity,idUser,kode);

        setTimeout(()=>{
            setstatusLoadingPromokode(false);
        },1000);

        if(ReultServer.error === null){
            dispatch(setPromokodeErrorUpdate(false,null));
            dispatch(setPromokodeUpdate(ReultServer.result,true));

        }else{
            dispatch(setPromokodeErrorUpdate(true,ReultServer.errorInfo));
            dispatch(setPromokodeUpdate(null,false));
        }

     //   console.log('Проверка промокода - получили ответ - ==========>>>>>>>>',ReultServer);
    };

    return (
        <ModalPage
            modalVisible={StatusModal}
            // PressToanimate={animateModal}
            ContentModal={
                <View
                    scrollEnabled={false}
                    style={styles.containerContent}
                >
                    {PromokodeActiveStatus ? PromokodeInfoServer() :
                        <>
                            <FlatList
                                data={FlatListData}
                                renderItem={({item, index}) => (
                                    item
                                )}
                                keyExtractor={item => item.id}
                                // contentContainerStyle={}

                            />
                            <Group
                                styles={{
                                    position:"absolute",
                                    // top:Dimensions.get('window').height*0.6,
                                    // bottom:Dimensions.get('window').height*0.18,
                                    bottom:0,
                                    backgroundColor:"#ffffff",
                                    // paddingBottom: "10%",
                                    width:"100%",
                                    left:0,
                                    // marginBottom: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 25 : 0
                                    marginBottom: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 25 : Dimensions.get('window').width >460 ? 7 : 0
                                }} shadow={false}
                            >
                                <ButtonEbee
                                    onPress={()=>{
                                        //setModalVisible(!modalVisible);
                                        handleActivePromokode(CityIds,idUserSystem);
                                        //setanimateModal(true);
                                        //handleSetStatusModal("MODAL_PROMOKODE",false);
                                    }}
                                    size="xl"
                                    borderRadius={20}
                                    shadow={false}
                                >
                                    Активировать
                                </ButtonEbee>
                            </Group>
                        </>
                    }
                </View>
            }
            HeaderStyle={styles.headerContent}
            ContentModalStyle={styles.Modal}
            HeaderContent={
                <View style={styles.containerHeader}>
                    <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? styles.topIndicatorWeb : Dimensions.get('window').width >460 ? styles.topIndicatorWeb : styles.topIndicator}>
                        {/*{Dimensions.get('window').width >= 421 ? <ButtonEbee*/}
                        {/*    onPress={()=>{*/}
                        {/*        handleSetStatusModal("MODAL_PROMOKODE",false);*/}
                        {/*        //setModalVisible(!modalVisible);*/}
                        {/*        setanimateModal(false);*/}
                        {/*    }}*/}
                        {/*    size={'l'}*/}
                        {/*    borderRadius={20}*/}
                        {/*>*/}
                        {/*    Закрыть*/}
                        {/*</ButtonEbee> : false}*/}
                        {getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ?
                        <AntDesign name="closecircleo" size={20} color={THEME.MAIN_COLOR}
                                   onPress={()=>{
                                       handleSetStatusModal("MODAL_PROMOKODE",false);
                                       //setModalVisible(!modalVisible);
                                       setanimateModal(false);
                                   }}
                                   style={
                                       {
                                           alignSelf:'end',
                                           // paddingRight:'2%',
                                           paddingTop:10,
                                           paddingRight:15
                                       }}
                        />
                        :
                        Dimensions.get('window').width >460 ?
                        <AntDesign name="closecircleo" size={20} color={THEME.MAIN_COLOR}
                                   onPress={()=>{
                                       handleSetStatusModal("MODAL_PROMOKODE",false);
                                       //setModalVisible(!modalVisible);
                                       setanimateModal(false);
                                   }}
                                   style={{
                                       alignSelf:'end',
                                       // paddingRight:'2%',
                                       paddingTop:10,
                                       paddingRight:15
                                   }}
                        />
                        :
                            false}
                    </View>
                </View>
            }
            onClose={() => {
                handleSetStatusModal("MODAL_PROMOKODE",false);
                //setModalVisible(!modalVisible);
                setanimateModal(false);
            }}

        />
    );
};

const styles = StyleSheet.create({
    centeredViewWindowTop:{
        justifyСontent: "center",
        alignItems: "center",
        display: "flex",
        height: "100%",
    },
    centeredViewActive: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        // backgroundColor:"rgba(0,0,0,0.40)"
    },
    centeredView: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 10,
        //backgroundColor:"rgba(0,0,0,0.40)"
    },
    PressableClose:{
        height:"100%",
        width:"100%"
    },

    ChildBlock:{
        marginVertical:10
    },
    TextInfoMini:{
        backgroundColor:THEME.INFO_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    BorderRadiusBox:{
        backgroundColor:"#FFFFFF",
        borderRadius:10,
        overflow:"hidden",
        paddingHorizontal:2,
        paddingVertical:10
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    TextInfoMiniError:{
        backgroundColor:THEME.DANGER_COLOR,
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        paddingVertical:5,
        paddingHorizontal:2
    },
    titleChildBlock:{
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:16,
        marginBottom:10
    },

    centeredViewWindow: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        backgroundColor:"rgba(0,0,0,0.40)"
    },
    modalView: {
        width:"100%",
        height:"80%",
        margin: 0,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 0,
        position:"absolute",
        alignItems: "center",
        bottom:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5
    },

    topIndicator:{
        width:40,
        height:3,
        borderRadius:10,
        marginVertical:4,
        backgroundColor:"#dddddd"
    },
    topIndicatorWeb:{
        // width:40,
        width:'32%',
        // height:3,
        height:'30%',
        borderRadius:10,
        marginVertical:4,
        alignSelf:'end'
        // backgroundColor:"#dddddd"
    },
    modalContent:{
        width:"100%",
        paddingHorizontal:"3%",
        paddingVertical: 3,
        paddingBottom:Dimensions.get('window').height*0.2,
        // paddingTop:'3%',
        // paddingTop:'17%', // базовое значение для всех платформ
        paddingTop:getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? '22%' : Dimensions.get('window').width >460 ? '33%' :'80%',
        alignItems:'center'
        //paddingBottom:Platform.OS === 'android' ? 0 : 0,
        // height:Dimensions.get('window').height*0.8
    },

    TitleModal:{
        width:"100%",
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center",
        fontSize:18,
        //backgroundColor:"#ff7300"
    },
    modalTextTitle:{
        // backgroundColor:"#ff7300",
        textAlign:"center",
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
        fontSize:18
    },

    ChildModal:{
        marginVertical:0,
        // maxWidth:300,
        // minWidth:300
        width:300
    },






    containerContent: {
        flex: 1,
        marginTop: getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? 20 : 0,
        paddingHorizontal:"1%",
        paddingVertical:"1%",
        // paddingBottom:"40%",
        // paddingBottom:"40%",
        backgroundColor:"#fafafa",
        //height:Dimensions.get('window').height*0.84
        // overflow:'hidden'
        // alignItems:'center',
        // justifyContent:'flex-end'
    },
    containerHeader: {
        flex: 1,
        alignContent: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        height: 30,
        backgroundColor: "#fafafa",
        borderTopLeftRadius:10,
        borderTopRightRadius:10
    },
    headerContent:{
        marginTop: 0,
    },
    Modal: {
        backgroundColor: '#005252',
        marginTop: 0,
    }
});