            //     let PrevSum1 = prev.id in allProducts[prev1.categoryId].ProductList ? getTypeProductData(prev1.type_id,allProducts[prev1.categoryId].ProductList[prev.id]).price : 0;
            //
            //     return nextSum1 - PrevSum1;
