                // console.log('PriceDef = ',PriceDef,' ReturnResult.SumResult = ',ReturnResult.SumResult);

                // let PriceDef = ((TypeProductData.price*1)+(sumModifyPrice*1));
                AllPrice = AllPrice+PriceDef;
