import React from 'react'

export const TypeDataReturnRender = ({data = null, type = null, separator = null, linebreak = false})=>{
    let returnData = null;
    let linebreakN = '';

    if(linebreak){
        linebreakN = '\n';
    }

    if(data === null || data == '' || data == ' '){
        returnData = null;
    }else{
        data = data.replace(type,'').trim();
        separator === null || separator === false ? returnData = linebreakN+''+type+''+data.trim() : returnData = separator+''+linebreakN+''+type+''+data.trim()
    }
    return (returnData);
};