import React, {useCallback, useEffect, useRef} from 'react';
import {Image, StyleSheet, Text, View} from "react-native";
import {GetPriceProductBasketPromokodeVisual, GetPriceProductBasketVisual} from "../Basket/getPriceProductBasket";
import {ButtonInCart} from "../Catalog/ButtonInCart";
import {ModyficateBlock} from "../Catalog/Product/Modyficate/ModyficateBlock";
import {THEME} from "../../theme";
import {Price} from "../Catalog/Product/ProductCatd/Price";
import {Group} from "../../templateComponents/Group/GroupMain";
import { AntDesign } from '@expo/vector-icons';
import {useSelector} from "react-redux";
import {ButtonInCartBasket} from "./ButtonInCartBasket";
import {CheckModifyProductAccordance} from "../CheckModifyFunction/CheckModifyProductAccordance";

export const OneProductCartBasket = ({productBasket = [], allProducts = null, navigation , keys, listProductThreeForTwo = [], numberProductInCart = null}) => {

    // console.log('что передано в oneProductInCart = ',numberProductInCart)

    const DataPromokodeDiscountProduct = useSelector(state => state.post.dataPromokodeDiscoun);

    if(allProducts === null){
        return "";
    }

    const GetListModifyProductBasket = ({modifyList = []})=>{

        let resultModifyReturn = '';

        if(modifyList?.length > 0) {
            modifyList?.map((d,n) => {
                n > 0 ?
                    resultModifyReturn += " + "+d.title
                    :
                    resultModifyReturn += '+ '+d.title
            })
        }else{
            resultModifyReturn = "Модификаторы отсутсвтуют";
        }

      //console.log(resultModifyReturn)

        return  (<Text>{resultModifyReturn}</Text>);
    };

    const checkedFreeProductThreeForTwo = (product,modifyListBasket = [])=>{
        let statusFree = false;
        let countProductInFree = 0;
        listProductThreeForTwo.forEach((data,number)=>{
            if(data.idProduct == product.id){
                // console.log('id совпали');
                if(data.type_id == product.type_product){
                    // console.log('Типы совпали');
                    if(CheckModifyProductAccordance(data?.modifyList,modifyListBasket)){
                        statusFree = true;
                        countProductInFree++;
                    }else{
                        // console.log('В сравнении модификаторов товаров было это ',modifyListBasket);
                    }

                }
            }
        });

        // console.log('Статус нулевого товара для ',product.id+' с типом '+product.type_product, statusFree);

        return {"status":statusFree,"countProductInFree":countProductInFree};
    }


    const ProductsView = productBasket.type.map((typeProducts,numbs)=>{

        let product = [];
        let countFreeProductThreeForTwo = 0;

        if(typeProducts.categoryId in allProducts){
            //Если категорния есть
            if(productBasket.id in allProducts[typeProducts.categoryId]['ProductList']){
                //Если товар есть
                if(allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].lenght<=0){return false;}else
                {
                    let statusTypeProduct = false;
                    let numberArr = 0;
                    allProducts[typeProducts.categoryId]['ProductList'][productBasket.id].forEach((dataProduct, numb) => {
                        if(typeProducts.type_id == dataProduct['type_product']){
                            //Если такой тип товара есть
                            //Идем по ретурну в конце функции------>>>
                            statusTypeProduct = true;
                            numberArr = numb;
                        }
                    });

                    if(statusTypeProduct === false){
                       // console.log("Неудача ",typeProducts.type_id);
                        return false;}else{
                        product = {
                            ...allProducts[typeProducts.categoryId]['ProductList'][productBasket.id][numberArr],
                            ...{id:productBasket.id}
                        };

                        // console.log('Что в переменной product',product);
                        let statusInFreeProductThreeForTwo = checkedFreeProductThreeForTwo(product,typeProducts?.modifyList);
                        countFreeProductThreeForTwo = statusInFreeProductThreeForTwo.countProductInFree
                        if(statusInFreeProductThreeForTwo.status === true){
                            // product.price = 0;
                            // product = {...product,...{price:0}};

                            let newCountProductInCart = product.count

                            // if(product.price_discount !== false){
                            //     // product.price_discount.new_price = 0;
                            //     // let newPrice = {...product.price_discount,...{new_price:0}};
                            //
                            //     product = {...product,...{price_discount:{...product.price_discount,...{new_price:0}}}};
                            // }else{
                            //     product.price_discount = {...product.price_discount, ...{
                            //         old_price:product.price, new_price:0, active:'1'
                            //     }}
                            // }


                            // console.log('Далжны заменить цену товара на нулевую',product);

                        }

                        //----->>>>
                        return (
                            <Group style={styles.productBlock} key={keys+'_'+typeProducts.type_id+'_'+numberProductInCart+'_'+numbs} shadow={false} marginBottom={3}>
                                <View style={styles.textWrap}>
                                    <Image style={styles.image} source={{ uri: "https:"+product.img }} />
                                    <View style={styles.TextBlockProduct}>
                                        <View style={styles.BlockTitle}>
                                            <Text style={styles.title}>
                                                {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                            </Text>
                                            {/*<Text>*/}
                                            {/*    <Price ProductData={product} fontFamily="regular"/>*/}
                                            {/*</Text>*/}
                                            {/*<AntDesign name="close" size={24} color={THEME.TEXT_DESCRIPTION_PRODUCT} />*/}
                                        </View>
                                        <View style={styles.ViewTypeInfo}>
                                            <Text style={styles.weight}>{product.weight}{product.weight_type}</Text>
                                            {product.type_info !== '' ? <Text style={styles.TypeInfo}>
                                                {product.type_info.trim()}
                                            </Text>: false}
                                        </View>
                                        <View style={styles.ViewModifyList}>
                                            {typeProducts?.modifyList?.length > 0 ? <Text style={styles.ModifyListText}>
                                                <GetListModifyProductBasket modifyList={typeProducts?.modifyList}/>
                                            </Text>: false}
                                        </View>
                                    </View>
                                    <ModyficateBlock ModyficateProduct={product.modyficate}/>
                                </View>
                                <View style={styles.PriceAndButtonInCartBlock}>
                                    <View style={styles.ViewPrice}>
                                            {
                                                DataPromokodeDiscountProduct.allSumDiscount>0
                                                    ?
                                                    <GetPriceProductBasketPromokodeVisual
                                                        BasketPromoDiscount={DataPromokodeDiscountProduct.arrayProductDiscount}
                                                        ProductId={product.id} typeProduct={product.type_product}
                                                        ProductData={product}
                                                        countProduct={typeProducts.count}
                                                        modifyList={typeProducts?.modifyList}
                                                        countProductFree={countFreeProductThreeForTwo}

                                                        numberProductInCart={numberProductInCart}
                                                        numberTypeProductInCart={numbs}

                                                    />
                                                    :
                                                    <GetPriceProductBasketVisual
                                                        ProductData={product}
                                                        countProduct={typeProducts.count}
                                                        modifyList={typeProducts?.modifyList}
                                                        countProductFree={countFreeProductThreeForTwo}
                                                    />
                                            }

                                        <ButtonInCartBasket countProductInCart={typeProducts.count} numberProductInCart={numberProductInCart} numberTypeProductInCart={numbs} Product={product} typeButton={true} modifyList={typeProducts?.modifyList} typeProductModify={typeProducts?.modifyList?.length > 0 ? true : false}/>
                                    </View>
                                </View>
                            </Group>
                        );
                    }
                }

            }else{
                return false;
            }
            //console.log(productBasket);
            //return false;
        }else{
            // console.log(productBasket);
            return false;
        }

    });

    return ProductsView;

};

const styles = StyleSheet.create({
    productBlock: {
        marginBottom: 0,
        overflow: 'hidden',
    },
    image: {
        width: THEME.WIDTH_PRODUCT_ONE_PRODUCT_IN_CART_IMG,
        height: THEME.HEIGHT_PRODUCT_ONE_PRODUCT_IN_CART_IMG,
        marginRight:5,
        borderRadius:THEME.BORDER_RADIUS_DEFAULT,
    },
    TextBlockProduct:{
        width:"65%",
        paddingRight:0
    },
    BlockTitle:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"stretch",
        width:"97%",

    },
    textWrap: {
        flexDirection:"row",
        justifyContent:"space-evenly",
        backgroundColor: '#ffffff',
        paddingVertical: 1,
        paddingHorizontal:"1%",
        alignItems: 'center',
        width: '100%'
    },
    title: {
        color: THEME.TEXT_DESCRIPTION_COLOR,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize: 16,
        width:"90%",
    },
    weight:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
        color: THEME.TEXT_DESCRIPTION_PRODUCT
    },
    description: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-regular',
        fontSize:9.5
    },
    ViewDescription:{
        width:'97%',
        paddingBottom:10
    },
    PriceAndButtonInCartBlock:{
        width:"100%",
        paddingHorizontal: "3%",
    },
    ViewPrice:{
        width:'100%',
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        borderTopWidth:1,
        paddingTop:7,
        paddingBottom:2,
        borderColor:"#dddddd"
    },
    TypeInfo: {
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontFamily: 'open-bold, segoe ui, Arial',
        fontWeight:700,
        fontSize:14,
        borderWidth:0,
        borderColor:THEME.MAIN_COLOR,
        paddingVertical:4,
        paddingBottom:3,
        paddingHorizontal:7,
        borderRadius:10,
        marginLeft:5
    },
    ViewTypeInfo:{
        width:'80%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5
    },
    ViewModifyList:{
        width:'98%',
        flexDirection:"row",
        alignItems:"center",
        justifyContent: "flex-start",
        paddingBottom: 5,
    },
    ModifyListText:{
        fontFamily:"open-bold, segoe ui, Arial",
        color: THEME.TEXT_DESCRIPTION_PRODUCT,
        fontSize:12
    },
})