import React, { useEffect, useCallback } from 'react'
import {useSelector, useDispatch} from "react-redux";
import {
    ActivityIndicator,
    Platform, Pressable,
    SafeAreaView,
    ScrollView,
    StyleSheet,
    Text,
    TouchableOpacity,
    View
} from "react-native";
import {Group} from "../../templateComponents/Group/GroupMain";
import {MaterialIcons} from "@expo/vector-icons";
import {loadAuthInfo, logoutApp, logoutAppClearData} from "../../store/actions/post";
import {THEME} from "../../theme";

export const DeletedProfileScreen = ({ navigation, route }) => {
    const CityLoadPhone = useSelector(state => state.post.cityLoadPhone);
    const AuthUserStatus = useSelector(state => state.post.AuthUserStatus);

    if (CityLoadPhone || (!AuthUserStatus)) {
        //console.log('Переводим на страницу');
        // navigation.navigate('Authorization');
        return (
            <View style={styles.center}>
                <Pressable
                    style={styles.buttonCity}
                    color={THEME.MAIN_COLOR}
                    onPress={() => {
                        navigation.navigate('Authorization');
                    }}
                >
                    <Text style={{color: THEME.MAIN_COLOR}}>Авторизоваться</Text>
                </Pressable>
            </View>
        )
    } else {
        return (
            <GoodPages navigation={navigation} route={route}/>
        )
    }

}

export const GoodPages = ({ navigation, route }) => {
    const dispatch = useDispatch();
    const UserData = useSelector(state=>state.post.AuthUserData.UserDataServer);

    const GetProfileBlock = ()=>{
        return (
            <>
                <Group styles={{paddingHorizontal:"4%"}} shadow={false}>
                    {getListInfoV2('Информация','Удаление аккаунта подразумевает под собой полное стирание профиля пользователя, с историей заказов, адресов доставки, бонусных баллов и тд. \n\nДанное действие необратимо!')}

                    {/*<Group>*/}
                    {/*    {getListInfo('Дата рождения',UserData.birthday)}*/}
                    {/*    {getListInfo('Email',UserData.email)}*/}
                    {/*    {getListInfo('Телефон',UserData.phone)}*/}
                    {/*</Group>*/}
                    <TouchableOpacity onPress={()=>{
                        dispatch(logoutAppClearData());
                        navigation.navigate('Catalog');
                    }}>
                        {getListInfo('Подтвердить удаление',
                            <View style={styles.ViewInView}>
                                <Text></Text>
                                <View ></View>
                            </View>,true)}
                    </TouchableOpacity>
                </Group>
            </>
        );
    };

    const getListInfoV2 = (title,value,type = false)=>{
        if(!type){
            return (
                <View>
                    <View><Text style={styles.hTitleListV2}>{title}</Text></View>
                    <View><Text style={styles.hValueListV2}>{value}</Text></View>
                </View>
            )
        }else{
            return (
                <View>
                    <View style={styles.hTitleList}><Text>{title}</Text></View>
                    <View style={styles.hValueList}>{value}</View>
                </View>
            )
        }
    };


    const getListInfo = (title,value,type = false)=>{
        if(!type){
            return (
                <View style={styles.list}>
                    <Text style={styles.hTitleList}>{title}</Text>
                    <Text style={styles.hValueList}>{value}</Text>
                </View>
            )
        }else{
            return (
                <View style={styles.list}>
                    <View style={styles.hTitleList}><Text style={{color:THEME.DANGER_COLOR}}>{title}</Text></View>
                    <View style={styles.hValueList}>{value}</View>
                </View>
            )
        }
    };

    return (
        <ScrollView>
            <GetProfileBlock />
        </ScrollView>
    )
};


DeletedProfileScreen.navigationOptions = ({ navigation, route }) => ({
    headerTitle: 'Ваш профиль'
});

