import React, { useEffect, useCallback } from 'react'
import { WebView } from 'react-native-webview';
import { API_KEY, URL_API, CITY_DATA, URL_SITE } from './../store/types';
import {useDispatch, useSelector} from "react-redux";
import {HeaderButtons, Item} from "react-navigation-header-buttons";
import {AppHeaderIcon} from "../components/AppHeaderIcon";
import {setActiveOrderScreenData} from "../store/actions/post";
import {Platform} from "react-native";

export const ReviewScreen = ({ navigation, route }) => {

    const CityData = useSelector(state => state.post.cityData);

    const idOrderReview = route.params.idOrderReview;

    const URL = URL_SITE+"api/getPageInfo&apiKey="+API_KEY+"&getPageUrl=reviews&CityIdSystem="+CityData.cityId+"&fontSizeVh=2.1&orderIdReviews="+idOrderReview;

    return Platform.OS === 'web' ?  (

        <iframe
            src={URL}
            height={'100%'}
            width={'100%'}
            style={{border:'none'}}
        />

    ) :  (

        <WebView  source={{
            uri: URL ,
            headers:{
                'user-agent':'Mozilla/5.0 (iPhone; CPU iPhone OS 13_2_3 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/13.0.3 Mobile/15E148 Safari/604.1'
            }
        }} />

    )
};


ReviewScreen.navigationOptions = ({ navigation, route }) => {
    const dispatch = useDispatch();
    const idOrder = route.params.idOrderReview;
    const title = 'Отзыв о заказе №'+idOrder;
    return {
        headerTitle: title,
        headerLeft: (
            <HeaderButtons HeaderButtonComponent={AppHeaderIcon}>
                <Item
                    title='Toggle Drawer'
                    iconName='arrow-back'
                    color={"#333333"}
                    onPress={() => {
                        let statusReloadOrderScreen = dispatch(setActiveOrderScreenData(idOrder*1,true));
                        navigation.navigate('OrderListProfile', {
                        title: 'Список заказов',
                        productData:[],
                        idOrderNow: idOrder*1,
                        reloads:true
                        })}
                    }
                />
            </HeaderButtons>
        )
    }
};