import React, {useEffect,useState} from "react";
import {Text, View} from "react-native";
import { useDispatch, useSelector } from 'react-redux';
import {THEME} from "../../theme";
import {setTreeForTwoActive} from "../../store/actions/post";


export const ThreeForTwoComponent = (props)=> {
    let Options = {
        ...{
            title: false,
            bottomLabel: false,
            allProducts:[]
        },
        ...props
    };
    const dispatch = useDispatch();

    const SettingsModul = useSelector(state => state.post.TreeForTwoList);
    const basket = useSelector(state => state.post.basket);

    const [TextReturn,setTextRerurn] = useState(<Text>Модуль 3 = 2</Text>);

    const dataPromokodeDiscoun = useSelector(state => state.post.dataPromokodeDiscoun);
    const promokodeActive = useSelector(state => state.post.promokodeActive);



    //Получаем товары из корзины, которые подходят под условия акции 3 = 2
    const getProductListInCart = ()=>{
        let listProductConditions = [];
        let AllCountProductInCart = 0;
        let CountFreeProduct = 0;
        let arrayAllProductPrice = {};
        // console.log('Проходимся по корзинен и проверяем какие товары у нас лежат в ней ',basket);
        // console.log('Список всех товаров ',Options.allProducts);
        basket.forEach((item, number)=> {
            //  console.log('Товар ',item.id);
            item.type.forEach((type, n) => {
                let sumModifyPrice = 0;
                type?.modifyList?.forEach((d,n)=>{
                    sumModifyPrice += d.price*1;
                });

                if(checkProductonditions(item.id,type,SettingsModul) === true){
                    item.type[n].price = checkProductonditionsGetPrice(item.id,type,SettingsModul);

                    let countPr = type.count;

                    if(promokodeActive == true){
                        dataPromokodeDiscoun.listProductInCartForPromokode.forEach((dP,nP)=>{
                            if(dP.id == item.id){
                                  //console.log('Товары совпали по id, но типы видимо нет',type);
                                if(dP.type_id == type.type_id){
                                    countPr = type.count-dP.count;
                                      //console.log('Товары совпали, нужно исключить кол-ва товаров по промокоду из 3=2',type.count,dP.count,countPr);
                                }
                            }else{
                            }
                        });
                    }else{

                    }

                    if(countPr > 0) {
                        listProductConditions.push(item);
                        AllCountProductInCart += countPr;

                        let PriceProductAndModify = (item.type[n].price*1)+sumModifyPrice;

                          //console.log('Новая стоимость товара'+type.id+', получилась вот такой =',PriceProductAndModify);

                        let newTypeSet = {...type,...{count:countPr,price:PriceProductAndModify}};

                        if (PriceProductAndModify in arrayAllProductPrice) {
                            arrayAllProductPrice[PriceProductAndModify].push({...newTypeSet, ...{idProduct: item.id}});
                        } else {
                            arrayAllProductPrice[PriceProductAndModify] = [];
                            arrayAllProductPrice[PriceProductAndModify].push({...newTypeSet, ...{idProduct: item.id}});
                        }
                    }else{
                          //console.log('Товар не должен записаться в список по 3=2');
                    }
                }
            });
        });

        // console.log('Отсортированный массив с товарами по цене', arrayAllProductPrice);
        CountFreeProduct = Math.floor(AllCountProductInCart/3);

        let ItogFreeProductList = setListFreeProductItog(arrayAllProductPrice,CountFreeProduct);

        // console.log('Список товаров, которые должны быть с 0 стоимостью ', ItogFreeProductList);

        //получили список твоаров которые подходят под условия акции
        // console.log('Под условия подходят товары: ',AllCountProductInCart,listProductConditions,'бесплатных из них = '+CountFreeProduct,ItogFreeProductList);


        if(CountFreeProduct <=0){
            setTextRerurn(false);
            dispatch(setTreeForTwoActive(false,[]));
        }else{
            dispatch(setTreeForTwoActive(true,ItogFreeProductList.listProduct,ItogFreeProductList.sumDiscount));
            setTextRerurn(
                <View style={{justifyContent:"center",width:"100%",backgroundColor:THEME.MAIN_COLOR,color:THEME.TEXT_COLOR}}>
                    <Text style={{color:THEME.TEXT_COLOR,paddingVertical:15,textAlign:"center",fontSize:16}}>
                        {CountFreeProduct} {getTextLabelFreeProduct(CountFreeProduct)} в подарок уже ждет вас!
                    </Text>
                </View>
            );
        }

    }

    const setListFreeProductItog = (listAll,countFree)=>{
        let allChecked = [];
        let AllSumDiscount = 0;
        Object.keys(listAll).forEach((num,n)=>{
            listAll[num].forEach((dataProduct,numberProduct)=>{
               if(allChecked.length >= countFree){}else{
                   for(let i = 0;i<dataProduct.count;i++){
                       if(allChecked.length >= countFree){}else {
                           allChecked.push({...dataProduct, ...{count: 1}});
                           AllSumDiscount +=dataProduct.price*1;
                       }
                   }
               }
            });
        });

        // dispatch();


        return {"listProduct":allChecked,"sumDiscount":AllSumDiscount};
    }

    const getTextLabelFreeProduct = (count)=>{
        let n = Math.abs(count);
        n %= 100;
        if (n >= 5 && n <= 20) {
            return SettingsModul?.arrayCategorySaleTitles[0].name_Five;
        }
        n %= 10;
        if (n === 1) {
            return SettingsModul?.arrayCategorySaleTitles[0].name_One;
        }
        if (n >= 2 && n <= 4) {
            return SettingsModul?.arrayCategorySaleTitles[0].name_Two;
        }
        return SettingsModul?.arrayCategorySaleTitles[0].name_Five;
    };

    const checkProductonditions = (id,item,condition)=>{
        let status = false;
        if(condition.typeSale == '0'){
            //Проверка категории
            condition?.arrayCategorySale?.forEach((idCategory,n)=>{
                if(idCategory == item.categoryId){
                    status = true;
                }
            });
        }else{
            //Проверка списка товаров
            condition?.arrayCategoryProductSale?.forEach((idProduct,n)=>{
                if(idProduct == id){
                    status = true;
                }
            });
        }

        return status;
    };


    const checkProductonditionsGetPrice = (id,item,condition)=>{
        let price = 0;
        if(condition.typeSale == '0'){
            //Проверка категории
            condition?.arrayCategorySale?.forEach((idCategory,n)=>{
                if(idCategory == item.categoryId){
                    // console.log('Стоимость товара для возврата в массив ',Options.allProducts[item.categoryId].ProductList[id][0].price);
                    if(Options.allProducts[item.categoryId].ProductList[id][0].price_discount !== false) {

                        price = Options.allProducts[item.categoryId].ProductList[id][0].price_discount.new_price;

                    }else{
                        price = Options.allProducts[item.categoryId].ProductList[id][0].price;

                    }
                }
            });
        }else{
            //Проверка списка товаров
            condition?.arrayCategoryProductSale?.forEach((idProduct,n)=>{
                if(idProduct == id){
                    if(Options.allProducts[item.categoryId].ProductList[id][0].price_discount !== false) {
                        price = Options.allProducts[item.categoryId].ProductList[id][0].price_discount.new_price;

                    }else{
                        price = Options.allProducts[item.categoryId].ProductList[id][0].price;

                    }
                }
            });
        }

        return price;
    };

    useEffect(()=>{
        // dispatch(setTreeForTwoActive(false,[]));
        getProductListInCart();
    },[basket,promokodeActive]);

    return(
        <View>
            {TextReturn}
        </View>
    );
}