import * as React from 'react';
import { StyleSheet, View, Text } from 'react-native';
import StepIndicator from 'react-native-step-indicator';
// import Swiper from 'react-native-swiper';
import { MaterialIcons } from '@expo/vector-icons';
import {THEME} from "../../../theme";

const labels = ["Cart","Delivery Address","Order Summary","Payment Method","Track"];

const customStyles = {
    stepIndicatorSize: 30,
    currentStepIndicatorSize: 30,
    separatorStrokeWidth: 2,
    currentStepStrokeWidth: 3,
    stepStrokeCurrentColor: THEME.MAIN_COLOR,
    stepStrokeWidth: 3,
    stepStrokeFinishedColor: THEME.MAIN_COLOR,
    stepStrokeUnFinishedColor: '#dedede',
    separatorFinishedColor: THEME.MAIN_COLOR,
    separatorUnFinishedColor: '#dedede',
    stepIndicatorFinishedColor: THEME.MAIN_COLOR,
    stepIndicatorUnFinishedColor: '#ffffff',
    stepIndicatorCurrentColor: THEME.MAIN_COLOR,
    stepIndicatorLabelFontSize: 0,
    currentStepIndicatorLabelFontSize: 0,
    stepIndicatorLabelCurrentColor: 'transparent',
    stepIndicatorLabelFinishedColor: 'transparent',
    stepIndicatorLabelUnFinishedColor: 'transparent',
    labelColor: '#999999',
    labelSize: 0,
    currentStepLabelColor: '#7eaec4',
};

export const STepper = ({ActiveStep})=>{

    ActiveStep = ActiveStep*1;

    // console.log('Устанавливаем активный статус лайн ',ActiveStep);

    const getNumbers = ()=>{
        if(ActiveStep >4){
            return 4;
        }
        return ActiveStep;
    };


    return (
            <StepIndicator
                customStyles={customStyles}
                currentPosition={getNumbers()}
                // labels={labels}
            />
    )
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: '#ffffff',
    },
    stepIndicator: {
        marginVertical: 50,
    },
    page: {
        flex: 1,
        flexDirection:"row",
        justifyContent: 'space-between',
        alignItems: 'flex-start',
        width:"100%",
        backgroundColor: "#dddddd"
    },
    stepLabel: {
        fontSize: 12,
        textAlign: 'center',
        fontWeight: '500',
        color: '#999999',
    },
    stepLabelSelected: {
        fontSize: 12,
        textAlign: 'center',
        fontWeight: '500',
        color: '#4aae4f',
    },
});