import React, { useState } from "react";
import {View, StyleSheet, Pressable, Text, Modal, Dimensions, ActivityIndicator,ScrollView} from "react-native";
import {useDispatch, useSelector} from "react-redux";
import {setListDataDelievryList, setModalStatus, setPreorderData} from "../../../store/actions/post";
import {THEME} from "../../../theme";
import {ServerDB} from "../../../serverDb";
import { Ionicons } from '@expo/vector-icons';
import { FontAwesome5 } from '@expo/vector-icons';
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const ContentModalDateDelivery = ({keys = null}) => {

    const dispatch = useDispatch();

    const listDataDelivery =  useSelector(state=>state.post.listDataDelivery);
    const statusShowDateList = useSelector(state=>state.post.statusLoadServerPreorderData);

    const DateValueShow = useSelector(state=>state.post.preorderData.dateDelivery);

    const WorkTimePointsCity = useSelector(state=>state.post.AuthUserData.WorkTimePointsCity);



    const handleSetStatusModal = (nameModal,status)=>{
        dispatch(setModalStatus(nameModal,status));
    };

    const handleSetDatePreorder = (dates)=>{
        let dataReport = {
            dateDelivery: dates,
            timeDelivery: 0
        };

        dispatch(setPreorderData(dataReport));
    };

    const getTitleMountRus = (nMounts)=>{
        switch (nMounts) {
            case "01": return 'Января';
            case "02": return 'Февраля';
            case "03": return 'Марта';
            case "04": return 'Апреля';
            case "05": return 'Мая';
            case "06": return 'Июня';
            case "07": return 'Июля';
            case "08": return 'Августа';
            case "09": return 'Сентября';
            case "10": return 'Октября';
            case "11": return 'Ноября';
            case "12": return 'Декабря';
            default: return nMounts;
        }
    };

    const GetModalOption = ({})=>{

        let ListOption;

        if(!statusShowDateList){
            ListOption = (
                <Pressable
                    onPress={()=>{
                        //setModalVisibleS(false);
                        // handleSetStatusModal("MODAL_DATE_DELIVERY",false);
                        //setStatusShowDateList(false);
                    }}
                    style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}
                    key={"Pressable_Select_"+keys+"_loading"}
                >
                    <ActivityIndicator color={THEME.MAIN_COLOR}/>
                </Pressable>
            );
        }else{
            let ArrayList = [{title:"Как можно скорее",value:0}];

            if(WorkTimePointsCity.resultWorkTime === false){
                ArrayList = [{title:"Доставить после открытия",value:0}];
            }else{
            }
            Object.keys(listDataDelivery).forEach((dates,n)=>{
                let TitlesDef = dates.split('-');
                ArrayList.push({title:TitlesDef[2]+' '+getTitleMountRus(TitlesDef[1])+' '+TitlesDef[0],value:dates});
            });
            ListOption = ArrayList.map((data,n)=>{
                return (<Pressable
                        onPress={()=>{

                            //setModalVisibleS(false);
                            handleSetStatusModal("MODAL_DATE_DELIVERY",false);
                            handleSetDatePreorder(data.value);
                        }}
                        style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}
                        key={"Pressable_Select_"+keys+"_"+n}
                    >
                        {DateValueShow === data.value ?
                            <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? [styles.optionSelectWeb,styles.optionSelectTed] :[styles.optionSelect,styles.optionSelectTed]}><Text>{data.title}</Text><FontAwesome5 name="check" size={16} color={THEME.MAIN_COLOR} /></View>
                            :
                            <Text style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? styles.optionSelectWeb : styles.optionSelect}>{data.title}</Text>
                        }

                    </Pressable>
                )
            });
        }

        return (
                <View style={styles.modalBlock} key={"SelectBlock_"+keys}>
                    <View style={styles.modalContent}>
                        <View style={styles.backArrowBlock}>
                            <Pressable
                                onPress={()=>{
                                    handleSetStatusModal("MODAL_DATE_DELIVERY",false);
                                }}
                                style={styles.iconBack}>
                                <Ionicons name="arrow-back" size={24} color="black" />
                            </Pressable>
                            <Text style={styles.textTitleModal}>Доставить</Text>
                        </View>
                        <ScrollView>
                        <View style={styles.padding}>{ListOption}</View>
                        </ScrollView>
                    </View>
                </View>
        )
    };


    return (
        <GetModalOption />
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 0,
        alignItems: "flex-start",
        backgroundColor: "#ffffff",
        zIndex:99
    },
    backArrowBlock:{
        width:"100%",
        height:30,
        flexDirection:"row",
        alignItems:"center",
        justifyContent:"flex-start"
    },
    iconBack:{
        marginRight:"3.1%"
    },
    textTitleModal:{
        fontSize:20,
        fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,
    },
    defaultValue:{
        textAlign: "left",
        width:"100%",
        paddingHorizontal: "2%",
        paddingVertical:"2%"
    },
    modalBlock:{
        backgroundColor:"#FFFFFF",
        height: "100%",
        width: Dimensions.get('window').width,
        flexDirection:"row",
        justifyContent:"center",
        alignItems: "center",
        paddingTop:"3%"
    },
    modalContent:{
        width:"100%",
        height:"100%",
        backgroundColor:"#ffffff",
        paddingVertical:"4%",
        paddingHorizontal:"3%",
        borderRadius:0
    },
    padding:{
        paddingVertical:"3%",
        paddingHorizontal:"8%",
    },
    optionSelect:{
        width:"100%",
        marginVertical: "5%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "5%",
        paddingHorizontal:"5%",
        borderRadius: 10
    },
    optionSelectWeb:{
        width:"100%",
        marginVertical: "2%",
        fontSize:16,
        borderWidth:1,
        borderColor:"#dddddd",
        paddingVertical: "4%",
        paddingHorizontal:"5%",
        borderRadius: 10
    },
    optionSelectTed:{
        borderColor:THEME.MAIN_COLOR,
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    },
    optionSelect2:{
        fontSize:12,
        borderColor:"#dddddd",
        paddingVertical: "3%",
        color:THEME.MAIN_COLOR
    }
});