import React from 'react';
import {addProductBasket} from "../../../store/actions/post";
import {useDispatch, useSelector} from "react-redux";

export const AddProductInCartFromPromokodeSettiongs = async (ProductId,TypeProduct,CategoryProduct,basketProduct = [])=>{
    // const dispatch = useDispatch();

    let inCart = false;
    let countInCart = 0;


    //console.log'Вызывается для добавления товара в корзину, пришло это ',ProductId,TypeProduct,CategoryProduct);

    const reloadBasketData = (idProduct,TypeProduct)=>{
        basketProduct.forEach((product)=>{
            //console.log(product.id, idProduct);
            if(product.id === idProduct){
                product.type.forEach((typeId,numb)=>{
                    if(typeId.type_id === TypeProduct){
                        //console.log('Совпанеие есть, товар есть в корзине');
                        inCart = true;
                        countInCart = typeId.count;
                    }else{
                        //console.log(typeId, TypeProduct);
                        //console.log('Товара по типу нет в корзине');
                    }
                });
            }else{
                //console.log('Товара нет в корзине');
            }
        });
    }

    let PrArray =[];


    const addTypeProduct = (i,TypeProduct,idCategory)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type.push(
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: 1
                    }
                );
            }
        });
        return ProductsType;
    };


    const reloadTypeProduct = (i,TypeProduct,idCategory,num,count)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type[num] =
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: count
                    }
                ;
            }
        });
        //console.log(num,ProductsType);
        return ProductsType;
    };

    const RemoveFullTypeProduct = (i,TypeProduct,num,count)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                if(ProductsType[i].type.length<=1){
                    ProductsType[i].type.splice(num, 1);
                    //console.log('Удаляем полностью');
                }else{
                    ProductsType[i].type.forEach((PrType,nn)=>{
                        if(PrType.type_id === TypeProduct){
                            ProductsType[i].type.splice(nn, 1);
                            //console.log(ProductsType[i].type);
                        }else{}
                    });
                }
            }
        });
        //console.log(ProductsType[i].type);
        return ProductsType;
    };

    const setOneProduct = (idProduct,TypeProduct,idCategory)=>{
        return {
            id:idProduct,
            "type":[
                {
                    type_id:TypeProduct,
                    categoryId:idCategory,
                    count: 1
                }]
        };
    };


    const checkCartProduct = (idProduct,TypeProduct,idCategory)=>{
        // console.log("Добавление товара");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;

        if(basketProduct.length){
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    ProductInBasket = true;
                    product.type.forEach((typeId,numb)=>{
                        if(typeId.type_id === TypeProduct){
                            ProductTypeInBasket = true;
                            //console.log('Совпанеие есть, товар есть в корзине');
                            PrArray = [...basketProduct];
                            reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count+1)
                        }
                    });
                    if(ProductInBasket & !ProductTypeInBasket){
                        //console.log('Товара по типу нет в корзине');
                        PrArray = addTypeProduct(i,TypeProduct,idCategory);
                        //console.log('Товара нет в корзине добавляем');
                    }
                }
            });

            if(!ProductInBasket){
                //Товара нет в корзине
                PrArray = [...basketProduct];
                PrArray.push(setOneProduct(idProduct,TypeProduct,idCategory));
            }
        }else{
            //Корзина пуста, добавляем первый товар
            PrArray = [setOneProduct(idProduct,TypeProduct,idCategory)];
        }
    };


    const RemoveFullProduct = (idProduct)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(PR.id === idProduct && PR.type.length<=1){
                //console.log('Удаляем весь товар '+idProduct, PR);
                ProductsType.splice(n, 1);
            }
        });
        PrArray = [...ProductsType];
    };

    const checkCartProductRemove = (idProduct,TypeProduct,idCategory)=>{
        // console.log("Добавление товара");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;

        let RemoveAllProduct = true;

        if(basketProduct.length){
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    ProductInBasket = true;
                    if(product.type.length>1){
                        RemoveAllProduct = false;
                    }
                    product.type.forEach((typeId,numb)=>{
                        if(typeId.type_id === TypeProduct){
                            ProductTypeInBasket = true;
                            //console.log('Совпанеие есть, товар есть в корзине');
                            PrArray = [...basketProduct];
                            if(typeId.count<=1){
                                RemoveFullTypeProduct(i,TypeProduct,idCategory,numb);
                            }else{
                                if(typeId.count>1){
                                    RemoveAllProduct = false;
                                }
                                reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count-1);
                            }
                        }else{
                            if(typeId.count>0){
                                //console.log('Нет товара для удаления',typeId);
                                //reloadTypeProduct(i,typeId.type_id,idCategory,numb,typeId.count-1);
                                RemoveAllProduct = false;
                            }
                        }
                    });
                }
                if(ProductInBasket & !ProductTypeInBasket){
                    //console.log('Товара по типу нет в корзине, удаляем ');
                    //RemoveFullProduct(idProduct);
                }

            });

            if(RemoveAllProduct){
                RemoveFullProduct(idProduct)
            }

        }else{
            //Корзина пуста, добавляем первый товар
            PrArray = [];
        }
    };


    const addProductBasketHandler = (id,type_id,idCategory) => {
        //console.log('Добавляем товар в корзину ',id,type_id,idCategory);
        checkCartProduct(id,type_id,idCategory);
        // dispatch(addProductBasket(PrArray));
        return true;
    };


    // const removeProductBasketHandler = (id,type_id,idCategory) => {
    //     checkCartProductRemove(id,type_id,idCategory);
    //     dispatch(addProductBasket(PrArray));
    // };



   await addProductBasketHandler(ProductId,TypeProduct,CategoryProduct);

   //console.log('Должны вернуть в ретурн это ',PrArray);
    return PrArray;
}