import React from 'react';
import {Platform, StyleSheet, TextInput} from "react-native";
import {THEME} from "../../../theme";

export const PromokodeInput = ({value,setValue})=>{


    return (
        <TextInput
            style={styles.promokodeInput}
            placeholder={"0000"}
            onChangeText={(text) => setValue(text.replace(/[а-яА-ЯЁё\s+]/g, ''))}
            maxLength={7}
            value={value}
            multiline={false}
            textAlign={'center'}
            keyboardType={Platform.OS === 'android' ? "default" : "default"}

        />
    )
};


const styles = StyleSheet.create({
    promokodeInput:{
        borderWidth:0,
        borderColor:'#333333',
        backgroundColor:'#f2f2f2',
        borderRadius:8,
        width:"100%",
        minWidth:170,
        paddingVertical: Platform.OS === 'android' ? 4 : 10,
        paddingHorizontal:5,
        textAlign:'center'
    },
    promokodeInputError:{
        //borderColor: 'red'
    },
    promokodeInputSuccess:{
        // borderColor:'green'
    },
    buttonsSuccess:{
        backgroundColor:THEME.MAIN_COLOR,
        opacity:1
    },
    buttonsError:{
        backgroundColor:"#f2f2f2",
        opacity:0.3
    },
    textSuccess:{
        color:THEME.TEXT_COLOR,
        opacity:1
    },
    textError:{
        color:"#333333",
        opacity:0.9
    },

    refreshInputButton:{
        paddingVertical: 0,
        paddingHorizontal: 0,
        elevation: 0,
        marginVertical:10,
        marginHorizontal:"5%",
        color:"#333333",
        fontFamily: "open-bold, segoe ui, Arial",
        backgroundColor:"#ffffff",
        borderRadius:0,
        opacity:1
    },
    textRefreshButton:{
        color:"#a8a8a8",
        fontSize:10,
        textAlign:'center',
        lineHeight:20
    },
    codeInputDefault:{
        color:'#333'
    },
    codeInputSuccess:{
        backgroundColor:'#34ff6d',
        color:'#fff'
    },
    codeInputError:{
        backgroundColor:'#ff4234',
        color:'#fff'
    }
});
