                    proc_Sum = sumRemaining;
                    sales = sumRemaining;
                    sumRemaining = 0;
                    // console.log("По идее срабатывает... sumRemaining===", sumRemaining, proc_Sum)
                    // if(countProductinCartLocal > 0){
                    //     countProductinCartLocal--
                    // }
                    // if(countProductBasket > 0) {
                    //     countProductBasket--;
                    // }
