import Moment from 'moment';

export const CheckWorkTimeAffiliate = (Start,Stop,Now)=>{

    Moment.locale('ru');

    const getNumberPlusNull = (str)=>{
        let returns = 0;

        if(str*1 < 9){
            returns = '0'+str;
        }else{
            returns = str;
        }
        return returns;
    };

    //console.log(Start,Stop);

    const getParseDateStr = (str)=>{
        let d = new Date();
        let Now = Moment(d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate()+' '+str,"YYYY-DD-MM hh:mm+03");
        //console.log(d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate()+' '+str+' - Дата которая подставлялась');
       // console.log(Now.unix());
        return Now.unix();
    };


    const getParseDateStrPlus = (dates,str)=>{
        let d = dates;
        let Now = Moment(d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate()+' '+str,"YYYY-DD-MM hh:mm+03");
        //console.log(d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate()+' '+str);
        return Now.unix();
    };

    const getdayDate = (a = false)=>{
        let d = new Date();
        //console.log('сегодня', d.getDate());
        //создаем новую дату из дня старой вычитая 1 день
        if(a === false) {
            d.setDate(d.getDate() - 1);
        }else{
            d.setDate(d.getDate() + 1);
        }
        //console.log('Установили дату на '+d.getDate());
        return d;
    };

    const getTimeWork = (dateStart,dateStop,newDate)=>{
        //Проверка времени работы точек
        //Ваши входные данные
        //Начало работы
        let start_work = dateStart;
        //Конец работы
        let end_work = dateStop;
        //Текущее время
        let currentTime = new Date();

        //текущее время с датой, даже если оно у вас 12:00 все равно имеет дату
        let currentDateTime = getParseDateStr(getNumberPlusNull(currentTime.getHours())+':'+getNumberPlusNull(currentTime.getMinutes()));

       // console.log('Текущее время в мил сек ',currentDateTime);

        //Дата и время во сколько мы закрылись вчера
        let previousDayEnd;
        //Дата и время во сколько мы открылись сегодня
        let startDateTime;
        //Дата и время во сколько мы закрылись сегодня
        let endDateTime;

        startDateTime = getParseDateStr(start_work);

        if (getParseDateStr(start_work) <= getParseDateStr(end_work)){

            let yesterday = getdayDate(false);

            endDateTime = getParseDateStr(end_work);
            previousDayEnd = getParseDateStrPlus(yesterday,end_work);
        }
        else{

            let tomorrow = getdayDate(true);

            endDateTime = getParseDateStrPlus(tomorrow,end_work);
            previousDayEnd = getParseDateStr(end_work);
        }

        //console.log('currentDateTime: '+currentDateTime,'previousDayEnd: '+previousDayEnd,'startDateTime: '+startDateTime,'endDateTime: '+endDateTime);

        //проверить полученные результаты
        // echo "Мы закрылись в : " .  date("Y-m-d H:i:s", previousDayEnd). "\n";
        // echo "Открытие : " . date("Y-m-d H:i:s", startDateTime) . "\n";
        // echo "Закрытие  : " .  date("Y-m-d H:i:s", endDateTime). "\n";

        if (currentDateTime >= startDateTime && currentDateTime <= endDateTime) {
            // echo "Сейчас рабочее время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
           // console.log('Сейчас рабочее время 1');
            return true;
        }
        else if(currentDateTime < startDateTime && currentDateTime < previousDayEnd ){
            // echo "Сейчас рабочее время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
           // console.log('Сейчас рабочее время 2');
            return true;
        }
        else {
            // echo "Мы закрыты в это время : " . date("Y-m-d H:i:s", currentDateTime) ."\n";
           // console.log('Сейчас Мы закрыты');
            return false;
        }

    };


    return getTimeWork(Start,Stop,0);

};