import React, {useCallback, useEffect, useRef} from 'react';
import {
    View,
    StyleSheet,
    ActivityIndicator,
    Image,
    Text,
    Platform,
    SafeAreaView,
    Modal,
    Dimensions,
    Pressable
} from 'react-native';
import { useDispatch, useSelector } from 'react-redux';
import { AntDesign } from '@expo/vector-icons';
import {ListProductBasket} from "../components/Basket/ListProductBasket";
import {OrderModal} from "../components/Basket/OrderModal/OrderModal";
import {PromokodeModal} from "../components/Basket/PromokodeModal/PromokodeModal";
import {THEME} from "../theme";
import {ScaleGift} from "../templateComponents/Gift/GiftScale";
import {GiftVariantModal} from "../templateComponents/Gift/GiftVariantModal";
import {ThreeForTwoComponent} from "../templateComponents/ThreeForTwo/ThreeForTwo";

export const MainBakset = ({ navigation, route }) => {

    const CityLoadPhone =  useSelector(state => state.post.cityLoadPhone);
    const AuthUserStatus =  useSelector(state => state.post.AuthUserStatus);

    if (CityLoadPhone || (!AuthUserStatus)) {
        //console.log('Переводим на страницу');
        // navigation.navigate('Authorization');
        return (
            <View style={styles.center}>
                <Text style={{fontSize:14,textAlign:"center",marginBottom:20}}>Чтобы свершить заказ, Вам нужно подтвердить номер телефона, а для этого необходимо войти в аккаунт или создать его.</Text>
                <Pressable
                    style={styles.buttonCity}
                    color={THEME.MAIN_COLOR}
                    onPress={() => {
                        navigation.navigate('Authorization');
                    }}
                >
                    <Text style={{color:THEME.MAIN_COLOR,fontFamily:"open-bold, segoe ui, Arial", fontWeight:700,marginVertical:20}}>Авторизоваться</Text>
                </Pressable>
            </View>
        )
    } else {
        return (
            <GoodPages navigation={navigation} route={route}/>
        )
    }

}

export const GoodPages = ({ navigation, route }) => {

    const dispatch = useDispatch();

    const basket = useSelector(state => state.post.basket);
    const CityIdActive = useSelector(state=>state.post.cityData.cityId);
    const BasketActiveStatus = useSelector(state=>state.post.AuthUserData.BasketServerSettings[CityIdActive].active_app_mobile);
    const modalVisiblePreloader = useSelector(state=>state.post.modalStateStatus.MODAL_ORDER_PRELOADER);



    //console.log('Активность корзины для выбранного города ',BasketActiveStatus);

   // console.log(basket);



    if (BasketActiveStatus*1 === 0) {
        return (
            <View style={styles.center}>
                <AntDesign name="closecircleo" size={50} color={THEME.MAIN_COLOR} />
                <Text style={styles.TextInfo}>Корзина отключена</Text>
                <Text style={styles.TextInfoMini}>Приносим извинения, корзина временно не доступна!</Text>
            </View>
        )
    }

    if (basket.length<=0) {
        return (
            <View style={styles.center}>
                <AntDesign name="closecircleo" size={50} color={THEME.MAIN_COLOR} />
                <Text style={styles.TextInfo}>Корзина пуста</Text>
                <Text style={styles.TextInfoMini}>Для того чтобы продолжить, положите хотя бы один товар в корзину</Text>
            </View>
        )
    }

    const ModalPreloader = ()=>{
        return (
            <Modal
                animationType="fade"
                transparent={true}
                visible={modalVisiblePreloader}
                onShow={()=> {
                    // Alert.alert("Модальное окно было открыто");
                }}
                onRequestClose={() => {
                    // Alert.alert("Модальное окно было закрыто");
                    setModalVisible(!modalVisiblePreloader);
                }}
            >
                <View style={styles.preloaderModal}>
                    <Text style={{fontSize:16,paddingVertical:10}}>Отправляем заказ</Text>
                    <ActivityIndicator color={THEME.MAIN_COLOR} size={26}/>
                </View>
            </Modal>
        );
    }

    return (
        <SafeAreaView key={"MainScr"}>
            {/*<ScaleGift navigation={navigation}/>*/}
            <ListProductBasket navigation={navigation} route={route}/>
            <OrderModal navigation={navigation} route={route}/>
            <PromokodeModal />
            <GiftVariantModal />
            <ModalPreloader />
        </SafeAreaView>
    );

};

MainBakset.navigationOptions = ({ navigation, route }) => ({
    headerTitle: 'Корзина',
    drawerLockMode: 'locked-closed',
})

const styles = StyleSheet.create({
    preloaderModal:{
        width:"100%",
        height:Dimensions.get('window').height+(Dimensions.get('window').height*0.05),
        backgroundColor:"#ffffff",
        flexDirection:"column",
        justifyContent: "center",
        alignItems: "center",
        top:0,left:0,
    },
    center: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        paddingHorizontal:"5%",
        textAlign:"center"
    },
    TextInfo:{
        fontSize: 24
    },
    TextInfoMini:{
        fontSize: 16,
        color:"#333333",
        textAlign: "center"
    }

})