import React, {useCallback} from 'react'
import {
    View,
    StyleSheet,
    Image,
    ImageBackground,
    Text,
    TouchableOpacity
} from 'react-native'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'
import {addProductBasket} from "../../store/actions/post";
import {CheckModifyProductAccordance} from "../CheckModifyFunction/CheckModifyProductAccordance";
// import {GetAllPriceBasket} from "../Basket/GetAllPriceBasket";

export const ButtonInCart = ({ Product, titleButton = 'В корзину', SizeButton = 'normal',typeProductModify = false,modifyList = [], typeButton = false}) => {

    if(typeButton === true) {
        // console.log('typeProductModify = ', typeProductModify, modifyList);
    }

    const dispatch = useDispatch();
    const basketProduct = useSelector(state => state.post.basket);
    let inCart = false;
    let countInCart = 0;

    const modificationActive = useSelector(state => state.post.modifyTmp);
    const ActiveButtonSetProduct = useSelector(state => state.post.activeButtonSetProduct);


    const reloadBasketData = (idProduct,TypeProduct)=>{
        basketProduct.forEach((product)=>{
            //console.log(product.id, idProduct);
            if(product.id === idProduct){
                product.type.forEach((typeId,numb)=>{
                    if(typeId.type_id === TypeProduct){
                        //console.log('Совпанеие есть, товар есть в корзине');
                        inCart = true;
                        countInCart = typeId.count;
                    }else{
                        //console.log(typeId, TypeProduct);
                        //console.log('Товара по типу нет в корзине');
                    }
                });
            }else{
                //console.log('Товара нет в корзине');
            }
        });
    }

    let PrArray =[];


    const addTypeProduct = (i,TypeProduct,idCategory,modificationActiveSet = [],modifyProduct = false)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type.push(
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: 1,
                        modifyList:modificationActiveSet,
                        modifyProduct:modifyProduct
                    }
                );
            }
        });
        return ProductsType;
    };


    const reloadTypeProduct = (i,TypeProduct,idCategory,num,count,modificationActiveSet = [],modifyProduct = false)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                ProductsType[i].type[num] =
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: count,
                        modifyList:modificationActiveSet,
                        modifyProduct:modifyProduct
                    }
                ;
            }
        });
        //console.log(num,ProductsType);
        return ProductsType;
    };

    const RemoveFullTypeProduct = (i,TypeProduct,num,count)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(n === i){
                if(ProductsType[i].type.length<=1){
                    ProductsType[i].type.splice(num, 1);
                    //console.log('Удаляем полностью');
                }else{
                    ProductsType[i].type.forEach((PrType,nn)=>{
                        if(PrType.type_id === TypeProduct){
                            ProductsType[i].type.splice(nn, 1);
                            //console.log(ProductsType[i].type);
                        }else{}
                    });
                }
            }
        });
        //console.log(ProductsType[i].type);
        return ProductsType;
    };

    const setOneProduct = (idProduct,TypeProduct,idCategory,modificationActiveSet = [],modifyProduct = false)=>{
        return {
            id:idProduct,
                "type":[
                    {
                        type_id:TypeProduct,
                        categoryId:idCategory,
                        count: 1,
                        modifyList:modificationActiveSet,
                        modifyProduct:modifyProduct
                    }]
        };
    };


    const checkCartProduct = (idProduct,TypeProduct,idCategory, typePrModify = false)=> {
        // console.log("Добавление товара");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;
        let ModifyCheckProduct = false;


        if(typePrModify === true){
            //console.log('товар с модификаторами и его нужно добавлять в другой массив, а модификаторы такие', modifyList);

            //Если товар с модификаторами

            let StatusAddNewProduct = true; //По умолчанию, считаем что товар новый и его нужно добавить в массив как новый.
            let countAllProductInCart = 0; //Кол-во товаров в корзине, разного типа. Кол-во одного и того же товара не учитывается.
            let numberOldProduct = null; //Дефолтная переменная с номером ключа массива, товара который уже есть в сессии, по умолчанию null, то есть товара нет


            if(basketProduct.length){
                //В корзине есть товары, проверяем дальше
                basketProduct.forEach((product,i)=>{
                    //Проходим по массиву с товарами и получаем каждый товар
                    if(product.id === idProduct){
                        //Если id товара совпадает с тем, который мы хотим добавить
                        ProductInBasket = true; //Устанавливаем значение фильтра, как товар уже есть (id)
                        product.type.forEach((typeId,numb)=>{
                            //Получаем все типы товара, которые есть у совпавшего по id
                            if(typeId.type_id === TypeProduct){
                                //Если тип товара который мы хотим добавить совпал с тем что уже есть в корзине

                                //Сначала сравниваем кол-во модификаторов и есть ли они у проверяемого товара, если они не равны или у товара их нет,
                                // значит это точно не такой же товар, если кол-во равно, то проверяем дальше
                                if(modifyList.length === typeId?.modifyList?.length){
                                    ModifyCheckProduct = CheckModifyProductAccordance(typeId?.modifyList,modifyList); //true если модификаторы такие же, false если модификаторы не такие
                                }else{}

                                if(ModifyCheckProduct === true){
                                    ProductTypeInBasket = true; //Устанавливаем фильтр, как совпал по типу, id и модификаторам
                                    //console.log('Совпанеие есть, товар есть в корзине');
                                    PrArray = [...basketProduct];
                                    reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count+1,modifyList,true) //увеличиваем кол-во на 1, для проверяемого товара
                                }

                                //Первое условие - товар имеет такие же модификаторы или иные, если такие же, то просто добавлем этому товару кол-во (count)
                                //Функция сравнения модификаторов

                                //Второе условие проверки - если модификаторы не равны тем которые уже есть у товара, то
                            }
                        });


                        if(ProductInBasket & !ProductTypeInBasket){
                            //Выполнение условия, что товар не совпал по типу, но совпал по id
                            //console.log('Товара по типу нет в корзине');
                            PrArray = addTypeProduct(i,TypeProduct,idCategory,modifyList,true);
                            //console.log('Товара нет в корзине добавляем');
                        }
                    }
                });

                if(!ProductInBasket){
                    //Ни один id товара в корзине не совпал с тем, который мы хотим добавить
                    //Товара нет в корзине, значит добавляем его как первый
                    PrArray = [...basketProduct];
                    PrArray.push(setOneProduct(idProduct,TypeProduct,idCategory,modifyList,true));
                }
            }else{
                //Корзина пуста, добавляем первый товар
                PrArray = [setOneProduct(idProduct,TypeProduct,idCategory,modifyList,true)];
            }

        }else{
            //Если обычный товар

            if (basketProduct.length) {
                basketProduct.forEach((product, i) => {
                    if (product.id === idProduct) {
                        ProductInBasket = true;
                        product.type.forEach((typeId, numb) => {
                            if (typeId.type_id === TypeProduct) {
                                ProductTypeInBasket = true;
                                //console.log('Совпанеие есть, товар есть в корзине');
                                PrArray = [...basketProduct];
                                reloadTypeProduct(i, TypeProduct, idCategory, numb, typeId.count + 1)
                            }
                        });
                        if (ProductInBasket & !ProductTypeInBasket) {
                            //console.log('Товара по типу нет в корзине');
                            PrArray = addTypeProduct(i, TypeProduct, idCategory);
                            //console.log('Товара нет в корзине добавляем');
                        }
                    }
                });

                if (!ProductInBasket) {
                    //Товара нет в корзине
                    PrArray = [...basketProduct];
                    PrArray.push(setOneProduct(idProduct, TypeProduct, idCategory));
                }
            } else {
                //Корзина пуста, добавляем первый товар
                PrArray = [setOneProduct(idProduct, TypeProduct, idCategory)];
            }

        }
    };


    const RemoveFullProduct = (idProduct)=>{
        let ProductsType = [...basketProduct];
        ProductsType.forEach((PR,n)=>{
            if(PR.id === idProduct && PR.type.length<=1){
                //console.log('Удаляем весь товар '+idProduct, PR);
                ProductsType.splice(n, 1);
            }
        });
        PrArray = [...ProductsType];
    };

    const checkCartProductRemove = (idProduct,TypeProduct,idCategory)=>{
        // console.log("Добавление товара");

        let ProductInBasket = false;
        let ProductTypeInBasket = false;

        let RemoveAllProduct = true;

        if(basketProduct.length){
            basketProduct.forEach((product,i)=>{
                if(product.id === idProduct){
                    ProductInBasket = true;
                    if(product.type.length>1){
                        RemoveAllProduct = false;
                    }
                    product.type.forEach((typeId,numb)=>{
                        if(typeId.type_id === TypeProduct){
                            ProductTypeInBasket = true;
                            //console.log('Совпанеие есть, товар есть в корзине');
                            PrArray = [...basketProduct];
                            if(typeId.count<=1){
                                RemoveFullTypeProduct(i,TypeProduct,idCategory,numb);
                            }else{
                                if(typeId.count>1){
                                    RemoveAllProduct = false;
                                }
                                reloadTypeProduct(i,TypeProduct,idCategory,numb,typeId.count-1);
                            }
                        }else{
                            if(typeId.count>0){
                                //console.log('Нет товара для удаления',typeId);
                                //reloadTypeProduct(i,typeId.type_id,idCategory,numb,typeId.count-1);
                                RemoveAllProduct = false;
                            }
                        }
                    });
                }
                if(ProductInBasket & !ProductTypeInBasket){
                    //console.log('Товара по типу нет в корзине, удаляем ');
                    //RemoveFullProduct(idProduct);
                }

            });

            if(RemoveAllProduct){
                RemoveFullProduct(idProduct)
            }

        }else{
            //Корзина пуста, добавляем первый товар
            PrArray = [];
        }
    };

    // const addProductBasketHandler = useCallback((id,type_id) => {
    //     checkCartProduct(id,type_id)
    //     dispatch(addProductBasket(PrArray));
    // }, [dispatch,PrArray]);


    const addProductBasketHandler = (id,type_id,idCategory,typePrModify = false) => {
        checkCartProduct(id,type_id,idCategory,typePrModify);
        dispatch(addProductBasket(PrArray));
    };


    const removeProductBasketHandler = (id,type_id,idCategory,typePrModify = false) => {
        checkCartProductRemove(id,type_id,idCategory,typePrModify);
        dispatch(addProductBasket(PrArray));
    };


    const returnButtonIncart = (typeProductModify = false)=>{
        reloadBasketData(Product.id,Product.type_product);

        if(inCart){
            return (<View style={styles.BlockButtonCount} >
                    <TouchableOpacity activeOpacity={0.7} onPress={() => removeProductBasketHandler(Product.id,Product.type_product,Product.id_Category,typeProductModify)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>-</Text>
                        </View>
                    </TouchableOpacity>
                    <View style={styles.CountProductInCart}>
                        <Text style={styles.CountProductInCartText}>{countInCart}</Text>
                    </View>
                    <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category,typeProductModify)}>
                        <View style={styles.ButtonBlock}>
                            <Text style={styles.buttonText}>+</Text>
                        </View>
                    </TouchableOpacity>
                </View>
            )
        }else{
            return (
                <TouchableOpacity activeOpacity={0.7} onPress={() => addProductBasketHandler(Product.id,Product.type_product,Product.id_Category)}>
                <View style={
                    SizeButton == 'normal' ? styles.ButtonBlock : styles.ButtonBlockMini}>
                    <Text style={SizeButton == 'normal' ?  styles.buttonText : styles.buttonTextMini }>{titleButton}</Text>
                </View>
                </TouchableOpacity>
            )
        }
    };

    return returnButtonIncart(typeProductModify)
};

const styles = StyleSheet.create({
    BlockButtonCount:{
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center",
        // width: 100
    },
    CountProductInCart:{
        color: "#252622",
        paddingHorizontal: 10
    },
    CountProductInCartText:{
        color: "#252622",
    },
    ButtonBlock: {
        paddingHorizontal:13,
        paddingVertical:5,
        paddingBottom:7,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:10
    },
    ButtonBlockMini: {
        paddingHorizontal:10,
        paddingVertical:2,
        paddingBottom:4,
        backgroundColor:THEME.MAIN_COLOR,
        width:"auto",
        borderRadius:7
    },
    buttonText:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
        fontSize:14,
        color:THEME.TEXT_COLOR
    },
    buttonTextMini:{
        fontFamily:"open-bold, segoe ui, Arial",
        fontWeight:700,
        fontSize:10,
        color:THEME.TEXT_COLOR
    }
});
