import React from 'react'
import {View, StyleSheet, FlatList, Text, ScrollView,SafeAreaView,Dimensions} from 'react-native'
import { OneProduct } from '../Product/ProductCatd/OneProduct'
import {OneKomboProduct} from "./OneKomboProduct";
import { useDispatch, useSelector } from 'react-redux'
import {THEME} from '../../../theme'
import {getTypeDevice} from "../../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

export const KomboProductList = ({ data = [], onOpen }) => {
    if (!data.length) {
        //console.log(data);
        return (
            <View style={styles.wrapper}>
                <Text style={styles.noItems}></Text>
            </View>
        )
    }

//    console.log(data);

    const allProducts = useSelector(state => state.post.allProducts);


    const KomboList = ({productList}) => {
        let KomboList = [];



        Object.keys(allProducts).map((key,n)=>{
                let ProductsListCheck = allProducts[key].ProductList;
                Object.keys(ProductsListCheck).map((id,num)=>{

                    Object.keys(productList).map((keyKombo,number)=>{
                        if(keyKombo === id){
                            ProductsListCheck[id].map((type)=>{
                                KomboList.push({...type,
                                    id:id,
                                    img:"https:"+type.img,
                                    count:productList[keyKombo].count
                                });
                            });
                        }else{

                        }
                    });

                });
        });

        const ProductList = KomboList.map(
            (Pr,n)=>{
                return <OneKomboProduct product={[KomboList[n]]} key={"KomboListNumber_"+n}/>;
            }
        );

        return ProductList;
        // return null
    };

    return (
        data[0].length <=0 ? false : <SafeAreaView style={styles.wrapper}>
            <Text style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? [styles.TitleKomboList,{fontSize:16,paddingVertical:'1%'}] : styles.TitleKomboList}>В набор входят:</Text>
             <FlatList
                data={data}
                numColumns={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ?  2 : 1}
                keyExtractor={product => "komboProductList_" + product.id}
                listKey={product => "komboList_" + product.id}
                renderItem={({item}) => (<KomboList productList={item}/>)}
                columnWrapperStyle={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {flexWrap:"wrap"} : false}
            />
        </SafeAreaView>
    )
}

const styles = StyleSheet.create({
    wrapper: {
        paddingVertical: 10,
        paddingHorizontal:"3%",
        borderTopWidth:1,
        borderColor:THEME.BORDER_COLOR_DEFAULT,
        width:"92vw",
        alignSelf:"center"
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 18
    },
    TitleKomboList:{
        fontFamily: "open-bold, segoe ui, Arial",
        fontSize: 14
    }
})
