export const FilterCategoryScreen = ({navigation, route}) => {

    const [filterModal, setFilterModal] = React.useState(false);
    const [typeSearch, setTypeSeach] = React.useState(false);
    const [typeMark, setTypeMark] = React.useState("all");
    const dispatch = useDispatch();
    const activeCategory = useSelector(state => state.post.activeCategoryProductList);
    const allCategory = useSelector(state => state.post.allCategory);
    const listFilter = getAllFilter(allCategory,activeCategory);
    const activeFilter = useSelector(state => state.post.activeFilterCategory);

    const allProduct = useSelector(state => state.post.allProducts);

    const handleOpenCloseFilter = async ()=>{
        // setFilterModal(!filterModal);
        await dispatch(setActiveFilterCategory(typeMark))
        //console.log("VVVVVVV?",activeFilter,typeMark)
        navigation.goBack()
    }

    const handleTypeMark = (e)=>{
        setTypeMark(e);
    }

    const FilterSettings = ()=>{
        return (
            <View style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {height:Dimensions.get("window").height-95,backgroundColor:"#ffffff"} :{height:Dimensions.get("window").height-115,backgroundColor:"#ffffff"}}>
            {/*    <RadioBottomUI*/}
            {/*    status={ !typeSearch }*/}
            {/*    label={"Поиск по названию и описанию"}*/}
            {/*    onPress={handleTypeSearch}*/}
            {/*    description={"Для поиска нескольких товаров, введите наименования через запятую"}*/}
            {/*/>*/}
            {/*    <RadioBottomUI*/}
            {/*        status={ typeSearch }*/}
            {/*        label={"Обратный поиск"}*/}
            {/*        onPress={handleTypeSearch}*/}
            {/*        description={"Введите слова через запятую. Поиск исключит все товары, где они присутствуют в описании."}*/}
            {/*    />*/}

                <GetMarksProduct productList={allProduct} />
                <View style={{paddingHorizontal:"2%",paddingBottom:"4%",position:"absolute",width:"100%",bottom:0,backgroundColor:"#ffffff"}}>
                    <ButtonEbee style={{borderRadius:10,marginHorizontal: "2%",boxSizing:"border-box"}} onPress={() => handleOpenCloseFilter()}>Применить</ButtonEbee>
                </View>
            </View>
        )
    }

    const contains = (arr, elem)=>{
        for (var i = 0; i < arr.length; i++) {
            if (arr[i].mod === elem) {
                return true;
            }
        }
        return false;
    }


    const getTextMarkInfo = (mark)=>{
        switch (mark) {
            case "all": return {"name":"Все","description":false};
            case "hot": return {"name":"Горячие","description":false};
            case "new": return {"name":"Новинка","description":false};
            case "burn": return {"name":"Острые","description":false};
            case "fire": return {"name":"Жареные","description":false};
            case "vegan": return {"name":"Веганские","description":false};
            case "fish": return {"name":"Сырая рыба","description":false};
            default: return {"name":mark,"description":false};
        }
    }

    const GetMarksProduct = ({productList})=>{
        let MarksArray = [{mod:"all",title:"Все"}];
       listFilter.map((d, n) => {
           MarksArray.push({mod:d.value,title:d.title});
        });

        return(
            <ScrollView>
                {MarksArray.map((d,n)=>{
                    return(
                        <RadioBottomUI
                            status={ typeMark === d.mod ? true : false}
                            label={
                                !typeSearch || d.mod == "all"
                                    ?
                                    d.mod == "all" ? getTextMarkInfo(d.mod).name :
                                        d?.title
                                            ?
                                            d?.title
                                            :
                                            getTextMarkInfo(d?.mod).name
                                    :
                                    d?.title
                                        ?
                                        "Исключить: "+d?.title
                                        :
                                        "Исключить: "+getTextMarkInfo(d.mod).name
                            }
                            // key={d?.mod}
                            onPress={()=>handleTypeMark(d?.mod)}
                            description={getTextMarkInfo(d?.mod).description}
                        />
                    )
                })}
                <View style={{height:60}}></View>
            </ScrollView>
        );
    }


    return (
        <View style={[styles.center,{width:"100%"}]}>
            <View style={{width:"100%"}}>
                <View style={{backgroundColor:"#ffffff",paddingVertical:"2%",width:"100%",transition:"2s",display:"flex"}}>
                    <FilterSettings/>
                </View>
            </View>
        </View>
    )
}

